import { Injectable, EventEmitter } from '@angular/core';
import { NavigationService } from './navigation.service';
import { ContentService } from '../general/content.service';
import { NavigationData } from '../../shared-classes/common/navigation-data';
import { MapStateService } from '../mapState/map-state.service';
import { CurrentEventDetailsService } from '../event/current-event-details.service';
import { EmployeesTeamData } from '../../shared-classes/employees/employee-team-data';
import * as moment from 'moment/moment';
import { HomeService } from '../home/home.service';
import { CarCheckModel } from '../../shared-classes/car/car-check-model';
import { CarCheckData } from '../../shared-classes/car/car-check-data';
import { EventDetailsService } from '../event/event-details.service';
import { detailsbarTypes } from '../../shared-classes/common/detailsbar-types';
import { RoutePath } from '../../shared-classes/common/route-path';

@Injectable()
export class MappingNavigationDataService {
    public content: any;  
    public $teamEmployeeSaveEmitter: EventEmitter<string>;    
    public $suppliesSaveEmitter: EventEmitter<string>;
    public $suppliesCloseEmitter: EventEmitter<string>;
    public $checkSaveEmitter: EventEmitter<string>;
    public $reportSaveEmitter: EventEmitter<string>;
    private routes: RoutePath;
    
    constructor(
        private navigationService: NavigationService,
        private contentService: ContentService,
        private mapStateService: MapStateService,
        private currentEventDetailsService: CurrentEventDetailsService,
        private eventDetailsService: EventDetailsService,
        private homeService: HomeService      
    ){
        this.content = this.contentService.getContent('mapping-data');    
        this.$teamEmployeeSaveEmitter = new EventEmitter();          
        this.$suppliesSaveEmitter = new EventEmitter();  
        this.$suppliesCloseEmitter = new EventEmitter(); 
        this.$checkSaveEmitter = new EventEmitter();  
        this.$reportSaveEmitter = new EventEmitter();  
        this.routes = new RoutePath();      
    }

    getNavDataByRouteUrl(routeUrl: string){
        switch (routeUrl){
            case this.routes.EVENTS:{
                return this.getEventsData();
            } 
            case this.routes.LOGS:{
                return this.getLogsData();
            } 
            case this.routes.EMPLOYEES_TEAM:{
                return this.getTeamEmployeeData();
            }   
            case this.routes.SUPPLIES:{
                return this.getSuppliesData();
            }  
            case this.routes.QUICK_REPORT:{
                return this.getQuickReportData();
            }   
            case this.routes.CAR_CHECK:{
                return this.getCarCheckData();
            } 
            case this.routes.CAR_STATUS:{
                return this.getCarStatusData();
            }
            case this.routes.CAR_CHECK_START:{
                return this.getCarCheckStartData();
            }  
            case this.routes.CURRENT_STATUS:{
                return this.getMinimizeEventDetailsBarData("current-status");
            }      
            case this.routes.PREVENT_FIRE:{
                return this.getMinimizeEventDetailsBarData("prevent-fire");
            }   
            case this.routes.REPORT:{
                return this.getReportDetailsBarData();
            }    
            default:{
                return null;
            }
        }
    }

    private getEventsData(){                
        return { 
            parentClass: "events",
            buttonRight: {
                callback: () => {
                    this.navigationService.goBack();
                },
                class: "image-sprite back"
            },
            buttonLeft: {
                callback: () => {                  
                    this.navigationService.navigate(new NavigationData(this.routes.EVENT_DETAILS));
                    this.mapStateService.changeMapState(true);
                }, 
                class: "image-sprite shape-706"
            },
            title: this.content["events-title"]
        };
    }
    
    private getLogsData(){                
        return { 
            parentClass: "logs",
            buttonRight: {
                callback: () => {
                    this.navigationService.goBack();
                },
                class: "image-sprite back"
            },
            title: this.content["logs-title"],
            isCurrentEventHide: true
        };
    }

    private getTeamEmployeeData(){   
        let data = this.navigationService.getNavigationData();

        let btnRight = data && !data.isBack ? null : {
            callback: () => {
                this.navigationService.goBack();
            },
            class: "image-sprite back"
        };

        return { 
            parentClass: "employees",            
            buttonRight: btnRight,
            buttonLeft: {
                callback: () => { 
                    this.$teamEmployeeSaveEmitter.emit("$teamEmployeeEmitter");               
                }, 
                class: "image-sprite tab-icn-ok"
            },
            title: this.content["team-employee-title"],
            isCurrentEventHide: true
        };
    }

    private getSuppliesData(){           
        return { 
            parentClass: "supplies",            
            buttonRight: {
                callback: () => {
                    this.navigationService.goBack();
                    this.$suppliesCloseEmitter.emit("$suppliesCloseEmitter");
                },
                class: "image-sprite back"
            },
            buttonLeft: {
                callback: () => { 
                    this.$suppliesSaveEmitter.emit("$suppliesSaveEmitter");               
                }, 
                class: "image-sprite tab-icn-ok"
            },
            title: this.content["supplies-title"],
            isCurrentEventHide: true
        };
    }

    private getQuickReportData(){   
        return { 
            parentClass: "quick-report",            
            buttonRight: {
                callback: () => {
                    this.navigationService.goBack();
                },
                class: "image-sprite back"
            },
            buttonLeft: {},
            title: this.content["quickReport-title"],
            isCurrentEventHide: true
        };
    }
    
    private getCarCheckData(){  
        let title = this.content["car-check-title"];
        let carName = '';        
        let data = this.navigationService.getNavigationData();    
        
        if(data instanceof CarCheckData){
            title = title + " " + data.carName;
        }
        else{
            title = title + " " + data.car.carCallCode;
        }

        return { 
            parentClass: "car-check",            
            buttonRight: {
                callback: () => {
                    this.navigationService.navigate(new NavigationData(''));                    
                },
                class: "image-sprite back"
            },
            buttonLeft: {       
                callback: () => { 
                    this.$checkSaveEmitter.emit("$checkSaveEmitter");               
                }, 
                class: "image-sprite tab-icn-ok"
            },
            title: title
        };
    }

    private getCarStatusData(){                
        let data = this.navigationService.getNavigationData();    

        return { 
            parentClass: "car-status",            
            buttonRight: {
                callback: () => {
                    this.navigationService.goBack();                 
                },
                class: "image-sprite back"
            },
            buttonLeft: {       
                text: moment().format("DD.MM.YYYY"),   
                class: "text"
            },
            title: this.content["car-status-title"] + " " + data.carName
        };
    }

    private getCarCheckStartData(){  
        let title = this.content["car-check-title"];
        let data = <CarCheckData>this.navigationService.getNavigationData();    
        
        if(data)
        {
            title = title + " " + data.carName;
        }

        return { 
            parentClass: "car-check-start",            
            buttonRight: {
                callback: () => {
                    this.navigationService.navigate(new NavigationData(''));  
                },
                class: "image-sprite back"
            },
            buttonLeft: {       
                text: moment().format("DD.MM.YYYY"),   
                class: "text"
            },
            title: title
        };
    }

    private getMinimizeEventDetailsBarData(className: string){   
        let data = this.navigationService.getNavigationData();  
        let eventGeneralInfo = null;
        if(this.currentEventDetailsService.isCurrentEventById(data.eventId)){
            eventGeneralInfo = this.currentEventDetailsService.getCurrentStatus().generalInfo;
        }
        else{
            eventGeneralInfo = this.eventDetailsService.getCurrentStatus().generalInfo;
        }          
         
        return { 
            detailsbarType: detailsbarTypes.minimizeEvent,
            parentClass: className,   
            eventGeneralInfo: eventGeneralInfo,                       
            buttonRight: {
                callback: () => {
                    this.navigationService.goBack();
                },
                class: "image-sprite back"
            },
            buttonLeft: null,
            isCurrentEventHide: true
        };
    }

    private getReportDetailsBarData(){   
        let data = this.getMinimizeEventDetailsBarData("report");  
        data.buttonLeft = {     
            callback: () => { 
                this.$reportSaveEmitter.emit("$reportSaveEmitter");               
            }, 
            class: "image-sprite tab-icn-ok"
        };
        
        return data;
    }
}


