import { Component, OnInit } from '@angular/core';
import { CarEquipmentDamageData } from '../../shared-classes/car/car-equipment-damage-data';
import { EquipmentDamageType } from '../../shared-classes/car/car-equipment-damage-types';

@Component({
  selector: 'car-check-status',
  templateUrl: './car-check-status.component.html',
  inputs:['cellWrapperClass', 'cellId', 'content', 'data']
})
export class CarCheckStatusComponent {
  private content: any;
  private data: Array<CarEquipmentDamageData>;
  private cellWrapperClass: string;
  private cellId: string;
  private isCellShow: boolean = true;
  private isDamageCellShow: boolean = true;

  constructor(){
  }


  private isDamageShow(){
    if(this.data == null || this.data.length == 0){
      return false;
    }

    for(let i = 0; i < this.data.length; i ++){
      if(this.data[i].equipmentDamageTypeId == EquipmentDamageType.Damage){
        return true;
      }    
    }    
    
    return false;
  }

  private isLackShow(){
    if(this.data == null || this.data.length == 0){
      return false;
    }

    for(let i = 0; i < this.data.length; i ++){
      if(this.data[i].equipmentDamageTypeId == EquipmentDamageType.Lack){
        return true;
      }    
    }    
    
    return false;
  }
}



