import { IApplicationResponse } from './IApplicationResponseStatus';
import { ApplicationResponseStatus } from './ApplicationResponseStatus';

export class ApplicationResponse<T> implements IApplicationResponse<T>{
    resultStatus: ApplicationResponseStatus;
    data: T;
    errorMessage: string;
    successMessage: string;

    constractor(){
        this.errorMessage = '';
        this.successMessage = '';
        this.data = null;
        this.resultStatus = ApplicationResponseStatus.Success;
    }

    public isSuccess(): boolean{
        return this.resultStatus == ApplicationResponseStatus.Success;
    }

    public isSuccessWithResult(): boolean{
        return this.resultStatus == ApplicationResponseStatus.Success && this.data != null;
    }

    public isFail(): boolean{
        return this.resultStatus == ApplicationResponseStatus.Fail;
    }

    public CreateSuccessResponse<T>(): ApplicationResponse<T>{
        var response = new ApplicationResponse<T>();
        response.resultStatus = ApplicationResponseStatus.Success;
        return response;
    }

    public CreateFailResponse<T>(): ApplicationResponse<T>{
        var response = new ApplicationResponse<T>();
        response.resultStatus = ApplicationResponseStatus.Fail;
        return response;
    }

    public CreateErrorResponse<T>(error: string): ApplicationResponse<T>{
        var response = this.CreateFailResponse<T>();
        response.errorMessage = error;
        return response;
    }

    public CreateResultResponse<T>(result: T): ApplicationResponse<T>{
        var response = this.CreateSuccessResponse<T>();
        response.data = result;
        return response;
    }
}


