export class AddEquipmentRequestBase{
    carId: number;
    carCheckId: number;
    carEquipmentId: number;
    reportedEmployeeId: number;

    constructor(carId: number, carCheckId: number, carEquipmentId: number, reportedEmployeeId: number){
        this.carId = carId;
        this.carCheckId = carCheckId;
        this.carEquipmentId = carEquipmentId;
        this.reportedEmployeeId = reportedEmployeeId;
    }
}


