import { Component, Output, EventEmitter, OnInit } from '@angular/core';
import { LOCATION_INITIALIZED } from '@angular/common';

@Component({
  selector: 'sac',
  templateUrl: './server-autocomplete.component.html',
  inputs:['customClass', 'placeholder', 'search', 'data', 'noDataText', 'searchcolumn', 'isDisabled', 'selectedItem']
})
export class ServerAutocomplete {
  @Output() onValueSelected: EventEmitter<any> = new EventEmitter();

  public customClass: string;
  public placeholder: string = ' ';
  public search: Function;
  public data: Array<any>;
  public selectedItem: any = {};
  public noDataText: string = ' ';
  public searchcolumn: string;
  public isResultShow: boolean = false;
  public isDisabled: boolean = false;

  constructor(
  ) { 
 
  }

  ngAfterViewInit(){

  }

  public ItemSelected(val){   
    this.selectedItem = val;    
    this.isResultShow = false;
    this.onValueSelected.emit(val);
  }
}



