import { Component, OnInit } from '@angular/core';
import { detailsbarTypes } from '../../shared-classes/common/detailsbar-types';
import { menuTypes } from '../../shared-classes/common/menu-types';
import { NavigationService } from '../../services/navigation/navigation.service';
import { MappingNavigationDataService } from '../../services/navigation/mapping-navigation-data.service';
import { EventGeneralInfo } from '../../shared-classes/event/event-general-info';
import { CurrentEventDetailsService } from '../../services/event/current-event-details.service';

@Component({
 selector:'default-layout',
  templateUrl: './default-layout.component.html'
})

export class DefaultLayoutComponent{
  private menuType: menuTypes = menuTypes.nomenu;
  private detailsbarType: detailsbarTypes = detailsbarTypes.default;
  private navigationData: any;
  private currentEvent: EventGeneralInfo;
  private currentEventAdded: any;
  private currentEventRemoved: any;
  private additionalClass: string;

  constructor(
    private navigationService: NavigationService,
    private navDataService: MappingNavigationDataService,
    private currentEventDetailsService: CurrentEventDetailsService
  ) 
  { 
    this.currentEventAdded = this.currentEventDetailsService.$currentEventAddedEmitter.subscribe((event) => {
      this.currentEvent = event.generalInfo;
    }); 

    this.currentEventRemoved = this.currentEventDetailsService.$currentEventRemovedEmitter.subscribe((event) => {
        this.currentEvent = null;          
    });
  }

  ngOnInit() {                   
    this.currentEventDetailsService.getItem().then((response) => {      
      if(response.isSuccessWithResult())
      {  
        this.currentEvent = response.data.generalInfo;   
      }
    });              
  }

  ngOnDestroy() {
    this.currentEventAdded.unsubscribe();
    this.currentEventRemoved.unsubscribe();
  }

  componentAdded(){    
    let router = this.navigationService.getRouter();
    this.navigationData = this.navDataService.getNavDataByRouteUrl(router.url);    

    if(this.navigationData.hasOwnProperty("parentClass")){
      this.additionalClass = this.navigationData.parentClass;
   }

    if(this.navigationData.hasOwnProperty("detailsbarType")){
       this.detailsbarType = this.navigationData.detailsbarType;
    }

    if(this.navigationData.hasOwnProperty("menuType")){
      this.menuType = this.navigationData.menuType;
    }
  }
}



