
import { Inject, Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { Observable } from 'rxjs/Rx';

@Injectable()
export class ContentService {  

    private content: Object = null;

    constructor(private http: Http) {
    }
    
    public getContent(key: any) {
        if(!key || key == ''){
            return "";
        }

        return this.content[key];      
    }

    public load() {        
        return new Promise((resolve, reject) => {                     
            if(this.content){
                resolve(true);
            } 
            else{              
                let request: any = this.http.get('additional-files/he.content.json');

                if (request) {
                    request
                        .map( res => res.json() )
                        .catch((error: any) => {
                            console.error('Error reading content file');
                            resolve(error);
                            return Observable.throw(error.json().error || 'Server error');
                        })
                        .subscribe((responseData) => {
                            this.content = responseData;                        
                            resolve(true);
                        });
                } else {
                    console.error('content file is not valid');
                    resolve(true);
                }
            }
        });
    }
}


