import { Component } from '@angular/core';
import { menuTypes } from '../../shared-classes/common/menu-types';
import { detailsbarTypes } from '../../shared-classes/common/detailsbar-types';
import { DeviceService } from '../../services/general/device.service';
import { AlertifyService } from '../../services/general/alertify.service';
import { HomeService } from '../../services/home/home.service';
import { LogInfo } from '../../shared-classes/common/log-info';
import { NavigationService } from '../../services/navigation/navigation.service';
import { MapStateService } from '../../services/mapState/map-state.service';
import { NavigationData } from '../../shared-classes/common/navigation-data';
import { RoutePath } from '../../shared-classes/common/route-path';

@Component({
  selector: 'detailsbar',
  templateUrl: './detailsbar.component.html',
  inputs: ['detailsbarType', 'menuType', 'data']
})
export class DetailsbarComponent {

  isShowMenu: boolean = false;
  menuType: menuTypes;
  detailsbarType: detailsbarTypes;
  barType: detailsbarTypes;
  data: any;
  private routes: RoutePath;
  
  constructor(private deviceService: DeviceService, 
    private alertifyService: AlertifyService, 
    private homeService: HomeService,
    private navigationService: NavigationService,
    private mapStateService: MapStateService){  

      this.routes = new RoutePath();
      this.menuType = menuTypes.detailsbar;
      this.detailsbarType = detailsbarTypes.default;       
  }

  ngOnInit() {    
    
  }

  ngAfterViewInit(){    
  }

  // destroy map
  ngOnDestroy() {

  }

  private navigate(navUrl: string, isMapVisible: boolean){
    this.navigationService.navigate(new NavigationData(navUrl));
    this.mapStateService.changeMapState(isMapVisible);  
  }

  private showDevice(){
    this.alertifyService.success(this.deviceService.getDeviceId());
  }
}



