import esriCircle = require("esri/geometry/Circle");
import { Point } from "./Point";
export class Circle extends esriCircle
{

        constructor(point: Object, radius: number);
        constructor(x: number, y: number, radius: number);

    constructor(p1: any, p2: any, p3?: any)
    {
            var point: Point = null;
            if (typeof (p1) === "object")
            {
                point = new Point(p1);
                p3 = p2;
            }
            else
            {
                point = new Point(p1, p2);
            }


            super(point, {
                radius: p3
            });
        }
}


