import { CanDeactivate, ActivatedRouteSnapshot, RouterStateSnapshot } from "@angular/router";
import { Component, Injectable } from '@angular/core';

@Injectable()
export class ChangesNotSavedGuard implements CanDeactivate<Component> {

    canDeactivate(component: Component,  route: ActivatedRouteSnapshot, state: RouterStateSnapshot):boolean {
        return window.confirm("Are you sure?");
    }
}



