import { ItemStorage } from '../base/ItemStorage';
import { Injectable, EventEmitter } from '@angular/core';
import { EventInfo } from '../../shared-classes/event/event-info';
import { HttpService } from '../../core/http.service';
import { StorageResponse } from '../base/StorageResponse';
import { ConnectionService } from '../connection/connection.service';
import { OfflineStorage } from '../../stores/offlineStorage/OfflineStorage';
import { EventGeneralInfo } from '../../shared-classes/event/event-general-info';
import { EventLog } from '../../shared-classes/event/event-log';
import { EventForceInfo } from '../../shared-classes/event/event-force-info';
import { Area } from '../../shared-classes/common/area';
import { CurrentStatusModel } from '../../shared-classes/event/current-status-model';
import { EventRequest } from '../../shared-classes/request/event-request';
import { EventLogItem } from '../../shared-classes/event/event-log-item';
import { ContentService } from '../general/content.service';
import { AlertifyService } from '../general/alertify.service';
import { ApplicationResponseStatus } from '../../shared-classes/response/ApplicationResponseStatus';

@Injectable()
export class EventDetailsService extends ItemStorage<EventInfo> {
    private storageItem: EventInfo;
    private localStorageKey: string = "eventInfo";
    public $eventUpdatedEmitter: EventEmitter<EventInfo>;
    public $eventLocationUpdatedEmitter: EventEmitter<EventInfo>;

    constructor(
        private httpService: HttpService,
        private connectionService: ConnectionService,
        contentService: ContentService,
        private store: OfflineStorage,
        private alertifyService: AlertifyService
    ){              
        super(contentService);
        this.$eventUpdatedEmitter = new EventEmitter(); 
        this.$eventLocationUpdatedEmitter = new EventEmitter(); 
    }

    public getItem(request: any): Promise<StorageResponse<EventInfo>> {                           
        if(this.connectionService.isOffline())
        {
            this.storageItem =  <EventInfo>JSON.parse(this.store.getItem(this.localStorageKey));
        }
        
        if(this.storageItem != null && this.storageItem.generalInfo.eventId == request.eventId) 
        {
            return new Promise((resolve, reject) => {  
                resolve(new StorageResponse<EventInfo>().CreateResultResponse(
                    this.storageItem
                ));
            });
        }
        else{
            var pThis = this;   
            return new Promise((resolve, reject) => {                           
                let url = this.httpService.baseWebApi + 'Events/GetEventDetails/';
                this.httpService.post<StorageResponse<any>>(url, request).subscribe(
                    (response) => { 
                        if(response.resultStatus == ApplicationResponseStatus.Fail){
                            this.errorMsgPromise.then(() => {                     
                                this.alertifyService.error(this.content.getDataErrorMessage);
                                resolve(new StorageResponse<EventInfo>().CreateFailResponse());   
                            });                             
                        }
                        else{
                            this.storageItem = <EventInfo>response.data.eventInfo;
                            this.store.setItem(this.localStorageKey, JSON.stringify(this.storageItem)); 
    
                            resolve(new StorageResponse<EventInfo>().CreateResultResponse(
                                this.storageItem
                            ));    
                        }
                    },
                    (err) => {
                        this.errorMsgPromise.then(() => {                     
                            this.alertifyService.warring(this.content.getConnectionErrorMessage);
                            resolve(new StorageResponse<EventInfo>().CreateFailResponse());   
                        }); 
                    },                  
                );
            });
        }                   
    }

    public addItem(item: EventInfo): Promise<StorageResponse<boolean>> {
        if(!item){
            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<boolean>().CreateErrorResponse(
                    "the given item is null or empty."
                ));
            });
        }

        this.storageItem = item;
        this.store.setItem(this.localStorageKey, JSON.stringify(this.storageItem));  
        
        return new Promise((resolve, reject) => {            
            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });       
    }

    public updateItem(item: EventInfo): Promise<StorageResponse<boolean>> {
        if(!item || !this.storageItem){
            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<boolean>().CreateErrorResponse(
                    "the given item is null or empty."
                ));
            });
        }
        
        if(item.generalInfo.eventId == this.storageItem.generalInfo.eventId){
            this.update(item);
            this.$eventUpdatedEmitter.emit(this.storageItem);
        }        
        
        return new Promise((resolve, reject) => {            
            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });       
    }

    public updateItemLocation(item: EventInfo): Promise<StorageResponse<boolean>> {
        if(!item || !this.storageItem){
            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<boolean>().CreateErrorResponse(
                    "the given item is null or empty."
                ));
            });
        }
        
        this.update(item);
        this.$eventLocationUpdatedEmitter.emit(this.storageItem);   
        
        return new Promise((resolve, reject) => {            
            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });       
    }

    public updateItemLog(info: EventInfo): Promise<StorageResponse<boolean>> {
        if(!info || !info.eventLog || !this.storageItem){
            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<boolean>().CreateErrorResponse(
                    "the given log is null or empty."
                ));
            });
        }

        if(info.generalInfo.eventId == this.storageItem.generalInfo.eventId){
            this.storageItem.eventLog = info.eventLog;
            this.store.setItem(this.localStorageKey, JSON.stringify(this.storageItem));
        }

        return new Promise((resolve, reject) => {            
            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });       
    }

    public removeItem(itemId: number): Promise<StorageResponse<boolean>>{        
        return new Promise((resolve, reject) => {            
            if(!this.storageItem){
                return new Promise((resolve, reject) => {            
                    resolve(new StorageResponse<boolean>().CreateErrorResponse(
                        "the given log is null or empty."
                    ));
                });
            }
    
            if(itemId == this.storageItem.generalInfo.eventId){
                this.storageItem = null;
                this.store.removeItem(this.localStorageKey);
            }

            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });  
    }

    public getCurrentStatus(): CurrentStatusModel{        
        return new CurrentStatusModel(this.storageItem);
    }

    private update(item){
        this.storageItem.areas = item.areas;
        this.storageItem.eventLog = item.eventLog;
        this.storageItem.forcesInfo = item.forcesInfo;
        this.storageItem.generalInfo = item.generalInfo;
        this.store.setItem(this.localStorageKey, JSON.stringify(this.storageItem));
    }

    public getAllEventLogs(request: EventRequest): Promise<StorageResponse<EventLog>> {                             
        return new Promise((resolve, reject) => {                           
            let url = this.httpService.baseWebApi + 'Events/GetEventLogReports/';
            this.httpService.post<StorageResponse<any>>(url, request).subscribe(
                (response) => {   
                    if(response.resultStatus == ApplicationResponseStatus.Fail){
                        this.errorMsgPromise.then(() => {                     
                            this.alertifyService.error(this.content.getDataErrorMessage);
                            resolve(new StorageResponse<EventLog>().CreateFailResponse());   
                        }); 
                    }
                    else{
                        resolve(new StorageResponse<EventLog>().CreateResultResponse(
                            response.data
                        ));    
                    }
                },
                (err) => {
                    this.errorMsgPromise.then(() => {                     
                        this.alertifyService.warring(this.content.getConnectionErrorMessage);
                        resolve(new StorageResponse<EventLog>().CreateFailResponse());   
                    });                     
                },                  
            );
        });                
    }
}


