import { ItemStorage } from '../base/ItemStorage';
import { Injectable, EventEmitter } from '@angular/core';
import { HttpService } from '../../core/http.service';
import { StorageResponse } from '../base/StorageResponse';
import { CarCheckModel } from '../../shared-classes/car/car-check-model';
import { StartCarCheckModel } from '../../shared-classes/car/start-car-check-model';
import { CarCellShelfData } from '../../shared-classes/car/car-cell-shelf-data';
import { CarCellData } from '../../shared-classes/car/car-cell-data';
import { CarEquipmentDamageData } from '../../shared-classes/car/car-equipment-damage-data';
import { ActiveCarCheckRequest } from '../../shared-classes/request/active-car-check.request';
import { CarCheckRequest } from '../../shared-classes/request/car-check.request';
import { StartCarCheckRequest } from '../../shared-classes/request/start-car-check.request';
import { AddEquipmentRequest } from '../../shared-classes/request/add-equipment-request';
import { AddEquipmentLackRequest } from '../../shared-classes/request/add-equipment-lack-request';
import { CloseDamageRequest } from '../../shared-classes/request/close-damage-request';
import { CloseLackRequest } from '../../shared-classes/request/close-lack-request';
import { SaveCarCheckFinish } from '../../shared-classes/request/save-car-check-finish';
import { CarDataRequest } from '../../shared-classes/request/car-data.request';
import { CarData } from '../../shared-classes/car/car-data';
import { ContentService } from '../general/content.service';
import { AlertifyService } from '../general/alertify.service';
import { StatusCode } from '../../shared-classes/common/status-code';
import { ApplicationResponseStatus } from '../../shared-classes/response/ApplicationResponseStatus';

@Injectable()
export class CarCheckService extends ItemStorage<CarCheckModel>{
    private storageItem: CarCheckModel;
    private startStorageItem: StartCarCheckModel;

    constructor( private httpService: HttpService,        
        private alertifyService: AlertifyService,
        contentService: ContentService){ 
       super(contentService);
    }

    public getActiveCarCheck(request: ActiveCarCheckRequest): Promise<StorageResponse<CarCheckModel>> {                             
        return new Promise((resolve, reject) => {                                       
            let url = this.httpService.baseWebApi + 'CarCheck/GetActiveCarCheck/';
            this.httpService.post<StorageResponse<any>>(url, request).subscribe(
                (response) => { 
                    if(response.resultStatus == ApplicationResponseStatus.Fail){                        
                        this.errorMsgPromise.then(() => {                     
                            this.alertifyService.error(this.content.getDataErrorMessage);
                            resolve(new StorageResponse<CarCheckModel>().CreateFailResponse());   
                        });                       
                    }

                    if(response.data != null && response.data.activeCarCheckData != null){
                            response.data.activeCarCheckData.customDamageList = this.setCustomDamageList(response.data.activeCarCheckData.car.cells);
                            this.storageItem = response.data.activeCarCheckData;
                            resolve(new StorageResponse<CarCheckModel>().CreateResultResponse(
                                response.data.activeCarCheckData
                            ));
                    }
                    else{
                        resolve(new StorageResponse<CarCheckModel>().CreateResultResponse(
                            null
                        ));
                    }
                },
                (err) => {
                    this.errorMsgPromise.then(() => {                     
                        this.alertifyService.warring(this.content.getConnectionErrorMessage);
                        resolve(new StorageResponse<CarCheckModel>().CreateFailResponse());
                    }); 
                },                  
            );
        });                 
    }

    public getCarCheck(request: CarCheckRequest): Promise<StorageResponse<CarCheckModel>> {     
        if(this.storageItem){
            return new Promise((resolve, reject) => {                                         
                resolve(new StorageResponse<CarCheckModel>().CreateResultResponse(
                    this.storageItem
                ));            
            });    
        }                       
        else{
            return new Promise((resolve, reject) => {                                       
                let url = this.httpService.baseWebApi + 'CarCheck/StartCarCheck/';
                this.httpService.post<StorageResponse<any>>(url, request).subscribe(
                    (response) => {  
                        if(response.resultStatus == ApplicationResponseStatus.Fail){
                            this.errorMsgPromise.then(() => {                     
                                this.alertifyService.error(this.content.getDataErrorMessage);
                                resolve(new StorageResponse<CarCheckModel>().CreateFailResponse());   
                            }); 
                        }
                        else{
                            response.data.activeCarCheckData.customDamageList = this.setCustomDamageList(response.data.activeCarCheckData.car.cells);
                            this.storageItem = response.data.activeCarCheckData;
                            resolve(new StorageResponse<CarCheckModel>().CreateResultResponse(
                                response.data.activeCarCheckData
                            ));
                        }   
                    },
                    (err) => {
                        this.errorMsgPromise.then(() => {                     
                            this.alertifyService.warring(this.content.getConnectionErrorMessage);
                            resolve(new StorageResponse<CarCheckModel>().CreateFailResponse());   
                        }); 
                    },                  
                );            
            });                     
        }
    }

    public GetCarStatus(request: CarDataRequest): Promise<StorageResponse<CarData>> { 
        return new Promise((resolve, reject) => {                                       
            let url = this.httpService.baseWebApi + 'CarCheck/GetCarData/';
            this.httpService.post<StorageResponse<any>>(url, request).subscribe(
                (response) => {  
                    if(response.resultStatus == ApplicationResponseStatus.Fail){
                        this.errorMsgPromise.then(() => {                     
                            this.alertifyService.error(this.content.getDataErrorMessage);
                            resolve(new StorageResponse<CarCheckModel>().CreateFailResponse());   
                        }); 
                    }
                    else{
                        response.data.customDamageList = this.setCustomDamageList(response.data.cells);
                        resolve(new StorageResponse<CarData>().CreateResultResponse(
                            response.data
                        ));    
                    }
                },
                (err) => {
                    this.errorMsgPromise.then(() => {                     
                        this.alertifyService.warring(this.content.getConnectionErrorMessage);
                        resolve(new StorageResponse<CarCheckModel>().CreateFailResponse());   
                    }); 
                },                  
            );            
        });                     
    }

    public getEmployeesCarCheck(request: StartCarCheckRequest): Promise<StorageResponse<StartCarCheckModel>> {             
        if(this.storageItem){
            return new Promise((resolve, reject) => {                                         
                resolve(new StorageResponse<StartCarCheckModel>().CreateResultResponse(
                    this.startStorageItem
                ));            
            });    
        }                       
        else{
            return new Promise((resolve, reject) => {                                       
                let url = this.httpService.baseWebApi + 'CarCheck/GetStartCarCheckInfo/';
                this.httpService.post<StorageResponse<any>>(url, request).subscribe(
                    (response) => {    
                        if(response.resultStatus == ApplicationResponseStatus.Fail){
                            this.errorMsgPromise.then(() => {                     
                                this.alertifyService.error(this.content.getDataErrorMessage);
                                resolve(new StorageResponse<CarCheckModel>().CreateFailResponse());   
                            }); 
                        }
                        else{
                            this.startStorageItem = response.data.currentWatchEmployees;
                            resolve(new StorageResponse<StartCarCheckModel>().CreateResultResponse(
                                response.data.currentWatchEmployees
                            ));
                        }                        
                    },
                    (err) => {
                        this.errorMsgPromise.then(() => {                     
                            this.alertifyService.warring(this.content.getConnectionErrorMessage);
                            resolve(new StorageResponse<CarCheckModel>().CreateFailResponse());   
                        }); 
                    },                  
                );            
            });                     
        }
    }

    private setCustomDamageList(cells: Array<CarCellData>): Array<CarEquipmentDamageData>{
        let array = Array<CarEquipmentDamageData>();
        if(cells == null || cells.length == 0){
            return array;
        }
        
        for(let index = 0; index < cells.length; index ++){
            let cell = cells[index];

            if(cell == null || cell.cellEquipments == null || cell.cellEquipments.length == 0){
                continue;
            }

            for(let i = 0; i < cell.cellEquipments.length; i ++){
                let cellEquipment = cell.cellEquipments[i];

                if(cellEquipment == null || cellEquipment.openDamageList == null || cellEquipment.openDamageList.length == 0){
                    continue;
                }

                for(let j = 0; j < cellEquipment.openDamageList.length; j ++){
                    cellEquipment.openDamageList[j].cellName = cell.carCellName;
                    array.push(cellEquipment.openDamageList[j]);
                }            
            }            
        }

        return array;
    }

    public addEquipmentDamage(request: AddEquipmentRequest): Promise<StorageResponse<CarEquipmentDamageData>> {             
        return new Promise((resolve, reject) => {                                       
            let url = this.httpService.baseWebApi + 'CarCheck/AddNewCarEquipmentDamage/';
            this.httpService.post<StorageResponse<any>>(url, request).subscribe(
                (response) => {    
                    if(response.resultStatus == ApplicationResponseStatus.Fail){
                        this.alertifyService.error(this.content.saveDataErrorMessage);
                        resolve(new StorageResponse<CarCheckModel>().CreateFailResponse());
                    }
                    else{
                        resolve(new StorageResponse<boolean>().CreateResultResponse(
                            response.data.damageData
                        ));    
                    }
                },
                (err) => {
                    this.alertifyService.error(this.content.saveDataErrorMessage);
                    resolve(new StorageResponse<CarCheckModel>().CreateFailResponse());
                },                  
            );                         
        });                     
    }

    public addEquipmentLack(request: AddEquipmentLackRequest): Promise<StorageResponse<CarEquipmentDamageData>> {             
        return new Promise((resolve, reject) => {                                       
            let url = this.httpService.baseWebApi + 'CarCheck/AddNewCarEquipmentLack/';
            this.httpService.post<StorageResponse<any>>(url, request).subscribe(
                (response) => {     
                    if(response.resultStatus == ApplicationResponseStatus.Fail){
                        this.alertifyService.error(this.content.saveDataErrorMessage);
                        resolve(new StorageResponse<CarCheckModel>().CreateFailResponse());
                    }
                    else{
                        resolve(new StorageResponse<CarEquipmentDamageData>().CreateResultResponse(
                            response.data.damageData
                        ));    
                    }
                },
                (err) => {
                    this.alertifyService.error(this.content.saveDataErrorMessage);
                    resolve(new StorageResponse<CarCheckModel>().CreateFailResponse());
                },                  
            );                   
        });                     
    }

    public addEquipmentTreat(request: AddEquipmentRequest): Promise<StorageResponse<CarEquipmentDamageData>> {             
        return new Promise((resolve, reject) => {                                       
            let url = this.httpService.baseWebApi + 'CarCheck/AddNewCarEquipmentDamageRecord/';
            this.httpService.post<StorageResponse<any>>(url, request).subscribe(
                (response) => {    
                    if(response.resultStatus == ApplicationResponseStatus.Fail){
                        this.alertifyService.error(this.content.saveDataErrorMessage);
                        resolve(new StorageResponse<CarCheckModel>().CreateFailResponse());
                    }
                    else{
                        resolve(new StorageResponse<boolean>().CreateResultResponse(
                            response.data.damageData
                        ));    
                    }
                },
                (err) => {
                    this.alertifyService.error(this.content.saveDataErrorMessage);
                    resolve(new StorageResponse<CarCheckModel>().CreateFailResponse());
                },                  
            );                         
        });                     
    }

    public addEquipmentDamageList(item: CarEquipmentDamageData, isAddToRoot: boolean = true){
        if(!this.storageItem || !item || !item.equipmentInfo){
            return;
        }
        
        if(isAddToRoot){
            this.storageItem.customDamageList.unshift(item);
        }        

        for(let index = 0; index < this.storageItem.car.cells.length; index ++){
            let cell = this.storageItem.car.cells[index];

            if(cell == null || cell.cellEquipments == null || cell.cellEquipments.length == 0){
                continue;
            }

            for(let i = 0; i < cell.cellEquipments.length; i ++){
                let cellEquipment = cell.cellEquipments[i];
                
                if(cellEquipment != null && cellEquipment.equipmentId == item.equipmentInfo.equipmentId){
                    item.cellName = cell.carCellName;

                    if(isAddToRoot){
                        if(!cellEquipment.openDamageList){
                            cellEquipment.openDamageList = new Array<CarEquipmentDamageData>();
                        }
    
                        cellEquipment.openDamageList.unshift(item);    
                    }
                    else{
                        if(!cellEquipment.closedDamageList){
                            cellEquipment.closedDamageList = new Array<CarEquipmentDamageData>();
                        }
    
                        cellEquipment.closedDamageList.unshift(item);    
                    }
                }
            }            
        }
    }

    public removeFromDamageList(cellId: number, carEquipmentDamageId: number){
        for(let index = 0; index < this.storageItem.customDamageList.length; index ++){
            if(this.storageItem.customDamageList[index].carEquipmentDamageId == carEquipmentDamageId){
                this.storageItem.customDamageList.splice(index, 1);
                break;
            }
        }        

        for(let index = 0; index < this.storageItem.car.cells.length; index ++){
            let cell = this.storageItem.car.cells[index];

            if(cell == null || cell.cellEquipments == null || cell.cellEquipments.length == 0 || cellId != cell.carCellId){
                continue;
            }

            for(let i = 0; i < cell.cellEquipments.length; i ++){
                let cellEquipment = cell.cellEquipments[i];
                
                if(!cellEquipment || !cellEquipment.openDamageList || cellEquipment.openDamageList.length == 0){
                    continue;
                }

                for(let j = 0; j < cellEquipment.openDamageList.length; j++){
                    if(cellEquipment.openDamageList[j].carEquipmentDamageId == carEquipmentDamageId){
                        cellEquipment.openDamageList.splice(j, 1);
                        return;
                    }
                }
            }            
        }
    }

    public updateLackList(cellId: number, carEquipmentDamageId: number, inputVal: number){
        for(let index = 0; index < this.storageItem.customDamageList.length; index ++){
            if(this.storageItem.customDamageList[index].carEquipmentDamageId == carEquipmentDamageId){
                this.storageItem.customDamageList[index].lackQuantity = inputVal;
                break;
            }
        }        

        for(let index = 0; index < this.storageItem.car.cells.length; index ++){
            let cell = this.storageItem.car.cells[index];

            if(cell == null || cell.cellEquipments == null || cell.cellEquipments.length == 0 || cellId != cell.carCellId){
                continue;
            }

            for(let i = 0; i < cell.cellEquipments.length; i ++){
                let cellEquipment = cell.cellEquipments[i];
                
                if(!cellEquipment || !cellEquipment.openDamageList || cellEquipment.openDamageList.length == 0){
                    continue;
                }

                for(let j = 0; j < cellEquipment.openDamageList.length; j++){
                    if(cellEquipment.openDamageList[j].carEquipmentDamageId == carEquipmentDamageId){
                        cellEquipment.openDamageList[j].lackQuantity = inputVal;
                        return;
                    }
                }
            }            
        }
    }

    public closeEquipmentDamage(request: CloseDamageRequest): Promise<StorageResponse<boolean>> {             
        return new Promise((resolve, reject) => {                                       
            let url = this.httpService.baseWebApi + 'CarCheck/CloseCarEquipmentDamage/';
            this.httpService.post<StorageResponse<any>>(url, request).subscribe(
                (response) => {                                                 
                    if(response.resultStatus == ApplicationResponseStatus.Fail){
                        this.alertifyService.error(this.content.updateDataErrorMessage);
                        resolve(new StorageResponse<CarCheckModel>().CreateFailResponse());
                    }
                    else{
                        resolve(new StorageResponse<boolean>().CreateResultResponse(
                            true
                        ));    
                    }
                },
                (err) => {
                    this.alertifyService.error(this.content.updateDataErrorMessage);
                    resolve(new StorageResponse<CarCheckModel>().CreateFailResponse());                    
                },                  
            );                   
        });                     
    }
    
    public updateEquipmentLack(request: CloseLackRequest): Promise<StorageResponse<CarEquipmentDamageData>> {             
        return new Promise((resolve, reject) => {                                       
            let url = this.httpService.baseWebApi + 'CarCheck/UpdateCarEquipmentLack/';
            this.httpService.post<StorageResponse<any>>(url, request).subscribe(
                (response) => {  
                    if(response.resultStatus == ApplicationResponseStatus.Fail){
                        this.alertifyService.error(this.content.updateDataErrorMessage);
                        resolve(new StorageResponse<CarCheckModel>().CreateFailResponse());
                    }
                    else{
                        resolve(new StorageResponse<CarEquipmentDamageData>().CreateResultResponse(
                            response.data.damageData
                        ));
                    }                                               
                },
                (err) => {
                    this.alertifyService.error(this.content.updateDataErrorMessage);
                    resolve(new StorageResponse<CarCheckModel>().CreateFailResponse());
                },                  
            );                   
        });                     
    }

    public finishCarCheck(request: SaveCarCheckFinish): Promise<StorageResponse<boolean>>{
        return new Promise((resolve, reject) => {                                       
            let url = this.httpService.baseWebApi + 'CarCheck/FinishCarCheck/';
            this.httpService.post<StorageResponse<any>>(url, request).subscribe(
                (response) => {  
                    if(response.resultStatus == ApplicationResponseStatus.Fail){
                        this.alertifyService.error(this.content.saveDataErrorMessage);
                        resolve(new StorageResponse<CarCheckModel>().CreateFailResponse());
                    }
                    else{
                        this.storageItem = null;
                        this.startStorageItem = null;
                        
                        resolve(new StorageResponse<boolean>().CreateResultResponse(
                            true
                        ));                            
                    }
                },
                (err) => {
                    this.alertifyService.error(this.content.saveDataErrorMessage);
                    resolve(new StorageResponse<CarCheckModel>().CreateFailResponse());
                },                  
            );                   
        });          
    }
}


