import { Component, OnInit,  Output, EventEmitter, ViewChild } from '@angular/core';
import { CarCellData } from '../../shared-classes/car/car-cell-data';
import { EquipmentDamageType } from '../../shared-classes/car/car-equipment-damage-types';
import { CarEquipmentData } from '../../shared-classes/car/car-equipment-data';
import { menuTypes } from '../../shared-classes/common/menu-types';
import { detailsbarTypes } from '../../shared-classes/common/detailsbar-types';
import { ActionSelectionTypes } from '../../shared-classes/common/action-selection-types';
import { CarEquipmentDamageData } from '../../shared-classes/car/car-equipment-damage-data';
import { CloseDamageRequest } from '../../shared-classes/request/close-damage-request';

@Component({
  selector: 'car-check-cell',
  templateUrl: './car-check-cell.component.html',
  inputs:['cellWrapperClass', 'cell', 'content']
})
export class CarCheckCellComponent {
  @Output() onSaveDamage: EventEmitter<any> = new EventEmitter();
  @Output() onCloseDamage: EventEmitter<any> = new EventEmitter();
  @ViewChild('popup') popup: any;

  private content: any;
  private cellWrapperClass: string;
  private cell: CarCellData;
  private isCellShow: boolean = true;
  private selectedEquipment: CarEquipmentData;
  private menuType: menuTypes = menuTypes.nomenu;
  private detailsbarType: detailsbarTypes = detailsbarTypes.default;
  private actionSelectionType: ActionSelectionTypes;
  private inputVal: string = '';
  private isValid: boolean = true;
  private carEquipmentDamageId: number = -1;

  constructor(){
  }

  private isDamageShow(){
    if(this.cell.cellEquipments == null || this.cell.cellEquipments.length == 0){
      return false;
    }
  
    for(let index = 0; index < this.cell.cellEquipments.length; index ++){
      let equipment = this.cell.cellEquipments[index];
  
      if(equipment == null || equipment.openDamageList.length == 0){
        continue;
      }
  
      for(let i = 0; i < equipment.openDamageList.length; i ++){
        if(equipment.openDamageList[i].equipmentDamageTypeId == EquipmentDamageType.Damage){
          return true;
        }    
      }    
    }
    
    return false;
  }

  private isLackShow(){
    if(this.cell.cellEquipments == null || this.cell.cellEquipments.length == 0){
      return false;
    }

    for(let index = 0; index < this.cell.cellEquipments.length; index ++){
      let equipment = this.cell.cellEquipments[index];

      if(equipment == null || equipment.openDamageList.length == 0){
        continue;
      }

      for(let i = 0; i < equipment.openDamageList.length; i ++){
        if(equipment.openDamageList[i].equipmentDamageTypeId == EquipmentDamageType.Lack){
          return true;
        }    
      }    
    }
    
    return false;
  }

  private isTreatShow(){
    if(this.cell.cellEquipments == null || this.cell.cellEquipments.length == 0){
      return false;
    }

    for(let index = 0; index < this.cell.cellEquipments.length; index ++){
      let equipment = this.cell.cellEquipments[index];

      if(equipment == null || equipment.closedDamageList.length == 0){
        continue;
      }

      for(let i = 0; i < equipment.closedDamageList.length; i ++){
        if(equipment.closedDamageList[i].equipmentDamageTypeId == EquipmentDamageType.Immediate){
          return true;
        }    
      }    
    }
    
    return false;
  }
  
  private selectAction(equipment: CarEquipmentData){ 
    this.selectedEquipment = equipment;    
  }

  private getNavigationData(){           
    return {         
        buttonRight: {
            callback: () => {
              this.popup.hide();
              this.inputVal = '';
              this.carEquipmentDamageId = -1;
            },
            class: "image-sprite tab-icn-x"
        },
        buttonLeft: {
            callback: () => { 
                this.saveDamage()               
            }, 
            class: "image-sprite tab-icn-ok"
        },
        title: this.getTitle()
    };
  } 

  private saveDamage(){
    if(!this.isInputValid()){
      this.inputVal = '';
      return;
    } 

    if(this.carEquipmentDamageId != -1){
      this.closeDamage(this.carEquipmentDamageId);
    }
    else{

      if(this.actionSelectionType == ActionSelectionTypes.Lack && this.isLackExists()){
        let damageID = this.getEquipmentDamageID();
        if(damageID != -1){
          this.closeDamage(damageID);
        }        
      }
      else{
        this.onSaveDamage.emit({ inputVal: this.inputVal, actionSelectionType: this.actionSelectionType, carEquipmentId: this.selectedEquipment.carEquipmentId, cellId: this.cell.carCellId });
      }
    }

    this.popup.hide();
    this.inputVal = '';
    this.carEquipmentDamageId = -1;
  }

  private isLackExists(){
    if(this.cell.cellEquipments == null || this.cell.cellEquipments.length == 0){
      return false;
    }

    for(let index = 0; index < this.cell.cellEquipments.length; index ++){
      let equipment = this.cell.cellEquipments[index];

      if(equipment == null || equipment.openDamageList.length == 0){
        continue;
      }

      for(let i = 0; i < equipment.openDamageList.length; i ++){
        if(equipment.openDamageList[i].equipmentInfo.equipmentId == this.selectedEquipment.equipmentId && equipment.openDamageList[i].equipmentDamageTypeId == EquipmentDamageType.Lack){
          return true;
        }    
      }    
    }
    
    return false;
  }

  private getEquipmentDamageID(): number{
    if(this.cell.cellEquipments == null || this.cell.cellEquipments.length == 0){
      return -1;
    }

    for(let index = 0; index < this.cell.cellEquipments.length; index ++){
      let equipment = this.cell.cellEquipments[index];

      if(equipment == null || equipment.openDamageList.length == 0){
        continue;
      }

      for(let i = 0; i < equipment.openDamageList.length; i ++){
        if(equipment.openDamageList[i].equipmentInfo.equipmentId == this.selectedEquipment.equipmentId && equipment.openDamageList[i].equipmentDamageTypeId == EquipmentDamageType.Lack){
          return equipment.openDamageList[i].carEquipmentDamageId;
        }    
      }    
    }
    
    return -1;
  }

  private isInputValid(){   
    if(this.actionSelectionType == ActionSelectionTypes.Damage || this.actionSelectionType == ActionSelectionTypes.Treat){
      this.isValid = this.inputVal && this.inputVal.trim() != '';
    }
    else if(this.actionSelectionType == ActionSelectionTypes.Lack){
      this.isValid = this.inputVal && this.inputVal.trim() != '' && /^[0-9]*$/.test(this.inputVal.trim());
    }
    
    return this.isValid;
  }

  private getTitle(){
    if(this.actionSelectionType == ActionSelectionTypes.Damage){
      return this.content.enterDamage;
    }
    else if(this.actionSelectionType == ActionSelectionTypes.Lack){
      return this.content.enterLack;
    }
    else if(this.actionSelectionType == ActionSelectionTypes.Treat){
      return this.content.enterTreatment;
    }
  }

  private closeDamage(carEquipmentDamageId: number){
    this.onCloseDamage.emit(
      { inputVal: this.inputVal, 
        actionSelectionType: this.actionSelectionType, 
        carEquipmentDamageId: carEquipmentDamageId,
        cellId: this.cell.carCellId
      });
  }
}



