import { Component, NgZone } from '@angular/core';
import { MapStateService } from './/services/mapState/map-state.service';
import { AppConfig } from './app.config';
import { DeviceService } from './services/general/device.service';
import { NodeService } from './services/node/node.service';
import { RebootService } from './services/general/reboot.service';
import { EmapService } from './core/emap.service';
import { CustomMap } from './shared-classes/esri/custom-map';
import { LogService } from './services/general/log.service';
import { LogInfo } from './shared-classes/common/log-info';
import { LogTypes } from './shared-classes/common/log-types';
import { ContentService } from './services/general/content.service';
import { BaseComponent } from './shared-classes/common/base-component';
import { ConnectionService } from './services/connection/connection.service';
import { Http } from '@angular/http';
import { HttpClient } from '@angular/common/http';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.less']
})
export class AppComponent extends BaseComponent {
  title: string = 'app';
  mapID: string = 'myMap';
  isMapShow: boolean = true;
  mapWidth: string = "1443px";
  mapHeight: string = "662px";  
  loadMap: boolean = false;
  private isOfflineShow: boolean = false;
  private test1: any;
  private test2: any;

constructor(
    mapStateService: MapStateService, 
    private config: AppConfig, 
    private deviceService: DeviceService,
    private nodeService: NodeService,
    private emapService: EmapService,
    private rebootService: RebootService,
    private logService: LogService,
    contentService: ContentService,
    private connectionService: ConnectionService,
    private zone: NgZone,
    private http: Http,
    private httpclient: HttpClient
  ){
    super(contentService, 'home-page');
    let namespace = config.getConfig('node_host') + config.getConfig('node_namespace');    

    nodeService.createSocket(namespace);  
    nodeService.registerNodeFireTruckEvent(namespace, config.getConfig('node-fire-truck-event'));
    nodeService.registerNodeAlertifyEvent(namespace, config.getConfig('node-alertify-event'));
    this.logService.addItem(new LogInfo("Device ID: " + this.deviceService.getDeviceId(), 'Device', '', LogTypes.info));
 
    mapStateService.mapStateChangedEvent$.subscribe(result => {        
      this.isMapShow = result;
    });
   
      this.connectionService.stateChanged.subscribe(result => {    
         this.isOfflineShow = this.connectionService.isOffline();
        if(!this.isOfflineShow)
        {
          this.rebootService.sendAllQueuedItems();  
        }
      });
  }
}



