import { Injectable, EventEmitter } from '@angular/core';
import { ConnectionStateTypes } from '../../shared-classes/common/connection-state-types';
import { LogService } from '../general/log.service';
import { LogInfo } from '../../shared-classes/common/log-info';
import { LogTypes } from '../../shared-classes/common/log-types';


@Injectable()
export class ConnectionService {  
    private connectionState: ConnectionStateTypes = ConnectionStateTypes.online;
    public stateChanged: EventEmitter<ConnectionStateTypes>;


    constructor(private logService: LogService) {
        this.stateChanged = new EventEmitter();
    }

    isOffline(): boolean{
        return this.connectionState == ConnectionStateTypes.offline;
    }

    onChangeState(state: ConnectionStateTypes){      
        this.connectionState = state;
        
        let stateDesc = "";
        switch(this.connectionState){
            case ConnectionStateTypes.offline:
                stateDesc = "offline";
            break;
            case ConnectionStateTypes.online:
                stateDesc = "online";
            break;
            case ConnectionStateTypes.resume:
                stateDesc = "resume";
            break;
        }
 
        this.stateChanged.emit(state);
    }
}


