import { EventEmployee } from "../event/event-employee";
import { Status } from "./status";
import { CarInfo } from "../car/car-info";

export class ActiveMission {
    public carInfo: CarInfo;
    public currentMissionStatus: Status;
    public employees: Array<EventEmployee>;
    public eventAreaId: number;
    public eventId: number;
    public isActive: boolean;
    public isMissionAtEvent: boolean;
    public missionId: number;
    public missionTeamId: number;
    public statusArrival: Status;
    public statusBackOnStation: Status;
    public statusControl: Status;
    public statusDispatch: Status;
    public statusExit: Status;
    public statusFinish: Status;
    public statusUnControl: Status;    
}


