import { AddEquipmentLackRequest } from "./add-equipment-lack-request";

export class CloseLackRequest extends AddEquipmentLackRequest{
    public carEquipmentDamageId: number;

    constructor(carId: number, carEquipmentDamageId: number, carCheckId: number, input: number, closeEmployeeId: number){
        super(carId, carCheckId, 0, closeEmployeeId, input);
        this.carEquipmentDamageId = carEquipmentDamageId;
    }
}


