
import esriWebTiledLayer = require("esri/layers/WebTiledLayer");
import SpatialReference = require("esri/SpatialReference");
import {WebTiledLayerOptions} from "./WebTiledLayerOptions";

export class WebTiledLayer extends esriWebTiledLayer
    {
        public urlTemplate: string;
        private layerName: string;
        private getUrlFunction: Function;                
        public levelsMapping: number[];
    
        constructor(options: WebTiledLayerOptions)
        {
            super(options.templateUrl, options);

            this.spatialReference = new SpatialReference({ "wkid": 2039 });
            this.initialExtent = options.initialExtent;
            this.fullExtent = options.fullExtent;
            this.tileInfo = options.tileInfo;            
            this.urlTemplate = options.templateUrl;            
            this.layerName = options.layerName;
            this.loaded = true;
            this.getUrlFunction = options.getUrlFunction;      
            
            if (options.templateUrl.indexOf('file://') !== -1)
            {
                this.urlTemplate = '../' + ((options.templateUrl.split('//'))[1]);//removing file://, added this in first place because esri get exception without it
            }
        }

        getTileUrl(level: number, row: number, column: number): string
        {
            return this.getUrlFunction(level, row, column, this);
        }
    }


