import { Component, OnInit } from '@angular/core';
import { ContentService } from '../../services/general/content.service';

export class BaseComponent implements OnInit{
    public content: any;

    constructor(contentService: ContentService, contentKey: string = null){
        if(contentKey != null && contentKey.trim() != ''){
            contentService.load().then(()=>{
                this.content = contentService.getContent(contentKey);
            });  
        }
    }

    ngOnInit(): void {
        
    }

    ngOnDestroy(): void{

    }
}


