import { AddEquipmentRequestBase } from "./add-equipment-request-base";

export class AddEquipmentRequest extends AddEquipmentRequestBase{
    damageDesc: string;

    constructor(carId: number, carCheckId: number, carEquipmentId: number, reportedEmployeeId: number, input: string){
        super(carId, carCheckId, carEquipmentId, reportedEmployeeId);

        this.damageDesc = input;
    }
}


