import { Injectable } from '@angular/core';
import { HomeModel } from '../../shared-classes/home/home-model';
import { StorageResponse } from '../base/StorageResponse';
import { HttpService } from '../../core/http.service';
import { ItemStorage } from '../base/ItemStorage';
import { OfflineStorage } from '../../stores/offlineStorage/OfflineStorage';
import { ConnectionService } from '../connection/connection.service';
import { ConnectionStateTypes } from '../../shared-classes/common/connection-state-types';
import { ContentService } from '../general/content.service';
import { AppConfig } from '../../app.config';
import { DeviceService } from '../general/device.service';
import { DeviceRequest } from '../../shared-classes/request/device-request';
import { employee } from '../../shared-classes/employees/employee';
import { CarInfo } from '../../shared-classes/car/car-info';
import { EventInfo } from '../../shared-classes/event/event-info';
import { fireTruckInfo } from '../../shared-classes/common/fire-truck-info';
import * as moment from 'moment/moment';
import { AlertifyService } from '../general/alertify.service';
import { ApplicationResponseStatus } from '../../shared-classes/response/ApplicationResponseStatus';
import { LogService } from '../general/log.service';
import { LogInfo } from '../../shared-classes/common/log-info';
import { LogTypes } from '../../shared-classes/common/log-types';

@Injectable()
export class HomeService extends ItemStorage<HomeModel>{
    private localStorageKey: string = "homeModel";
    private homeModel: HomeModel = null;
    private contentDetailsBar: any;

    constructor(
        private store: OfflineStorage, 
        private httpService: HttpService, 
        private connectionService: ConnectionService,
        contentService: ContentService,
        private appConfig: AppConfig,
        private deviceService: DeviceService,
        private alertifyService: AlertifyService,
        private logService: LogService
    ){ 
        super(contentService);  
         
        contentService.load().then(()=>{
            this.contentDetailsBar = contentService.getContent('details-bar-home');
          });            
    }

    public getItem(): Promise<StorageResponse<HomeModel>> {                 
        return new Promise((resolve, reject) => {  
            if(this.homeModel != null) 
            {            
                resolve(new StorageResponse<HomeModel>().CreateResultResponse(
                    this.homeModel
                ));
            }
            else{                       
                let request = new DeviceRequest(this.deviceService.getDeviceId());
                let url = this.httpService.baseWebApi + 'home/GetHomePageInfo/';

                this.httpService.post<StorageResponse<HomeModel>>(url, request).subscribe((response) => {                       
                        if(response.resultStatus != ApplicationResponseStatus.Success){                         
                            this.errorMsgPromise.then(() => {                     
                                this.alertifyService.error(this.content.getDataErrorMessage);
                                resolve(new StorageResponse<HomeModel>().CreateFailResponse());  
                            });                        
                        }
                        else{                         
                            this.homeModel = new HomeModel();
                            this.homeModel = response.data;                        
                            this.homeModel.systemName = this.contentDetailsBar.systemName;
                            this.homeModel.versionName = this.contentDetailsBar.version + this.appConfig.getConfig('version');
                            this.homeModel.title = this.getDayPart();
                            
                            this.addItem(this.homeModel);

                            resolve(new StorageResponse<HomeModel>().CreateResultResponse(
                                this.homeModel
                            ));    
                        }
                    },
                    (err) => {                    
                        this.errorMsgPromise.then(() => {                     
                            this.alertifyService.warring(this.content.getConnectionErrorMessage);
                            resolve(new StorageResponse<HomeModel>().CreateFailResponse());    
                        });
                    },                
                );             
            }
        });
    }                      

    public getCar(): Promise<StorageResponse<CarInfo>>{
        return new Promise((resolve, reject) => {            
            this.getItem().then((response) => {      
                if(response.isSuccessWithResult())
                {                        
                    resolve(new StorageResponse<CarInfo>().CreateResultResponse(
                        this.homeModel.fireTruckInfo.carInfo
                    ));
                }
                else{
                    resolve(new StorageResponse<CarInfo>().CreateFailResponse());
                }
              });   
        });          
    }

    public getTeamEmployees(): Promise<StorageResponse<Array<employee>>>{
        return new Promise((resolve, reject) => {            
            this.getItem().then((response) => {      
                if(response.isSuccessWithResult())
                {                        
                    resolve(new StorageResponse<Array<employee>>().CreateResultResponse(
                        this.homeModel.fireTruckInfo.employees
                    ));
                }
                else{
                    resolve(new StorageResponse<Array<employee>>().CreateFailResponse());
                }
              });   
        });          
    }

    public addItem(item: HomeModel): Promise<StorageResponse<boolean>> {           
        if(item == undefined){
            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<boolean>().CreateErrorResponse(
                    "the given item is null."
                ));
            });
        }

        this.store.setItem(this.localStorageKey, JSON.stringify(item));  
        this.homeModel = item;
        
        return new Promise((resolve, reject) => {            
            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });         
    }

    public removeItem(): Promise<StorageResponse<boolean>> {  
        return new Promise((resolve, reject) => {            
            this.homeModel = null;         
            this.store.removeItem(this.localStorageKey);         
            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });                
    }

    public reboot(): Promise<StorageResponse<boolean>> {  
          return new Promise((resolve, reject) => {   
            this.removeItem().then(() => {
                 this.getItem().then(() => {  
                    resolve(new StorageResponse<boolean>().CreateSuccessResponse());
                })
            });
        });                 
    }

    public updateActiveEventCount(isAdded: boolean):  Promise<StorageResponse<boolean>>{
        if(isAdded){
            this.homeModel.numberOfActiveEvents++;
        }
        else{
            this.homeModel.numberOfActiveEvents--;
        }        

        this.addItem(this.homeModel);

        return new Promise((resolve, reject) => {            
            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });  
    }

    public setActiveEventCount(counter: number):  Promise<StorageResponse<boolean>>{
        this.homeModel.numberOfActiveEvents = counter;
        this.addItem(this.homeModel);

        return new Promise((resolve, reject) => {            
            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });  
    }

    public updateTeamEmployees(employees: Array<employee>): Promise<StorageResponse<boolean>>{
        if(!employees || employees.length == 0)
        {
            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<boolean>().CreateErrorResponse(
                    "the given items is null."
                ));
            });
        }
        return new Promise((resolve, reject) => {            
            this.homeModel.fireTruckInfo.employees = employees;
            this.addItem(this.homeModel);
            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });          
    }

    public removeAtEvent(): Promise<StorageResponse<boolean>> {          
        if(this.homeModel && this.homeModel.fireTruckInfo && this.homeModel.fireTruckInfo.activeAtEvent){
            this.homeModel.fireTruckInfo.activeAtEvent = null;
            this.addItem(this.homeModel);
        }

        return new Promise((resolve, reject) => {            
            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });                
    }

    public setActiveAtEvent(item: EventInfo): Promise<StorageResponse<boolean>> {           
        if(item == undefined){
            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<boolean>().CreateErrorResponse(
                    "the given item is null."
                ));
            });
        }

        this.homeModel.fireTruckInfo.activeAtEvent = item;
        this.store.setItem(this.localStorageKey, JSON.stringify(this.homeModel));  
        
        
        return new Promise((resolve, reject) => {            
            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });         
    }

    public updateFireTruckData(item: fireTruckInfo): Promise<StorageResponse<boolean>> {           
        if(item == undefined){
            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<boolean>().CreateErrorResponse(
                    "the given item is null."
                ));
            });
        }

        this.homeModel.fireTruckInfo = item;   
        this.store.setItem(this.localStorageKey, JSON.stringify(this.homeModel));  
        
        
        return new Promise((resolve, reject) => {            
            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });         
    }

    public getDayPart(){        
        let hour = moment().hour();

        if(hour > 4 && hour < 12){
            return this.contentDetailsBar.morning;
        }
        else if(hour > 11 && hour < 16){
            return this.contentDetailsBar.afternoon;
        }
        else if(hour > 15 && hour < 21){
            return this.contentDetailsBar.evening;
        }
        else{
            return this.contentDetailsBar.night;
        }
    }
}


