import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'eventFirstNumberPipe'
})
export class EventFirstNumberPipe implements PipeTransform {
  transform(value: number, lastLettersCounter: number): string {
      let sNumber = value.toString();
      if(sNumber.length < lastLettersCounter){
        return sNumber;
      }

      return sNumber.substring(0, sNumber.length - lastLettersCounter);
  }
}


