import { Pipe, PipeTransform } from '@angular/core';
import * as moment from 'moment/moment';

@Pipe({
  name: 'beforeHours'
})
export class BeforeHoursPipe implements PipeTransform {
  transform(value: any): string {
      moment.locale('he');      
      return moment(value).startOf('minute').fromNow();  
  }
}


