export class CloseDamageRequest{
    public carId: number;
    public carEquipmentDamageId: number;
    public closeEmployeeId: number;

    constructor(carId: number, carEquipmentDamageId: number, closeEmployeeId: number){
        this.carId = carId;
        this.carEquipmentDamageId = carEquipmentDamageId;
        this.closeEmployeeId = closeEmployeeId;
    }
}


