import { Component, OnInit } from '@angular/core';
import { menuTypes } from '../../shared-classes/common/menu-types';
import { detailsbarTypes } from '../../shared-classes/common/detailsbar-types';
import { HomeService } from '../../services/home/home.service';
import { HomeModel } from '../../shared-classes/home/home-model';
import { CurrentEventDetailsService } from '../../services/event/current-event-details.service';
import { EventGeneralInfo } from '../../shared-classes/event/event-general-info';
import { MapStateService } from '../../services/mapState/map-state.service';
import { BaseComponent } from '../../shared-classes/common/base-component';
import { ContentService } from '../../services/general/content.service';
import { RebootService } from '../../services/general/reboot.service';

@Component({
 selector:'home-layout',
  templateUrl: './home-layout.component.html'
})

export class HomeLayoutComponent extends BaseComponent{
  private detailsbarMenuType: menuTypes = menuTypes.nomenu;
  private homeMenuType: menuTypes = menuTypes.home;
  private detailsbarType: detailsbarTypes = detailsbarTypes.home;
  private homeData: HomeModel;
  private currentEvent: EventGeneralInfo;
  private currentEventAdded: any;
  private currentEventRemoved: any;
  private rebootFired: any;

  constructor(
    private homeService: HomeService, 
    private currentEventDetailsService: CurrentEventDetailsService,
    private mapStateService: MapStateService,
    private rebootService: RebootService,
    contentService: ContentService
  ) 
  {  
      super(contentService, 'home-page');
      this.currentEventAdded = this.currentEventDetailsService.$currentEventAddedEmitter.subscribe((event) => {
          this.currentEvent = event.generalInfo;          
      });

      this.currentEventRemoved = this.currentEventDetailsService.$currentEventRemovedEmitter.subscribe((event) => {        
        this.currentEvent = null;          
      });

      this.rebootFired = this.rebootService.$rebootFired.subscribe((event) => {        
        this.getHomePageModel();
      });
  }

  ngOnInit() {   
    this.mapStateService.changeMapState(false);
    this.getHomePageModel();          
  }

  ngOnDestroy() {
    this.currentEventAdded.unsubscribe();
    this.currentEventRemoved.unsubscribe();
    this.rebootFired.unsubscribe();
  }

  private getHomePageModel(){
    this.homeService.getItem().then((response) => {            
      if(response.isSuccessWithResult())
      {                        
          this.homeData = response.data; 

          if(response.data.fireTruckInfo.activeAtEvent){
             this.currentEventDetailsService.addItem(this.homeData.fireTruckInfo.activeAtEvent, false);   
             this.currentEvent = this.homeData.fireTruckInfo.activeAtEvent.generalInfo;                
          }
          else{
            this.currentEventDetailsService.getItem().then((res) => {
              if(res.isSuccessWithResult())
              {
                this.currentEvent = res.data.generalInfo;
              }
            });
          }
      }        
    }); 
  }
}



