import { Component, OnInit } from '@angular/core';
import { MapStateService } from '../../services/mapState/map-state.service';
import { ContentService } from '../../services/general/content.service';
import { MappingNavigationDataService } from '../../services/navigation/mapping-navigation-data.service';
import { menuTypes } from '../../shared-classes/common/menu-types';
import { HomeService } from '../../services/home/home.service';
import { CarCheckRequest } from '../../shared-classes/request/car-check.request';
import { CarCheckService } from '../../services/car/car-check-service';
import { CarCheckModel } from '../../shared-classes/car/car-check-model';
import { NavigationService } from '../../services/navigation/navigation.service';
import { CarCheckData } from '../../shared-classes/car/car-check-data';
import { ActionSelectionTypes } from '../../shared-classes/common/action-selection-types';
import { AddEquipmentRequest } from '../../shared-classes/request/add-equipment-request';
import { AddEquipmentLackRequest } from '../../shared-classes/request/add-equipment-lack-request';
import { CloseDamageRequest } from '../../shared-classes/request/close-damage-request';
import { CloseLackRequest } from '../../shared-classes/request/close-lack-request';
import { SaveCarCheckFinish } from '../../shared-classes/request/save-car-check-finish';
import { NavigationData } from '../../shared-classes/common/navigation-data';
import { BaseComponent } from '../../shared-classes/common/base-component';

@Component({
  templateUrl: './car-check.component.html'
})

export class CarCheckComponent extends BaseComponent {
  private STATUSCELL: number = 999;

  private checkSaveEmitter: any;
  private carCheckMenuType: menuTypes = menuTypes.carCheck;
  private carCheckModel: CarCheckModel;
  private currentCell: number = 0;

  constructor(
    mapStateService: MapStateService,
    contentService: ContentService,
    mappingNavigationDataService: MappingNavigationDataService,
    navigationService: NavigationService,
    private homeService: HomeService,
    private carCheckService: CarCheckService,
  ){  
    super(contentService, 'car-check');
    mapStateService.changeMapState(false);

    let data = navigationService.getNavigationData();
    let request = null;

    if(data instanceof CarCheckData){
      request = new CarCheckRequest(data.carId, data.employeeId, data.carKm.toString());
    }
    else{
      request = new CarCheckRequest(data.car.carId, data.checker.employeeId, data.car.carKm.toString());
    }
    
    carCheckService.getCarCheck(request).then((response) => {
      if(response.isSuccessWithResult()){
        this.carCheckModel = response.data;  
        
        if(this.carCheckModel && this.carCheckModel.car && this.carCheckModel.car.cells && this.carCheckModel.car.cells.length > 0){
          this.currentCell = this.STATUSCELL;
        }
      } 
    });

    this.checkSaveEmitter = mappingNavigationDataService.$checkSaveEmitter.subscribe(result => {        
      let request = new SaveCarCheckFinish(this.carCheckModel.car.carId, this.carCheckModel.carCheckId);      
      this.carCheckService.finishCarCheck(request).then((response) => {
        if(response.isSuccessWithResult()){            
          navigationService.navigate(new NavigationData(''));
        }
      });
    });
  }

  ngOnDestroy() {
    this.checkSaveEmitter.unsubscribe();
  }

  public cellWrapperScroll() {
    let elem =  document.getElementById(this.STATUSCELL.toString());
    if(elem && this.isScrolledIntoView(elem)){
      this.currentCell = this.STATUSCELL;        
    }
    else{
      for(let index = 0; index < this.carCheckModel.car.cells.length; index ++){      
        let elem =  document.getElementById(this.carCheckModel.car.cells[index].carCellId.toString());  
        if(elem && this.isScrolledIntoView(elem)){
          this.currentCell = this.carCheckModel.car.cells[index].carCellId;        
        }
      }  
    }
  }

  private isScrolledIntoView(elem)
  {
      var docViewTop = 0;
      var docViewBottom = window.outerHeight - 50;

      var bounding = elem.getBoundingClientRect();  
      var elemTop = bounding.top;
      var elementBottom = elemTop + bounding.height;

      return (elemTop > docViewTop && elemTop < docViewBottom) || (elemTop < docViewTop && elementBottom > docViewBottom);
   }

  private save(savedElement){   
      if(savedElement.actionSelectionType == ActionSelectionTypes.Damage){     
        this.saveDamage(this.carCheckModel.car.carId, savedElement.carEquipmentId, savedElement.inputVal);
      }
      else if(savedElement.actionSelectionType == ActionSelectionTypes.Lack){
         this.saveLack(this.carCheckModel.car.carId, savedElement.carEquipmentId, savedElement.inputVal); 
      }
      else if(savedElement.actionSelectionType == ActionSelectionTypes.Treat){
        this.saveTreat(this.carCheckModel.car.carId, savedElement.carEquipmentId, savedElement.inputVal);
      }  
  }

  private itemClicked(item){
    this.currentCell = item;
    window.location.hash = ''
    window.location.hash = "#" + this.currentCell;    
  }


  private saveDamage(carId: number, carEquipmentId: number, input: string){
    let request = new AddEquipmentRequest(carId, this.carCheckModel.carCheckId, carEquipmentId, this.carCheckModel.checker.employeeId, input);      
    this.carCheckService.addEquipmentDamage(request).then((response) => {
      if(response.isSuccessWithResult()){            
        this.carCheckService.addEquipmentDamageList(response.data);
      }
    });
  }

  private saveTreat(carId: number, carEquipmentId: number, input: string){
    let request = new AddEquipmentRequest(carId, this.carCheckModel.carCheckId, carEquipmentId, this.carCheckModel.checker.employeeId, input);      
    this.carCheckService.addEquipmentTreat(request).then((response) => {
      if(response.isSuccessWithResult()){        
        this.carCheckService.addEquipmentDamageList(response.data, false);
      }
    });
  }

  private saveLack(carId: number, carEquipmentId: number, input: number){
    let request = new AddEquipmentLackRequest(carId, this.carCheckModel.carCheckId, carEquipmentId, this.carCheckModel.checker.employeeId, input);      
    this.carCheckService.addEquipmentLack(request).then((response) => {
      if(response.isSuccessWithResult()){            
        this.carCheckService.addEquipmentDamageList(response.data);
      }
    });
  }

  private close(closeElement){   
      if(closeElement.actionSelectionType == ActionSelectionTypes.Damage){     
        this.closeDamage(closeElement);
      }
      else if(closeElement.actionSelectionType == ActionSelectionTypes.Lack){
         this.updateLack(closeElement); 
      }  
  }

  private closeDamage(closeElement: any){
    let request = new CloseDamageRequest(this.carCheckModel.car.carId, closeElement.carEquipmentDamageId, this.carCheckModel.checker.employeeId);      
    this.carCheckService.closeEquipmentDamage(request).then((response) => {
      if(response.isSuccessWithResult()){            
        this.carCheckService.removeFromDamageList(closeElement.cellId, closeElement.carEquipmentDamageId);
      }
    });
  }

  private updateLack(closeElement: any){
    let request = new CloseLackRequest(this.carCheckModel.car.carId, closeElement.carEquipmentDamageId, this.carCheckModel.carCheckId, closeElement.inputVal, this.carCheckModel.checker.employeeId);      
    this.carCheckService.updateEquipmentLack(request).then((response) => {     
      if(response.isSuccessWithResult()){            
        this.carCheckService.updateLackList(closeElement.cellId, closeElement.carEquipmentDamageId, closeElement.inputVal);
      }
      else{
        this.carCheckService.removeFromDamageList(closeElement.cellId, closeElement.carEquipmentDamageId);
      }
    });
  }
}



