export class Sequence{
    private _sequence: number = 0;
    constructor(){

    }

    public set(seq_count: number)
    {
        this._sequence = seq_count
    }

    public next(){
        this._sequence++;
        return this._sequence;
    }

    public initialize()
    {
        this.set(0);
    }
}


