import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { MapStateService } from '../../../services/mapState/map-state.service';
import { NavigationService } from '../../../services/navigation/navigation.service';
import { NavigationData } from '../../../shared-classes/common/navigation-data';

@Component({
  selector: 'menu-item-button',
  templateUrl: './menu-item-button.component.html',
  inputs:['itemClass', 'data', 'imageClass', 'buttonText', 'circleColor', "isDisable"]
})
export class MenuItemButtonComponent {
  @Output() onClick: EventEmitter<any> = new EventEmitter();

  itemClass: string;
  data: any;
  imageClass: string;
  buttonText: string;
  circleColor: string;
  isDisable: boolean = false;
  
  constructor() {              
  }

  itemClicked(): void {    
    this.onClick.emit();
  }
}



