import { Component, OnInit } from '@angular/core';
import { EventGeneralInfo } from '../../shared-classes/event/event-general-info';
import * as moment from 'moment/moment';
import { AppConfig } from '../../app.config';
import { DateTimerService } from '../../services/general/date-timer-service';


@Component({
  selector:'event-general-info',
  templateUrl: './event-general-info.component.html',
  inputs: ['eventGeneralInfo', 'eventInfoType']
})

export class EventGeneralInfoComponent{

  eventGeneralInfo: EventGeneralInfo;
  dotsCounter: number = 5;
  isSpecialColor: boolean;
  dateTimerService: DateTimerService;
  clearTimer: any;
  eventStartDate: string;

  constructor(private config: AppConfig){
    
  }

  ngOnInit() {
    if(this.eventGeneralInfo){            
      let diff = this.config.getConfig('bold_event_time');
      let now = moment();
      let startDate = moment(this.eventGeneralInfo.eventStartDate);
      this.isSpecialColor = Math.abs(startDate.diff(now, 'minutes')) <= diff;   

      this.dateTimerService = new DateTimerService(this.eventGeneralInfo.eventStartDate);
      this.clearTimer = this.dateTimerService.getBeforeHours().subscribe(time => {        
        this.eventStartDate = time
      });
    }
  }

  
  ngOnDestroy(){
    if(this.clearTimer){
      this.clearTimer.unsubscribe();
    }    
  }
}



