import esri = require("esri");
import ArcGISDynamicMapServiceLayer = require("esri/layers/ArcGISDynamicMapServiceLayer");
import { Layer } from "./Layer";
export class DynamicMapServiceLayer extends ArcGISDynamicMapServiceLayer
{
    private layerMapping: {[layerName: string] : number};
    constructor(url: string, options: esri.ArcGISDynamicMapServiceLayerOptions)
    {
        super(url, options);        
        this.layerMapping = {};
    }

    addLayerMapping(layerName: string, layerId: number)
    {
        this.layerMapping[layerName.toUpperCase()] = layerId;
    }

    removeLayerMapping(layerName: string)
    {
        delete this.layerMapping[layerName.toUpperCase()];
    }

    getLayerID(layerName: string)
    {
        if (this.isLayerExists(layerName))
        {
            return this.layerMapping[layerName.toUpperCase()];
        }

        return null;
    }

    isLayerExists(layerName: string){
        return this.layerMapping.hasOwnProperty(layerName.toUpperCase());
    }
}


