import { IArrayStorage } from './IArrayStorage';
import { StorageResponse } from './StorageResponse';
import { ContentService } from '../general/content.service';

export class ArrayStorage<T> implements IArrayStorage<T>{
    public content: any;
    public errorMsgPromise: any;

    addItem(item: T): Promise<StorageResponse<boolean>> {
        throw new Error("Method not implemented.");
    }
    removeItem(itemID: number): Promise<StorageResponse<boolean>> {
        throw new Error("Method not implemented.");
    }
    getItemsByProperty(prop: any): Promise<StorageResponse<T>> {
        throw new Error("Method not implemented.");
    }
    getItemById(id: number): Promise<StorageResponse<T>> {
        throw new Error("Method not implemented.");
    }
    getItemIndex(item: T): Promise<StorageResponse<number>> {
        throw new Error("Method not implemented.");
    }
    getList(request: any): Promise<StorageResponse<Array<T>>> {
        throw new Error("Method not implemented.");
    }
    addList(items: Array<T>): Promise<StorageResponse<boolean>> {
        throw new Error("Method not implemented.");
    }
    removeList(): Promise<StorageResponse<boolean>> {
        throw new Error("Method not implemented.");
    }
    isItemExists(item: T): Promise<StorageResponse<boolean>> {
        throw new Error("Method not implemented.");
    }

    constructor(private contentService: ContentService){
        this.errorMsgPromise = contentService.load().then(()=>{
            this.content = contentService.getContent('messages');
        }); 
    };

  
}


