import { Injectable, EventEmitter } from '@angular/core';
import { StorageResponse } from '../../services/base/StorageResponse';
import { HomeService } from '../../services/home/home.service';
import { EventListService } from '../../services/event-list/event-list.service';
import { FireTruckPushMessage } from '../../shared-classes/node/fire-truck/fire-truck-push-message';
import { ShalhevetFireTruckPushMessageTrigger } from '../../shared-classes/node/fire-truck/shalhevet-fire-truck-push-message-trigger';
import { EventDetailsService } from '../event/event-details.service';
import { AlertifyPushMessage } from '../../shared-classes/node/alertify/alertify-push-message';
import { ShalhevetAlertifyPushMessageTrigger } from '../../shared-classes/node/alertify/shalhevet-alertify-push-message-trigger';
import { AlertifyService } from '../general/alertify.service';
import { CurrentEventDetailsService } from '../event/current-event-details.service';
import { EventDetailsData } from '../../shared-classes/event/event-details-data';
import { EventDetailsTypes } from '../../shared-classes/event/event-details-types';
import { NavigationData } from '../../shared-classes/common/navigation-data';
import { NavigationService } from '../navigation/navigation.service';
import { fireTruckInfo } from '../../shared-classes/common/fire-truck-info';
import * as io from 'socket.io-client';
import { RealtimeLayerPushMessage } from '../../shared-classes/node/realtime-layers/realtime-layers-pushpush-message';
import { RealtimeLayerPushMessageTrigger } from '../../shared-classes/node/realtime-layers/realtime-layer-push-message-trigger';
import { IGraphicLocationData } from '../../shared-classes/esri/IGraphicLocationData';
import { RealtimeLayerPushMessageData } from '../../shared-classes/node/realtime-layers/realtime-layers-push-message-data';
import { setInterval } from 'timers';
import { EventDetailsStatus } from '../../shared-classes/event/event-details-status';
import { AlertifyTypes } from '../../shared-classes/alertify/alertify-types';
import { ContentService } from '../general/content.service';
import { RoutePath } from '../../shared-classes/common/route-path';
import { HomeModel } from '../../shared-classes/home/home-model';
import { SoundService } from '../general/sound.service';
import { ConnectionService } from '../connection/connection.service';
import { ConnectionStateTypes } from '../../shared-classes/common/connection-state-types';
import { DeviceService } from '../general/device.service';
import { AppConfig } from '../../app.config';
import { LogInfo } from '../../shared-classes/common/log-info';
import { LogService } from '../general/log.service';
import { LogTypes } from '../../shared-classes/common/log-types';

const LAYER_UPDATE_EVENT_POSTFIX = '_REALTIME_DATA';

@Injectable()
export class NodeService {  
    private content: any;
    private sockets: any;
    private realtimeLayerDataEvents: {[event:string] : {[handle: number]: Function}} = {};
    private handlerIndex = 0;
    public $chatHistoryEmitter: EventEmitter<any>;
    public $chatServerBroadcastEmitter: EventEmitter<any>;
    private routes: RoutePath;

    constructor(
        private homeService: HomeService, 
        private eventListService: EventListService,
        private eventDetailsService: EventDetailsService,
        private alertifyService: AlertifyService,
        private currentEventDetailsService: CurrentEventDetailsService,
        private navigationService: NavigationService,
        private contentService: ContentService,
        private soundService: SoundService,
        private connectionService: ConnectionService,
        private config: AppConfig,
        private deviceService: DeviceService, 
        private logService:LogService
    ){    
        this.sockets = {};
        this.routes = new RoutePath();
        this.$chatHistoryEmitter = new EventEmitter(); 
        this.$chatServerBroadcastEmitter = new EventEmitter(); 
        
        this.contentService.load().then(()=>{
            this.content = this.contentService.getContent('messages'); 
        });    
    }

    public createSocket(namespace: string){        
        if(this.sockets.hasOwnProperty(namespace)){
            return;
        }

        let data = {
            query: this.config.getConfig('node_handshake_id') + "=" + this.deviceService.getDeviceId(),
            transports: ['websocket'],
            upgrade: false
          };            

        this.sockets[namespace] = io.connect(namespace, data);        
        this.sockets[namespace].on('disconnect', () =>  {
            //this.logService.addItem(new LogInfo("SOCKET DISCONNECTED: " + JSON.stringify(data), "node service", 'socket state', LogTypes.info));
        });        
        this.sockets[namespace].on('connect', () => {
            //this.logService.addItem(new LogInfo("CONNECT SOCKET : " + JSON.stringify(data), "node service", 'socket state', LogTypes.info));
        });          
        this.sockets[namespace].on('connect_error', (err) =>{
            //this.logService.addItem(new LogInfo("SOCKET ERROR: " + JSON.stringify(err), "node service", 'socket state', LogTypes.info));
        });        
    }

    public getSocket(namespace: string): any{
        if(this.sockets.hasOwnProperty(namespace)){
            return null;
        }

        return this.sockets[namespace];
    }

    public registerNodeFireTruckEvent(namespace: string, event: string)
    {        
        this.sockets[namespace].on(event, (message) =>
        {                        
          this.handleNodeMessage(message);
        });
    }

    public registerNodeAlertifyEvent(namespace: string, event: string){
        this.sockets[namespace].on(event, (message) =>
        {            
          this.handleAlertifyMessage(message);
        });
    }

    public registerChatEvent(namespace: string, event: string){
        this.sockets[namespace].emit('clientFirstConnection', event); 
 
        this.sockets[namespace].on('serverBroadcastMessage', (message) => {
             this.$chatServerBroadcastEmitter.emit(message);
        });
    
        this.sockets[namespace].on('serverUdpateChatHistory', (messagesHistoryArray) => {
             this.$chatHistoryEmitter.emit(messagesHistoryArray);
        }); 
    }

    public sendChatMessage(namespace: string, message: string, eventId: string){
        this.sockets[namespace].emit('clientSubmitMessage', { messageVal : message, room: eventId });
    }

    public unRegisterChatEvent(namespace: string, event: string){
        this.sockets[namespace].off('serverBroadcastMessage');    
        this.sockets[namespace].off('serverUdpateChatHistory');
    }

    private getLayerUpdateEventName(layerName: string) : string
    {
        return (layerName + LAYER_UPDATE_EVENT_POSTFIX); 
    }

    private isLayerUpdateEventRegistered(layerName: string)
    {
        return this.getLayerUpdateEventName(layerName) in this.realtimeLayerDataEvents;
    }

    private getLayerUpdateSubscribers(layerName: string) : {[handle: number]: Function}
    {
        return (this.isLayerUpdateEventRegistered(layerName)) ? this.realtimeLayerDataEvents[this.getLayerUpdateEventName(layerName)] : {};        
    }

    public registerLayerUpdateEvent(namespace: string, layerName: string, callback:Function)
    {                
        if (!this.isLayerUpdateEventRegistered(layerName)) //checking if event internally registered for any map
        {                
            this.realtimeLayerDataEvents[this.getLayerUpdateEventName(layerName)] = {}; //init empty  subscribers for event            
        }
        
        if (Object.keys(this.realtimeLayerDataEvents[this.getLayerUpdateEventName(layerName)]).length === 0) //is new event?
        {
            this.registerNodeLayerUpdateEvent(namespace, this.getLayerUpdateEventName(layerName));// registring node event
        }

        this.realtimeLayerDataEvents[this.getLayerUpdateEventName(layerName)][this.handlerIndex] = callback;

        this.handlerIndex++;
        return this.handlerIndex - 1;
    }

    unRegisterLayerUpdateEvent(namespace: string, layerName: string, handlerId: number)
    {
        if (!this.isLayerUpdateEventRegistered(layerName))
        {
            return;
        }

        delete this.realtimeLayerDataEvents[this.getLayerUpdateEventName(layerName)][handlerId];        

        if (Object.keys(this.realtimeLayerDataEvents[this.getLayerUpdateEventName(layerName)]).length === 0) //if no subscribers left
        {
            this.unregisterNodeLayerUpdateEvent(namespace, this.getLayerUpdateEventName(layerName)); // unregister node event
        }
    }

   private registerNodeLayerUpdateEvent(namespace, event: string)
    {
        this.sockets[namespace].on(event, (message) =>
        {     
            console.log("namespace: " + namespace + " event: " + event + " data recivied: " + message);       
            this.handleRealtimeLayer(message);
        });
    }

    private unregisterNodeLayerUpdateEvent(namespace, event: string)
    {
        this.sockets[namespace].off(event, (message) =>
        {            
          this.handleRealtimeLayer(message);
        });
    }

    demoRealtimePush(){
        let obj = <RealtimeLayerPushMessage>{
                data:                     
                {
                    layer:"POLICE_CAR",
                    data:[{                                        
                    geometry: {
                        id:543,
                        x: 181455,
                        y: 656842,
                        type: 0
                    }},
                    {                    
                    geometry: {
                        id:5431,
                        x: 181598,
                        y: 656834,
                        type: 0
                    }
                }]
                },
                pushMessageTrigger: RealtimeLayerPushMessageTrigger.UpdateLayerData
        }

        setInterval(()=>{

            obj.data.data[0].geometry.x = obj.data.data[0].geometry.x + (Math.floor(Math.random() * 100) + 1);
            obj.data.data[0].geometry.y = obj.data.data[0].geometry.y + (Math.floor(Math.random() * 100) + 1);
            obj.data.data[1].geometry.x = obj.data.data[1].geometry.x + (Math.floor(Math.random() * 100) + 1);
            obj.data.data[1].geometry.y = obj.data.data[1].geometry.y + (Math.floor(Math.random() * 100) + 1);

            this.handleRealtimeLayer(obj);
        }, 1000)
    } 

    handleRealtimeLayer(message: any): Promise<StorageResponse<boolean>>
    {
        if(message === undefined || message.data === undefined || message.data.data === undefined){
            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<boolean>().CreateErrorResponse(
                    "message from node server is null or undefined."
                ));
            });
        }
                
        let layerData = message.data.data;
        
        this.upadateRealtimeLayer(layerData);

        return new Promise((resolve, reject)=>{
            resolve(new StorageResponse<boolean>().CreateErrorResponse(
                "layer updated"
            ))
        });
    }

    upadateRealtimeLayer(msgData: RealtimeLayerPushMessageData)
    {
        let subscribers = this.getLayerUpdateSubscribers(msgData.layer);
        for (const key in subscribers) {
            if (subscribers.hasOwnProperty(key)) {
                const callback = subscribers[key];         
                
                try{
                    callback(msgData);
                }
                catch(e){
                    console.log('error in upadateRealtimeLayer: ' + e);
                }


            }
        }      
    }

    public handleNodeMessage(message: any): Promise<StorageResponse<boolean>>{
        this.logService.addItem(new LogInfo("PUSH NODE DATA RECIEVED : " + JSON.stringify(message.data), "node service", 'debugger'));

        if(message === undefined || message.data === undefined){
            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<boolean>().CreateErrorResponse(
                    "message from node server is null or undefined."
                ));
            });
        }
        console.log("PUSH NODE DATA RECIEVED : " + JSON.stringify(message.data));

        let fireTruckData = <FireTruckPushMessage>message.data;

        switch(fireTruckData.pushMessageTrigger){
            case ShalhevetFireTruckPushMessageTrigger.ActiveEventAdded:
            {
                this.eventAdded(fireTruckData.data);
            }
            break;
            case ShalhevetFireTruckPushMessageTrigger.ActiveEventClosed:
            {
                this.eventClosed(fireTruckData.data);
            }
            break;
            case ShalhevetFireTruckPushMessageTrigger.ActiveEventInfoUpdated:
            {                                            
                this.eventUpdated(fireTruckData.data);                                         
            }
            break;
            case ShalhevetFireTruckPushMessageTrigger.ActiveEventLocationUpdated:
            {                                            
                this.eventLocationUpdated(fireTruckData.data);                                         
            }
            break;
            case ShalhevetFireTruckPushMessageTrigger.ActiveEventLogChanged:
            {                
                this.eventDetailsService.updateItemLog(fireTruckData.data);
                this.currentEventDetailsService.updateItemLog(fireTruckData.data);
            }
            break;
            case ShalhevetFireTruckPushMessageTrigger.ActiveEventForcesChanged:
            {                                
                this.eventForceChanged(fireTruckData.data);
            }
            break;
            case ShalhevetFireTruckPushMessageTrigger.ActiveEventMissionStatusChanged:
            {                                                                                
                this.currentEventDetailsService.updateAcitveMission(fireTruckData.data);                                           
            }
            break;
            case ShalhevetFireTruckPushMessageTrigger.FireTruckInfoChange:
            {                                                     
                this.homeService.updateFireTruckData(fireTruckData.data);
                this.eventUpdated(fireTruckData.data.activeAtEvent); 

                if(fireTruckData.data.activeAtEvent == null){
                    this.currentEventDetailsService.removeItem(); //remove virtual event
                }
            }
            break;
            case ShalhevetFireTruckPushMessageTrigger.ActiveEventAreaChanged:
            {                
                this.eventUpdated(fireTruckData.data);
            }
        }

        return new Promise((resolve, reject) => {            
            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });
    }

    public handleAlertifyMessage(message: any): Promise<StorageResponse<boolean>>{
        if(message === undefined || message.data === undefined){
            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<boolean>().CreateErrorResponse(
                    "message from node server is null or undefined."
                ));
            });
        }
        
        console.log("PUSH ALERTIFY DATA RECIEVED : " + JSON.stringify(message.data));

        this.alertifyService.eventNotification(<AlertifyPushMessage>message.data);

        return new Promise((resolve, reject) => {            
            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });
    }

    private eventClosed(eventId){
        this.homeService.updateActiveEventCount(false);
        this.eventListService.removeItem(eventId);
        this.eventDetailsService.removeItem(eventId);
        this.currentEventDetailsService.removeItem(eventId);
        this.navigateToEvents();
    }

    private eventUpdated(fireTruckData){   
        if(fireTruckData == null){
            return;
        }   

        if(fireTruckData.generalInfo.eventId != -1){
            this.eventListService.addItem(fireTruckData.generalInfo);   
        }
        
        this.currentEventDetailsService.isCurrentEventByCarId(fireTruckData).then((response) => {
            if(response.isSuccess()){
                this.currentEventDetailsService.updateItem(fireTruckData);                                           
            }
            else if(fireTruckData.generalInfo.eventId != -1){
                this.eventDetailsService.updateItem(fireTruckData);    
            }
        });
    }

    private eventLocationUpdated(fireTruckData){  
        if(fireTruckData == null){
            return;
        }      

        this.eventListService.addItem(fireTruckData.generalInfo);
        this.eventDetailsService.updateItemLocation(fireTruckData);
        this.currentEventDetailsService.isCurrentEventByCarId(fireTruckData).then((response) => {
            if(response.isSuccess()){
                this.currentEventDetailsService.updateItemLocation(fireTruckData);                                           
            }
        });
    }

    private eventAdded(fireTruckData){  
        if(fireTruckData == null){
            return;
        }              


        this.homeService.updateActiveEventCount(true);
        this.eventListService.addItem(fireTruckData.generalInfo); 

        this.currentEventDetailsService.getCurrentCarTeam().then((response) => {
            if((!response.isSuccessWithResult()) 
                || response.data.activeMission.currentMissionStatus.missionStatusId == EventDetailsStatus.Finish
                || response.data.activeMission.currentMissionStatus.missionStatusId == EventDetailsStatus.BackOnStation){
                let pThis = this;

                this.soundService.play();

                this.alertifyService.notify(
                   this.alertifyService.createEventDetailNotification(fireTruckData.generalInfo), 
                   AlertifyTypes.notify, 
                   "event-details-notification", 
                   function(clicked){ 
                    pThis.alertifyService.clearNotifyAdditionalClass("event-details-notification");
                    
                    if(!clicked){
                        return;
                    }
                    
                    pThis.eventDetailsService.updateItemLocation(fireTruckData);

                    let data = new EventDetailsData(fireTruckData.generalInfo.eventId, EventDetailsTypes.CarRequest, true);
                    pThis.navigationService.navigate(new NavigationData(pThis.routes.EVENT_DETAILS, data));  
                   },
                   30
                )
            }
        });
    }

    private eventForceChanged(fireTruckData){  
        if(fireTruckData == null){
            return;
        }
              
        this.eventListService.addItem(fireTruckData.generalInfo);
        this.eventDetailsService.updateItem(fireTruckData);
        this.currentEventDetailsService.isCurrentEventByCarId(fireTruckData).then((response) => {
            if(response.isSuccess()){
                if(this.currentEventDetailsService.isCurrentEventById(fireTruckData.generalInfo.eventId)){
                    this.currentEventDetailsService.updateItem(fireTruckData);    
                    
                    this.currentEventDetailsService.getCurrentCarTeam().then((response) => {
                        if(response.data.activeMission.currentMissionStatus.missionStatusId == EventDetailsStatus.Dispatch){
                            let data = new EventDetailsData(fireTruckData.generalInfo.eventId, EventDetailsTypes.CarRequest);
                            this.navigationService.navigate(new NavigationData(this.routes.EVENT_DETAILS, data));  
                        }
                    });
                }
                else{
                    this.currentEventDetailsService.addItem(fireTruckData, true);
                }                
            }
            else{
                this.currentEventDetailsService.removeItem(fireTruckData.generalInfo.eventId);            
            }
        });
    }

    private navigateToEvents(){
        this.navigationService.navigate(new NavigationData(this.routes.EVENTS));
    }
}


