import { ItemStorage } from '../base/ItemStorage';
import { Injectable, EventEmitter } from '@angular/core';
import { HttpService } from '../../core/http.service';
import { StorageResponse } from '../base/StorageResponse';
import { EventPreventionModel } from '../../shared-classes/event/event-prevention-model';
import { PreventionCaseEventData } from '../../shared-classes/event/prevention-case-event-data';
import { ContentService } from '../general/content.service';
import { AlertifyService } from '../general/alertify.service';
import { ApplicationResponseStatus } from '../../shared-classes/response/ApplicationResponseStatus';

@Injectable()
export class PreventFireService extends ItemStorage<EventPreventionModel>{

    constructor(
        private httpService: HttpService,
        contentService: ContentService,
        private alertifyService: AlertifyService
    ){   
        super(contentService);          
    }

    public getPreventionInfo(request: any): Promise<StorageResponse<EventPreventionModel>> {                           
        return new Promise((resolve, reject) => {                         
            let url = this.httpService.baseWebApi + 'Events/GetEventPreventionInfo/';
            this.httpService.post<StorageResponse<any>>(url, request).subscribe(
                (response) => { 
                    if(response.resultStatus == ApplicationResponseStatus.Fail){
                        this.errorMsgPromise.then(() => {                     
                            this.alertifyService.error(this.content.getDataErrorMessage);
                            resolve(new StorageResponse<EventPreventionModel>().CreateFailResponse());    
                        });                       
                    }  
                    else{
                        resolve(new StorageResponse<EventPreventionModel>().CreateResultResponse(
                            response.data
                        ));    
                    }
                },
                (err) => {
                    this.errorMsgPromise.then(() => {                     
                        this.alertifyService.warring(this.content.getConnectionErrorMessage);
                        resolve(new StorageResponse<EventPreventionModel>().CreateFailResponse());    
                    });
                },                  
            );
        });                 
    }

    public getPreventionDetails(request: any): Promise<StorageResponse<Array<PreventionCaseEventData>>> {                           
        return new Promise((resolve, reject) => {                           
            let url = this.httpService.baseWebApi + 'Events/GetBuildingCaseList/';
            this.httpService.post<StorageResponse<any>>(url, request).subscribe(
                (response) => {     
                    if(response.resultStatus == ApplicationResponseStatus.Fail){
                        this.errorMsgPromise.then(() => {                     
                            this.alertifyService.error(this.content.getDataErrorMessage);
                            resolve(new StorageResponse<Array<PreventionCaseEventData>>().CreateFailResponse());    
                        });
                    }  
                    else{
                        resolve(new StorageResponse<Array<PreventionCaseEventData>>().CreateResultResponse(
                            response.data
                        ));    
                    }
                },
                (err) => {
                    this.errorMsgPromise.then(() => {                     
                        this.alertifyService.warring(this.content.getConnectionErrorMessage);
                        resolve(new StorageResponse<Array<PreventionCaseEventData>>().CreateFailResponse());    
                    });
                },                  
            );
        });                 
    }
}


