import { Component, OnInit } from '@angular/core';
import { EventInfo } from '../../shared-classes/event/event-info';
import { EventGeneralInfo } from '../../shared-classes/event/event-general-info';
import { CurrentEventDetailsService } from '../../services/event/current-event-details.service';
import { menuTypes } from '../../shared-classes/common/menu-types';
import { detailsbarTypes } from '../../shared-classes/common/detailsbar-types';
import { NavigationService } from '../../services/navigation/navigation.service';
import { ContentService } from '../../services/general/content.service';
import { EmapService } from '../../core/emap.service';
import { CustomMap } from '../../shared-classes/esri/custom-map';
import { ProjectionService } from '../../services/general/projection.service';
import { Response } from '@angular/http/src/static_response';
import { Extent } from '../../shared-classes/esri/Extent';
import { StorageResponse } from '../../services/base/StorageResponse';
import { CurrentStatusModel } from '../../shared-classes/event/current-status-model';
import { EventTypes } from '../../shared-classes/event/event-types';
import { setTimeout } from 'timers';
import { EventDetailsService } from '../../services/event/event-details.service';
import { BaseComponent } from '../../shared-classes/common/base-component';
import { EventRequest } from '../../shared-classes/request/event-request';
import { MapStateService } from '../../services/mapState/map-state.service';

@Component({
 selector:'current-status',
  templateUrl: './current-status.component.html'
})

export class CurrentStatusComponent extends BaseComponent{
  private menuType: menuTypes = menuTypes.nomenu;
  private detailsbarType: detailsbarTypes = detailsbarTypes.minimizeEvent;
  private navigationData: any;
  private isTeamsCellShow: boolean = true;
  private isReportsCellShow: boolean = true;
  private isAreasCellShow: boolean = true;
  private mapWidth: string = "1443px";
  private mapHeight: string = "240px";
  private currentStatus: CurrentStatusModel;
  private eventUpdated: any;
  private currentEventUpdated: any;
  private currentEventAdded: any;  
  private currentStatusUpdated: any;    
  private isHideMapButtons: boolean = true;
  private isLoadAllLogsShow: boolean = true;

  constructor(
    private currentEventDetailsService: CurrentEventDetailsService,
    private eventDetailsService: EventDetailsService,
    navigationService: NavigationService,
    contentService: ContentService,
    private emapService: EmapService,
    private projService: ProjectionService,
    mapStateService: MapStateService
  ) {  
    super(contentService, 'current-status');
    mapStateService.changeMapState(false);
    let data = navigationService.getNavigationData();
    
    if(this.currentEventDetailsService.isCurrentEventById(data.eventId)){
      this.currentStatus = currentEventDetailsService.getCurrentStatus();
    }
    else{
      this.currentStatus = eventDetailsService.getCurrentStatus();
    }
      
    this.eventUpdated = eventDetailsService.$eventUpdatedEmitter.subscribe((eventInfo) => {
      this.updateStatus(eventDetailsService.getCurrentStatus());
    });

    this.currentEventUpdated = this.currentEventDetailsService.$currentEventUpdatedEmitter.subscribe((eventInfo) => {
      this.updateStatus(currentEventDetailsService.getCurrentStatus());
    });

    this.currentStatusUpdated = this.currentEventDetailsService.$currentStatusUpdatedEmitter.subscribe((eventInfo) => {
      this.updateStatus(currentEventDetailsService.getCurrentStatus());
    });    

    this.currentEventAdded = this.currentEventDetailsService.$currentEventAddedEmitter.subscribe((eventInfo) => {
      this.updateStatus(currentEventDetailsService.getCurrentStatus());
    });
  }

  ngAfterViewInit() {    
    if(this.currentStatus && this.currentStatus.generalInfo && this.currentStatus.generalInfo.lat && this.currentStatus.generalInfo.lon){
      this.emapService.getMap("statusMap").then((map: CustomMap) => {         
            let coords = this.projService.transformWGS1984ToITM(this.currentStatus.generalInfo.lat, this.currentStatus.generalInfo.lon);
            map.setCenter({x: coords.x, y: coords.y, level: map.getNumberOfLevels() - 1 })
      });
    }
  }

  ngOnDestroy(){
    this.eventUpdated.unsubscribe();
    this.currentEventUpdated.unsubscribe();
    this.currentEventAdded.unsubscribe(); 
    this.currentStatusUpdated.unsubscribe();     
  }

  private resizeMap(){     
    let upperTableHeight = !this.isTeamsCellShow && !this.isReportsCellShow ? 240 : 0;
    let lowerTableHeight = !this.isAreasCellShow ? 90 : 0;
    this.mapHeight = (240 + upperTableHeight + lowerTableHeight) + "px"

    this.emapService.getMap("statusMap").then((map: CustomMap) => {
      setTimeout(()=>{
        map.resize(true);         
        map.reposition();
      }, 0);
    });
  }

  private loadAllLogs(){
    this.eventDetailsService.getAllEventLogs(new EventRequest(this.currentStatus.generalInfo.eventId)).then((response) => {
      if(response.isSuccessWithResult()){        
        this.currentStatus.log = response.data;
        this.isLoadAllLogsShow = false;
      }      
    });
  }

  private updateStatus(currentStatus: CurrentStatusModel){
    this.currentStatus.areas = currentStatus.areas;
    this.currentStatus.atEvent = currentStatus.atEvent;
    this.currentStatus.generalInfo = currentStatus.generalInfo;
    this.currentStatus.log = currentStatus.log;
    this.currentStatus.toEvent = currentStatus.toEvent;
    this.currentStatus.carsSummary = currentStatus.carsSummary;
    this.currentStatus.employeesSummary = currentStatus.employeesSummary;
  }
}


