import { Component, Output, EventEmitter } from '@angular/core';
import { menuTypes } from '../../shared-classes/common/menu-types';
import { ContentService } from '../../services/general/content.service';
import { ConnectionService } from '../../services/connection/connection.service';
import { ConnectionStateTypes } from '../../shared-classes/common/connection-state-types';
import { NavigationService } from '../../services/navigation/navigation.service';
import { MapStateService } from '../../services/mapState/map-state.service';
import { NavigationData } from '../../shared-classes/common/navigation-data';
import { QuickReportService } from '../../services/quick-report/quick-report.service';
import { QuickReportTypes } from '../../shared-classes/quick-report/quick-report-types';
import { Dictionary } from '../../shared-classes/IDictionary';
import { detailsbarTypes } from '../../shared-classes/common/detailsbar-types';
import { HomeService } from '../../services/home/home.service';
import { CarCheckService } from '../../services/car/car-check-service';
import { ActiveCarCheckRequest } from '../../shared-classes/request/active-car-check.request';
import { CarCheckData } from '../../shared-classes/car/car-check-data';
import { DropDownData } from '../../shared-classes/common/drop-down-data';
import { RoutePath } from '../../shared-classes/common/route-path';
import { CurrentEventDetailsService } from '../../services/event/current-event-details.service';

@Component({
  selector: 'menu',
  templateUrl: './menu.component.html',
  inputs:['menuType', 'data', 'currentCell']
})
export class MenuComponent {
  @Output() onItemClicked: EventEmitter<number> = new EventEmitter();

  menuType: menuTypes;
  data: any;
  content: any;
  isOffline: boolean = false;
  private isNotQualifiedShow: boolean = false;
  private isQualified: boolean = false;
  private reasonSelected = new DropDownData();
  private reasonsList: Array<DropDownData>;
  private navigationData: any;
  private detailsbarType: detailsbarTypes = detailsbarTypes.default;
  private qualifiedMenuType: menuTypes = menuTypes.nomenu;
  private isReasonValid: boolean = true;
  private currentCell: number;
  private routes: RoutePath;

  constructor(
    private contentService: ContentService, 
    private connectionService: ConnectionService,
    private navigationService: NavigationService, 
    private mapStateService: MapStateService,
    private quickReportService: QuickReportService,
    private homeService: HomeService,
    private carCheckService: CarCheckService,
    private currentEventDetailsService: CurrentEventDetailsService
  ) { 
      this.routes = new RoutePath();

      this.contentService.load().then(()=>{        
        this.content = this.contentService.getContent('menu-home');
        this.navigationData = this.setMappingNavigationData();
      });        

      this.connectionService.stateChanged.subscribe(state => 
      {
          this.isOffline = this.connectionService.isOffline();
      });
  }

  navigate(navUrl: string, isMapVisible: boolean){
    this.navigationService.navigate(new NavigationData(navUrl));
    this.mapStateService.changeMapState(isMapVisible);  
  }

  navigateToEmployees(){
    this.currentEventDetailsService.getItem().then((res)=>{
      this.navigationService.navigate(new NavigationData(this.routes.EMPLOYEES_TEAM, { isBack: !res.isSuccessWithResult() }));
      this.mapStateService.changeMapState(false);  
    });      
  }

  private valueChanged(value: DropDownData){    
    this.reasonSelected = value;
  }

  navigateToCarCheck(){
   this.homeService.getCar().then((response) => {
    this.carCheckService.getActiveCarCheck(new ActiveCarCheckRequest(response.data.carId)).then((res) => {

      if(res.isSuccessWithResult()){          
        this.navigationService.navigate(new NavigationData(this.routes.CAR_CHECK, res.data));
      }
      else{
        this.navigationService.navigate(new NavigationData(this.routes.CAR_CHECK_START, new CarCheckData(response.data.carCallCode)));
      }

      this.mapStateService.changeMapState(false);
    });
  });
  }

  navigateToCarStatus(){
    this.homeService.getCar().then((response) => {
        this.navigationService.navigate(new NavigationData(this.routes.CAR_STATUS, { carName: response.data.carCallCode }));
      });  
   }

  itemClicked(cellId){  
    this.onItemClicked.emit(cellId);   
  }

  showPopup(){
    this.isNotQualifiedShow = true;
    this.quickReportService.getNotQualifedReasons().then((response) => {
      if(response.isSuccessWithResult()){
        this.reasonsList = response.data;
      }
    });
  }

  report(reportType){
    let data = this.navigationService.getNavigationData();
    this.quickReportService.sendQuickReport(data.eventId, <QuickReportTypes>reportType);
    this.navigationService.goBack();
  }

  sendQualifiedReport(){        
    this.isReasonValid = this.isQualified || (!this.isQualified && this.reasonSelected && this.reasonSelected.value != undefined);

    if(this.isReasonValid)
    {
      let data = this.navigationService.getNavigationData();
      this.quickReportService.sendQuickReport(data.eventId, this.isQualified ?  QuickReportTypes.CarActive : QuickReportTypes.Broken, this.reasonSelected.text);
      this.navigationService.goBack();  
    }
  }

  private setMappingNavigationData(){           
    return {         
        buttonRight: {
            callback: () => {
                this.isNotQualifiedShow = false;
            },
            class: "image-sprite tab-icn-x"
        },
        buttonLeft: {
            callback: () => { 
                this.sendQualifiedReport()               
            }, 
            class: "image-sprite tab-icn-ok"
        },
        title: this.content.qualifiedTitle
    };
  }
}



