import { Injectable } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';
import { NavigationData } from '../../shared-classes/common/navigation-data';

@Injectable()
export class NavigationService {
  
    private data: any;

    constructor(
        private route: ActivatedRoute,
        private router: Router,
        private location: Location){              
    }

    navigate(navigationData: NavigationData): void {
        this.data = navigationData.data;
        
        if(!navigationData.params){
            this.router.navigate([navigationData.navigateTo]);
        }
        else{
            this.router.navigate([navigationData.navigateTo, navigationData.params]);
        }     
    }

    getRouter(){
        return this.router;
    }
    
    getNavigationData(){
        return this.data;
    }

    goBack(): void {
        this.location.back();
    }
}


