import { Component, OnInit } from '@angular/core';
import { BaseComponent } from '../shared-classes/common/base-component';
import { LogInfo } from '../shared-classes/common/log-info';
import { LogService } from '../services/general/log.service';
import { ContentService } from '../services/general/content.service';

@Component({
 selector:'logs',
  templateUrl: './logs.component.html'
})

export class LogsComponent extends BaseComponent{
 logs: Array<LogInfo>;

  constructor(
    private logService: LogService,
    private contentService: ContentService
  ) {   
    super(contentService, "logs")
    this.logs = new Array<LogInfo>();
  }

  ngOnInit() {                   
    this.logService.getList().then((response) => {
      this.logs = response.data;
    });
  }

  deleteAll(){
    this.logService.clearList();
    this.logs = new Array<LogInfo>();
  }
}



