/** Routing declartion */
import { Routes, RouterModule } from "@angular/router";
import { NgModule } from '@angular/core';

import { AuthGuard } from './guards/auth.guard';
import { ChangesNotSavedGuard } from './guards/changes-not-saves.guard';
import{ DefaultLayoutComponent } from '../layouts/default-layout/default-layout.component';
import{ HomeLayoutComponent } from '../layouts/home-layout/home-layout.component';
import { EventListComponent } from "../events/event-list/event-list.component";
import { LogsComponent } from "../logs/logs.component";
import { EmployeesTeamComponent } from "../employees/employees-team/employees-team.component";
import { CarCheckComponent } from "../car/car-check/car-check.component";
import { CarStatusComponent } from "../car/car-status/car-status.component";
import { CarCheckStartComponent } from "../car/car-check-start/car-check-start.component";
import { EventDetailsComponent } from "../events/event-details/event-details.component";
import { SuppliesComponent } from "../supplies/supplies.component";
import { QuickReportComponent } from "../quick-report/quick-report.component";
import { CurrentStatusComponent } from '../events/current-status/current-status.component';
import { PreventFireComponent } from '../events/prevent-fire/prevent-fire.component';
import { ReportComponent } from "../report/report.component";

const routes: Routes = [
  {
    path: '',
    component: HomeLayoutComponent,
    data: {pageTitle: ''},
    children:[
      {
          path: '',
          component: HomeLayoutComponent
      },
  ]
},
{
    path: '',
    component: DefaultLayoutComponent,
    data: {pageTitle: ''},
    children:      
    [
      {
        path: 'events',
        component: EventListComponent
      },
      {
        path: 'employees-team',
        component: EmployeesTeamComponent
      },
      {
        path: 'logs',
        component: LogsComponent
      },
      {
        path: 'car-check',  
        component: CarCheckComponent
      },
      {
        path: 'car-status',  
        component: CarStatusComponent
      },
      {
        path: 'car-check-start',  
        component: CarCheckStartComponent
      },
      {
        path: 'supplies',  
        component: SuppliesComponent
      },
      {
        path: 'quick-report',  
        component: QuickReportComponent
      },
      {
        path: 'current-status',
        component: CurrentStatusComponent
      },
      {
        path: 'prevent-fire',
        component: PreventFireComponent
      },
      {
        path: 'report',
        component: ReportComponent
      }
  ]
  },
  {
    path: 'event-details',
    component: EventDetailsComponent
  }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes)
  ],
  exports: [
    RouterModule
  ],
  providers: [
    AuthGuard, 
    ChangesNotSavedGuard
  ]
})
export class AppRoutingModule { }



