import { ItemStorage } from '../base/ItemStorage';
import { Injectable } from '@angular/core';
import { EventInfo } from '../../shared-classes/event/event-info';
import { HttpService } from '../../core/http.service';
import { StorageResponse } from '../base/StorageResponse';
import { ConnectionService } from '../connection/connection.service';
import { OfflineStorage } from '../../stores/offlineStorage/OfflineStorage';
import { EventGeneralInfo } from '../../shared-classes/event/event-general-info';
import { EventLog } from '../../shared-classes/event/event-log';
import { EventForceInfo } from '../../shared-classes/event/event-force-info';
import { fireTruckInfo } from '../../shared-classes/common/fire-truck-info';
import { DeviceRequest } from '../../shared-classes/request/device-request';
import { HomeService } from '../home/home.service';
import { DeviceService } from '../general/device.service';
import { ActiveMission } from '../../shared-classes/common/active-mission';
import { EventDetailsTypes } from '../../shared-classes/event/event-details-types';
import { EventEmitter } from '@angular/core';
import { NavigationService } from '../navigation/navigation.service';
import { NavigationData } from '../../shared-classes/common/navigation-data';
import { EventDetailsData } from '../../shared-classes/event/event-details-data';
import { ChangeMissionStatusRequest } from '../../shared-classes/request/change-mission-status-request';
import { CarTeam } from '../../shared-classes/car/car-team';
import { LaunchMissionStatusRequest } from '../../shared-classes/request/launch-mission-status-request';
import { ControlTimeRequest } from '../../shared-classes/request/control-time-request';
import { CurrentStatusModel } from '../../shared-classes/event/current-status-model';
import { RoutePath } from '../../shared-classes/common/route-path';
import { WazeService } from '../general/waze.service';
import { CancelMissionRequest } from '../../shared-classes/request/cancel-mission-request';
import { NewMissionStatusRequest } from '../../shared-classes/request/new-mission-status-request';
import { UpdateNewMissionStatusRequest } from '../../shared-classes/request/update-new-mission-status-request';
import { ContentService } from '../general/content.service';
import { AlertifyService } from '../general/alertify.service';
import { FireTruckOfflineActionTypes } from '../../shared-classes/offline/fire-truck-offline-action-types';
import { ApplicationResponseStatus } from '../../shared-classes/response/ApplicationResponseStatus';

@Injectable()
export class CurrentEventDetailsService extends ItemStorage<EventInfo> {
    private storageItem: EventInfo;
    private localStorageKey: string = "currentEventInfo";
    public $currentEventAddedEmitter: EventEmitter<EventInfo>;
    public $currentEventUpdatedEmitter: EventEmitter<EventInfo>;
    public $currentEventRemovedEmitter: EventEmitter<number>;
    public $currentEventLocationUpdatedEmitter: EventEmitter<EventInfo>;    
    public $currentActiveMissionUpdatedEmitter: EventEmitter<ActiveMission>;
    public $currentStatusUpdatedEmitter: EventEmitter<EventInfo>;
    
    private routes: RoutePath;

    constructor(
        private httpService: HttpService,
        private connectionService: ConnectionService,
        private store: OfflineStorage,
        private deviceService: DeviceService,
        private homeService: HomeService,
        private navigationService: NavigationService,
        contentService: ContentService,
        private alertifyService: AlertifyService 

    ){              
        super(contentService);

        this.$currentEventAddedEmitter = new EventEmitter();   
        this.$currentEventUpdatedEmitter = new EventEmitter();   
        this.$currentEventLocationUpdatedEmitter = new EventEmitter(); 
        this.$currentEventRemovedEmitter = new EventEmitter();
        this.$currentActiveMissionUpdatedEmitter = new EventEmitter();
        this.$currentStatusUpdatedEmitter = new EventEmitter();
        
        this.routes = new RoutePath();
    }

    public getItem(): Promise<StorageResponse<EventInfo>> {                                   
        if(this.connectionService.isOffline())
        {
            this.storageItem =  <EventInfo>JSON.parse(this.store.getItem(this.localStorageKey));
        }
        
        if(this.storageItem != null) 
        {
            return new Promise((resolve, reject) => {  
                resolve(new StorageResponse<EventInfo>().CreateResultResponse(
                    this.storageItem
                ));
            });
        }
        else{
            return new Promise((resolve, reject) => {  
                resolve(new StorageResponse<EventInfo>().CreateResultResponse(
                    null
                ));
            });
        }             
    }

    public addItem(item: EventInfo, isRedirect: boolean = true): Promise<StorageResponse<boolean>> {
        if(!item){
            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<boolean>().CreateErrorResponse(
                    "the given item is null or empty."
                ));
            });
        }

        this.storageItem = item;
        this.store.setItem(this.localStorageKey, JSON.stringify(item));  
        
        if(isRedirect){
            this.homeService.setActiveAtEvent(item);                        
            let data = new EventDetailsData(this.storageItem.generalInfo.eventId, EventDetailsTypes.CurrentEvent, true);
            this.navigationService.navigate(new NavigationData(this.routes.EVENT_DETAILS, data));   

            if(this.storageItem.generalInfo.eventId == -1){
                this.$currentEventUpdatedEmitter.emit(this.storageItem); 
            }
        }
        
        return new Promise((resolve, reject) => {            
            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });       
    }

    public updateItemLog(info: EventInfo): Promise<StorageResponse<boolean>> {
        if(!info || !info.eventLog || !this.storageItem){
            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<boolean>().CreateErrorResponse(
                    "the given log is null or empty."
                ));
            });
        }

        if(info.generalInfo.eventId == this.storageItem.generalInfo.eventId){
            this.storageItem.eventLog = info.eventLog;
            this.addItem(this.storageItem, false);
            this.$currentStatusUpdatedEmitter.emit(this.storageItem);
        }

        return new Promise((resolve, reject) => {            
            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });       
    }

    public updateItem(item: EventInfo): Promise<StorageResponse<boolean>> {
        if(!item){
            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<boolean>().CreateErrorResponse(
                    "the given item is null or empty."
                ));
            });
        }

        if(!this.storageItem){
            this.addItem(item);            
        }
        else if(item.generalInfo.eventId == this.storageItem.generalInfo.eventId){
            this.update(item);
            this.$currentEventUpdatedEmitter.emit(this.storageItem); 
        }        
        
        return new Promise((resolve, reject) => {            
            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });       
    }

    public updateItemLocation(item: EventInfo): Promise<StorageResponse<boolean>> {
        if(!item){
            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<boolean>().CreateErrorResponse(
                    "the given item is null or empty."
                ));
            });
        }
        
        if(item.generalInfo.eventId == this.storageItem.generalInfo.eventId){
            this.update(item);
            this.$currentEventLocationUpdatedEmitter.emit(this.storageItem); 
        }        
        
        return new Promise((resolve, reject) => {            
            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });       
    }

    private update(item){
        this.storageItem.areas = item.areas;
        this.storageItem.eventLog = item.eventLog;
        this.storageItem.forcesInfo = item.forcesInfo;
        this.storageItem.generalInfo = item.generalInfo;
        this.store.setItem(this.localStorageKey, JSON.stringify(item)); 
    }

    public isCurrentEventByCarId(item: EventInfo): Promise<StorageResponse<boolean>> {        
        if(!item || !item.forcesInfo || !item.forcesInfo.activeTeams || item.forcesInfo.activeTeams.length == 0)
        { 
            return new Promise((resolve, reject) => {  
                resolve(new StorageResponse<boolean>().CreateFailResponse());
            });
        }   

        return new Promise((resolve, reject) => {  
            this.homeService.getCar().then((response) => {      
                if(response.isSuccessWithResult())
                {  
                    for(let index = 0; index < item.forcesInfo.activeTeams.length; index ++)
                    {
                        if(item.forcesInfo.activeTeams[index].carInfo.carId == response.data.carId)
                        {  
                            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
                            return;
                        }
                    }
             
                    resolve(new StorageResponse<boolean>().CreateFailResponse());
                }
                else{                 
                    resolve(new StorageResponse<boolean>().CreateFailResponse());
                } 
            });
        });                           
    }

    public isCurrentEventById(itemId: number): boolean {
        return this.storageItem != null && this.storageItem.generalInfo.eventId == itemId;
    }    
    
    public getCurrentCarTeam(): Promise<StorageResponse<CarTeam>>{        
        if(!this.storageItem)
        {      
            return new Promise((resolve, reject) => {  
                resolve(new StorageResponse<CarTeam>().CreateResultResponse(
                    null
                ));
            });
        }

        return new Promise((resolve, reject) => { 
            this.homeService.getCar().then((response) => {                      
                if(response.isSuccessWithResult()){  
                    for(let index = 0; index < this.storageItem.forcesInfo.activeTeams.length; index ++){
                        if(this.storageItem.forcesInfo.activeTeams[index].carInfo.carId == response.data.carId){
                             
                            resolve(new StorageResponse<CarTeam>().CreateResultResponse(
                                this.storageItem.forcesInfo.activeTeams[index]
                            ));

                            break;
                        }
                    }

                    resolve(new StorageResponse<CarTeam>().CreateResultResponse(
                        null
                    ));
                }
            }); 
        });                
    }

    public removeItem(itemId: number = -1): Promise<StorageResponse<boolean>>{          
        if(!this.storageItem){
            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<boolean>().CreateErrorResponse(
                    "the given log is null or empty."
                ));
            });
        }

        if(itemId == this.storageItem.generalInfo.eventId || itemId == -1){
            this.storageItem = null;
            this.store.removeItem(this.localStorageKey);
            this.$currentEventRemovedEmitter.emit(itemId); 
            this.homeService.removeAtEvent();
        }

        return new Promise((resolve, reject) => {            
            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });
    }
    
    public cancelMission(request: CancelMissionRequest): Promise<StorageResponse<boolean>>{              
        var pThis = this;   
        return new Promise((resolve, reject) => {                           
            let url = this.httpService.baseWebApi + 'Events/CancelMission/';
            this.httpService.post<StorageResponse<any>>(url, request, FireTruckOfflineActionTypes.MissionStatusUpdate).subscribe(
                (response) => {   
                    if(response.resultStatus == ApplicationResponseStatus.Fail){
                        this.alertifyService.error(this.content.updateDataErrorMessage);
                        resolve(new StorageResponse<boolean>().CreateFailResponse());
                    }
                    else{
                        this.alertifyService.success(this.content.successMessage);
                        resolve(new StorageResponse<boolean>().CreateResultResponse(
                            true
                        ));    
                    }
                },
                (err) => {
                    this.alertifyService.error(this.content.updateDataErrorMessage);
                    resolve(new StorageResponse<EventLog>().CreateFailResponse());
                },                  
            );
        });
    }
    
    public changeMissionStatus(request: ChangeMissionStatusRequest): Promise<StorageResponse<boolean>> {                                      
        var pThis = this;  
         
        return new Promise((resolve, reject) => {                           
            let url = this.httpService.baseWebApi + 'Events/UpdateMissionStatus/';
            this.httpService.post<StorageResponse<any>>(url, request, FireTruckOfflineActionTypes.MissionStatusUpdate).subscribe(
                (response) => {   
                    if(response.resultStatus == ApplicationResponseStatus.Fail){
                        this.alertifyService.error(this.content.updateDataErrorMessage);
                        resolve(new StorageResponse<boolean>().CreateFailResponse());
                    }   
                    else{
                        this.alertifyService.success(this.content.successMessage);
                        resolve(new StorageResponse<boolean>().CreateResultResponse(
                            true
                        ));    
                    }
                },
                (err) => {
                    this.alertifyService.error(this.content.updateDataErrorMessage);
                    resolve(new StorageResponse<boolean>().CreateFailResponse()); 
                },                  
            );
        });
    }

    public launchMission(request: LaunchMissionStatusRequest): Promise<StorageResponse<boolean>> {                                       
        var pThis = this;   
        return new Promise((resolve, reject) => {                           
            let url = this.httpService.baseWebApi + 'Events/LaunchToEvent/';
            this.httpService.post<StorageResponse<any>>(url, request, FireTruckOfflineActionTypes.LaunchToEvent).subscribe(
                (response) => {  
                    if(response.resultStatus == ApplicationResponseStatus.Fail){
                        this.alertifyService.error(this.content.updateDataErrorMessage);
                        resolve(new StorageResponse<boolean>().CreateFailResponse());
                    }   
                    else{
                        this.alertifyService.success(this.content.successMessage);
                        resolve(new StorageResponse<boolean>().CreateResultResponse(
                            true
                        ));
                    }
                },
                (err) => {
                    this.alertifyService.error(this.content.updateDataErrorMessage);
                    resolve(new StorageResponse<boolean>().CreateFailResponse());  
                },                  
            );
        });
    }    

    public addNewMission(request: NewMissionStatusRequest): Promise<StorageResponse<boolean>> {                                        
        var pThis = this;   
        return new Promise((resolve, reject) => {                           
            let url = this.httpService.baseWebApi + 'Events/AddNewVirtualMission/';
            this.httpService.post<StorageResponse<any>>(url, request, FireTruckOfflineActionTypes.MissionStatusUpdate).subscribe(
                (response) => {    
                    if(response.resultStatus == ApplicationResponseStatus.Fail){
                        this.alertifyService.error(this.content.updateDataErrorMessage);
                        resolve(new StorageResponse<boolean>().CreateFailResponse());
                    }   
                    else{
                        this.alertifyService.success(this.content.successMessage);
                        resolve(new StorageResponse<boolean>().CreateResultResponse(
                            true
                        ));    
                    }
                },
                (err) => {
                    this.alertifyService.error(this.content.updateDataErrorMessage);
                    resolve(new StorageResponse<boolean>().CreateFailResponse());
                },                  
            );
        });
    }  

    public updateAcitveMission(carTeam: CarTeam): Promise<StorageResponse<boolean>> {                                   
        return new Promise((resolve, reject) => { 
            this.homeService.getCar().then((homeResponse) => {
                if(carTeam.activeMission.eventId != this.storageItem.generalInfo.eventId){
                    resolve(new StorageResponse<boolean>().CreateFailResponse());
                    return;                
                }

                for(let index = 0; index < this.storageItem.forcesInfo.activeTeams.length; index ++){
                    if(this.storageItem.forcesInfo.activeTeams[index].carInfo.carId == carTeam.carInfo.carId){    
                        this.storageItem.forcesInfo.activeTeams[index] = carTeam;                                        
                        this.store.setItem(this.localStorageKey, JSON.stringify(this.storageItem));
                        this.homeService.setActiveAtEvent(this.storageItem);
                        
                        if(homeResponse.data.carId == carTeam.carInfo.carId){
                            this.$currentActiveMissionUpdatedEmitter.emit(this.storageItem.forcesInfo.activeTeams[index].activeMission);                 
                        }
                        else{
                            this.$currentStatusUpdatedEmitter.emit(this.storageItem); 
                        }
                            
                        resolve(new StorageResponse<boolean>().CreateResultResponse(
                            true
                        ));
    
                        break;
                    }                
                }

                resolve(new StorageResponse<boolean>().CreateFailResponse());
            });
        });                
    }   
    
    public updateControlTime(request: ControlTimeRequest): Promise<StorageResponse<boolean>> {                                     
        var pThis = this;   
        return new Promise((resolve, reject) => {                           
            let url = this.httpService.baseWebApi + 'Events/UpdateEventControlStatus/';
            this.httpService.post<StorageResponse<any>>(url, request, FireTruckOfflineActionTypes.UpdateEventControlTime).subscribe(
                (response) => {    
                    if(response.resultStatus == ApplicationResponseStatus.Fail){
                        this.alertifyService.error(this.content.updateDataErrorMessage);
                        resolve(new StorageResponse<boolean>().CreateFailResponse());
                    }   
                    else{
                        this.alertifyService.success(this.content.successMessage);
                        resolve(new StorageResponse<boolean>().CreateResultResponse(
                            true
                        ));    
                    }
                },
                (err) => {
                    this.alertifyService.error(this.content.updateDataErrorMessage);
                    resolve(new StorageResponse<boolean>().CreateFailResponse()); 
                },                  
            );
        });
    }  

    public getCurrentStatus(): CurrentStatusModel{        
        return new CurrentStatusModel(this.storageItem);
    }

    public updateNewMissionStatus(request: UpdateNewMissionStatusRequest): Promise<StorageResponse<boolean>> {                                         
        var pThis = this;   
        return new Promise((resolve, reject) => {                           
            let url = this.httpService.baseWebApi + 'Events/UpdateVirtualMissionStatus/';
            this.httpService.post<StorageResponse<any>>(url, request, FireTruckOfflineActionTypes.MissionStatusUpdate).subscribe(
                (response) => {       
                    if(response.resultStatus == ApplicationResponseStatus.Fail){
                        this.alertifyService.error(this.content.updateDataErrorMessage);
                        resolve(new StorageResponse<boolean>().CreateFailResponse());
                    }   
                    else{
                        this.alertifyService.success(this.content.successMessage);
                        resolve(new StorageResponse<boolean>().CreateResultResponse(
                            true
                        ));    
                    }
                },
                (err) => {
                    this.alertifyService.error(this.content.updateDataErrorMessage);
                    resolve(new StorageResponse<boolean>().CreateFailResponse());
                },                  
            );
        });
    }  
}


