import { Injectable } from '@angular/core';
import { Headers, Response } from '@angular/http';
import { HttpService } from './http.service'
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map'
 
@Injectable()
export class AuthenticationService {
    public token: string;
    /** tokenStorageKeyName - The token key name that saved in the storage */
    private tokenStorageKeyName: string;
 
    constructor(private httpService: HttpService) {
        this.tokenStorageKeyName = 'nstCurrentUser';

        // set token if saved in local storage
        let currentUser = JSON.parse(localStorage.getItem(this.tokenStorageKeyName));
        this.token = currentUser && currentUser.token;
    }
 
    login(username: string, password: string): Observable<boolean> {
        let o: Observable<boolean> = new Observable<boolean>();
        return o.map(() => true);
        /*return this.httpService.post('/api/authenticate', JSON.stringify({ username: username, password: password }))
            .map((response: Response) => {
                // login successful if there's a jwt token in the response
                let token = response.json() && response.json().token;
                if (token) {
                    // set token property
                    this.token = token;
 
                    // store username and jwt token in local storage to keep user logged in between page refreshes
                    localStorage.setItem(this.tokenStorageKeyName, JSON.stringify({ username: username, token: token }));
 
                    // return true to indicate successful login
                    return true;
                } else {
                    // return false to indicate failed login
                    return false;
                }
            });*/
    }
 
    logout(): void {
        // clear token remove user from local storage to log user out
        this.token = null;
        localStorage.removeItem(this.tokenStorageKeyName);
    }
}


