import { EventDetailsStatus } from "../event/event-details-status";

export class UpdateNewMissionStatusRequest {
    public statusDateTime: Date;
    public statusTime: string;
    public carId: number;
    public missionStatusId: EventDetailsStatus;
    
    constructor(carId: number, missionStatusId: EventDetailsStatus){
        this.statusDateTime = new Date();
        this.carId = carId;
        this.missionStatusId = missionStatusId;
    }      
}


