import { Injectable } from '@angular/core';
import { HttpService } from '../../core/http.service';
import { ConnectionService } from '../connection/connection.service';
import { OfflineStorage } from '../../stores/offlineStorage/OfflineStorage';
import { StorageResponse } from '../base/StorageResponse';
import { SaveSuppliesRequest } from '../../shared-classes/request/save.supplies.request';
import { SaveAutsRequest } from '../../shared-classes/request/save.auts.request';
import { SuppliesRequest } from '../../shared-classes/request/supplies.request';
import { SuppliesModel } from '../../shared-classes/supplies/supplies-model';
import { ItemStorage } from '../base/ItemStorage';
import { ContentService } from '../general/content.service';
import { AlertifyService } from '../general/alertify.service';
import { FireTruckOfflineActionTypes } from '../../shared-classes/offline/fire-truck-offline-action-types';
import { ApplicationResponseStatus } from '../../shared-classes/response/ApplicationResponseStatus';

@Injectable()
export class SuppliesService extends ItemStorage<SuppliesModel> {    
    constructor(
        private httpService: HttpService,
        private connectionService: ConnectionService,
        private store: OfflineStorage,
        contentService: ContentService,
        private alertifyService: AlertifyService
    ) {
        super(contentService);
    }

    getSupplies(request: SuppliesRequest): Promise<StorageResponse<SuppliesModel>>{   
        return new Promise((resolve, reject) => {                           
            let url = this.httpService.baseWebApi + 'Events/GetCarEquipmentUseInMission/';          
            this.httpService.post<StorageResponse<SuppliesModel>>(url, request).subscribe(
                (response) => {  
                    if(response.resultStatus == ApplicationResponseStatus.Fail){
                        this.errorMsgPromise.then(() => {                     
                            this.alertifyService.error(this.content.getDataErrorMessage);
                            resolve(new StorageResponse<SuppliesModel>().CreateFailResponse());    
                        });                        
                    } 
                    else{
                        resolve(new StorageResponse<SuppliesModel>().CreateResultResponse(
                            response.data
                        ));        
                    }
                },
                (err) => {
                    this.errorMsgPromise.then(() => {                     
                        this.alertifyService.warring(this.content.getConnectionErrorMessage);
                        resolve(new StorageResponse<SuppliesModel>().CreateFailResponse());    
                    });   
                },                  
            );
        });
    }

    saveSupplies(request: SaveSuppliesRequest): Promise<StorageResponse<boolean>>{
        return new Promise((resolve, reject) => {            
            let url = this.httpService.baseWebApi + 'Events/UpdateCarEquipmentUseInMission/';
            this.httpService.post<StorageResponse<any>>(url, request).subscribe(
                (response) => {  
                    if(response.resultStatus == ApplicationResponseStatus.Fail){
                        this.alertifyService.error(this.content.saveDataErrorMessage);
                        resolve(new StorageResponse<boolean>().CreateFailResponse());
                    } 
                    else{
                        resolve(new StorageResponse<boolean>().CreateResultResponse(
                            true
                        ));    
                    }
                },
                (err) => {
                    this.alertifyService.error(this.content.saveDataErrorMessage);
                    resolve(new StorageResponse<boolean>().CreateFailResponse());
                },                  
            );       

            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });  
    }

    saveAuts(request: SaveSuppliesRequest): Promise<StorageResponse<boolean>>{
        return new Promise((resolve, reject) => {            
            let url = this.httpService.baseWebApi + 'Events/UpdateCarKmInMission/';
            this.httpService.post<StorageResponse<any>>(url, request, FireTruckOfflineActionTypes.UpdateCarKM).subscribe(
                (response) => {   
                    if(response.resultStatus == ApplicationResponseStatus.Fail){
                        this.alertifyService.error(this.content.saveDataErrorMessage);
                        resolve(new StorageResponse<boolean>().CreateFailResponse());
                    } 
                    else{
                        this.alertifyService.success(this.content.successMessage);
                        resolve(new StorageResponse<boolean>().CreateResultResponse(
                            true
                        ));
                    }
                },
                (err) => {
                    this.alertifyService.error(this.content.saveDataErrorMessage);
                    resolve(new StorageResponse<boolean>().CreateFailResponse());
                },                  
            );       

            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });  
    }
}


