import { Component, OnInit } from '@angular/core';
import { EventInfo } from '../../shared-classes/event/event-info';
import { EventGeneralInfo } from '../../shared-classes/event/event-general-info';
import { CurrentEventDetailsService } from '../../services/event/current-event-details.service';
import { menuTypes } from '../../shared-classes/common/menu-types';
import { detailsbarTypes } from '../../shared-classes/common/detailsbar-types';
import { NavigationService } from '../../services/navigation/navigation.service';
import { ContentService } from '../../services/general/content.service';
import { EmapService } from '../../core/emap.service';
import { CustomMap } from '../../shared-classes/esri/custom-map';
import { ProjectionService } from '../../services/general/projection.service';
import { Response } from '@angular/http/src/static_response';
import { Extent } from '../../shared-classes/esri/Extent';
import { StorageResponse } from '../../services/base/StorageResponse';
import { CurrentStatusModel } from '../../shared-classes/event/current-status-model';
import { EventTypes } from '../../shared-classes/event/event-types';
import { setTimeout } from 'timers';
import { EventDetailsService } from '../../services/event/event-details.service';
import { MapStateService } from '../../services/mapState/map-state.service';
import { PreventFireService } from '../../services/prevent-fire/prevent-fire.service';
import { PreventFireRequest } from '../../shared-classes/request/prevent-fire-request';
import { EventPreventionModel } from '../../shared-classes/event/event-prevention-model';
import { BuildingInfo } from '../../shared-classes/event/building-info';
import { PreventionDetailsRequest } from '../../shared-classes/request/prevention-details-request';
import { PreventionCaseEventData } from '../../shared-classes/event/prevention-case-event-data';
import { BaseComponent } from '../../shared-classes/common/base-component';

@Component({
 selector:'prevent-fire-model',
  templateUrl: './prevent-fire.component.html'
})

export class PreventFireComponent extends BaseComponent{
  private menuType: menuTypes = menuTypes.nomenu;
  private detailsbarType: detailsbarTypes = detailsbarTypes.minimizeEvent;
  private navigationData: any;
  private eventGeneralInfo: EventGeneralInfo;
  private preventFireModel: EventPreventionModel;
  private selectedItem: BuildingInfo;
  private preventionDetailsList: Array<PreventionCaseEventData>;
  
  constructor(
    private navigationService: NavigationService,
    private currentEventDetailsService: CurrentEventDetailsService,
    private eventDetailsService: EventDetailsService,
    private preventFireService: PreventFireService,
    contentService: ContentService,
    mapStateService: MapStateService
  ) {  
    super(contentService, 'prevent-fire');
    mapStateService.changeMapState(false);
    this.preventionDetailsList = new Array<PreventionCaseEventData>();

    let data = navigationService.getNavigationData();  
    let request = new PreventFireRequest(data.eventId);
    this.preventFireService.getPreventionInfo(request).then((response) => {
      
      if(response.isSuccessWithResult()){
        this.preventFireModel = response.data;

        if(this.preventFireModel.aroundEventBuildings && this.preventFireModel.aroundEventBuildings.length > 0){
          this.selectedItem = this.preventFireModel.aroundEventBuildings[0];
          this.preventionDetailsList = this.preventFireModel.selectedBuildingCaseList;
        }
      }
    }); 
  }

  private itemClicked(item){
    this.selectedItem = item;
    this.getDetailsList();
  }

  private getDetailsList(){
    if(this.selectedItem == null){
      return;
    }

    this.preventFireService.getPreventionDetails(new PreventionDetailsRequest(this.selectedItem.buildingId)).then((response) => {
      if(response.isSuccessWithResult()){
        this.preventionDetailsList = response.data;
      }
    });
  }
}


