import { Component, OnInit } from '@angular/core';
import { menuTypes } from '../shared-classes/common/menu-types';
import { QuickReportService } from '../services/quick-report/quick-report.service';
import { Dictionary } from '../shared-classes/IDictionary';
import { QuickReportTypes } from '../shared-classes/quick-report/quick-report-types';
import { ContentService } from '../services/general/content.service';
import { NavigationService } from '../services/navigation/navigation.service';
import { BaseComponent } from '../shared-classes/common/base-component';

@Component({
 selector:'quick-report',
  templateUrl: './quick-report.component.html'
})

export class QuickReportComponent extends BaseComponent{
  newReport: string = '';
  menuType: menuTypes = menuTypes.quickReport;
  isReasonValid: boolean = true;

  constructor(
    private quickReportService: QuickReportService,
    private navigationService: NavigationService,
    contentService: ContentService
  ){ 
    super(contentService, 'supplies');
  }

  customReport(){
    this.isReasonValid = true;

    if(!this.newReport || this.newReport.trim() == ''){
      this.isReasonValid = false;
      return;
    } 

    let data = this.navigationService.getNavigationData();
    this.quickReportService.sendQuickReport(data.eventId, QuickReportTypes.Custom, this.newReport).then((response) => {
      if(response.isSuccess()){
        this.navigationService.goBack();
      }      
    });
  }

  ngOnInit() { 
  
  }
}



