export class ChatMessage{
    private messageTime: string;
    private messageContent: string;
    private messageOwner: string;
    private isMyMessage: boolean = false;
    private ownerColor: string = '';

    constructor(messageTime: string, messageContent: string, messageOwner: string, isMyMessage: boolean){
        this.messageTime = messageTime;
        this.messageContent = messageContent;
        this.messageOwner = messageOwner;
        this.isMyMessage = isMyMessage;
        this.ownerColor = isMyMessage? '#4eccff' : this.getRandomColor();
    }

    private getRandomColor(){
        return "rgb("+ Math.floor(Math.random() * 255) + ","+ Math.floor(Math.random() * 255) + "," + Math.floor(Math.random() * 255) + ")";
    }
}


