import { LogTypes } from "./log-types";
import * as moment from 'moment/moment';

export class LogInfo{
    private timeStamp: string;
    private message: string;
    private source: string;
    private logDesc: string;
    private logType: LogTypes;
    private logTypeDesc: string;

    constructor(message: string, source: string, logDesc: string, logType: LogTypes = LogTypes.error){
        this.timeStamp = moment().format("YYYY/MM/DD HH:mm:ss");
        this.message = message;
        this.source = source;
        this.logDesc = logDesc;
        this.logType = logType;
        this.logTypeDesc = this.getTypeDesc();
    }

    private getTypeDesc(): string{
        switch (this.logType){
            case LogTypes.error:
                return 'Error';
            case LogTypes.debugger:
                return 'Debugger';
            case LogTypes.info:
                return 'Info';
        }
    }
}


