import { Injectable } from '@angular/core';
import { ConnectionService } from '../connection/connection.service';
import { AppConfig } from '../../app.config';
import { DeviceInformation } from '../../shared-classes/common/deviceInformation';
import { ConnectionStateTypes } from '../../shared-classes/common/connection-state-types';
import { AlertifyService } from './alertify.service';

declare var launchnavigator;
@Injectable()
export class WazeService {      
    constructor(private connectionService:ConnectionService, private config:AppConfig, private alertifyService: AlertifyService) {        
    }    

    navigate(lat:number, lon:number): void {           
        if (typeof launchnavigator === 'undefined' || launchnavigator === null) {
            this.alertifyService.error("     "); 
            return;
        }

        launchnavigator.isAppAvailable(launchnavigator.APP.WAZE, function(isAvailable){            
        var app;
        if(isAvailable){
            app = launchnavigator.APP.WAZE;
        }else{                
            app = launchnavigator.APP.USER_SELECT;
        }

        launchnavigator.navigate([lat, lon], {
            app: app
        });
        });	
    }
}


