import esriPoint = require("esri/geometry/Point");   

export class Point extends esriPoint
{
    constructor(point: Object);
    constructor(x: number, y: number);

    constructor(p1?: any, p2?: any)
    {
        var dstPoint = {
            "spatialReference": { "wkid": 2039 }
        };

        if (typeof (p1) === "object")
            dstPoint = Object.assign(dstPoint, p1);
        else
        {
            if (typeof p1 == 'string')
                p1 = parseFloat(p1);

            if (typeof p2 == 'string')
                p2 = parseFloat(p2);

            dstPoint = Object.assign(dstPoint, { "x": p1, "y": p2 });
        }

        super(dstPoint);
    }
}


