import { FireTruckOfflineActionTypes } from "./fire-truck-offline-action-types";
import * as moment from 'moment/moment';
import { ChangeMissionStatusRequest } from "../request/change-mission-status-request";

export class FireTruckOfflineAction{
    public offlineActionType: FireTruckOfflineActionTypes;
    public actionRealTime: string;
    public actionData: any;

    constructor(offlineActionType: FireTruckOfflineActionTypes, actionData: any){
        this.offlineActionType = offlineActionType;        
        this.actionRealTime = moment().format("YYYY/MM/DD HH:mm:ss");
        this.actionData = JSON.stringify(actionData);
    }
}


