import { IDictionaryStorage } from './IDictionaryStorage';
import { StorageResponse } from './StorageResponse';
import { Dictionary } from '../../shared-classes/IDictionary';
import { ApplicationResponseStatus } from '../../shared-classes/response/ApplicationResponseStatus';

export class DictionaryStorage<T> implements IDictionaryStorage<T>{
    dictionary: Array<Dictionary<T>>;

    constructor(){                        
        this.dictionary = new Array<Dictionary<T>>();
    }

    addItem(key: string, item: T): Promise<StorageResponse<boolean>> {        
        return new Promise((resolve, reject) => {   
            if(item == undefined || key == undefined){                     
                resolve(new StorageResponse<boolean>().CreateErrorResponse(
                    "the given item or key is null."
                ));
                return;
            }

            this.isItemExists(key).then((response) => {
                if(response.isSuccess()){
                    this.getItem(key).then((response) => {
                        if(response.isSuccessWithResult)
                        {
                            response.data = item;
                            resolve(new StorageResponse<boolean>().CreateResultResponse(true));
                        } 
                        else{                                 
                            resolve(new StorageResponse<boolean>().CreateErrorResponse(
                                "no item to update with given key."
                            ));
                        }           
                    });

                    return;
                } 

                this.dictionary.push(new Dictionary(key, item));       
                resolve(new StorageResponse<boolean>().CreateSuccessResponse());
            });
        });
    }

    removeItem(key: string): Promise<StorageResponse<boolean>> {
        return new Promise((resolve, reject) => {   
            if(key == undefined){                     
                resolve(new StorageResponse<boolean>().CreateErrorResponse(
                    "the given key is null."
                ));
                return;
            }

            this.isItemExists(key).then((response) => {
                if(response.resultStatus == ApplicationResponseStatus.Fail){            
                    resolve(new StorageResponse<boolean>().CreateErrorResponse(
                        "the item with the given key does not exists in list."
                    ));
                    return;
                } 

                this.getItemIndex(key).then((response) => {
                    if(!response.isSuccessWithResult()){           
                        resolve(new StorageResponse<boolean>().CreateErrorResponse(
                            response.errorMessage
                        ));
                        return;
                    }

                    this.dictionary.splice(response.data, 1);           
                    resolve(new StorageResponse<boolean>().CreateSuccessResponse());
                });
            });            
        });
    }

    getItem(key: string): Promise<StorageResponse<T>> {
        return new Promise((resolve, reject) => {
            let keyTrim = key.trim();            
            if(keyTrim == ""){                        
                resolve(new StorageResponse<number>().CreateErrorResponse(
                    "the given key is null."
                )); 
                return;           
            }

            this.getItemIndex(keyTrim).then((response)=>{
                if(!response.isSuccessWithResult())
                {                                 
                    resolve(new StorageResponse<T>().CreateErrorResponse(
                        response.errorMessage
                    ));
                    return
                }
                
                resolve(new StorageResponse<T>().CreateResultResponse(
                    this.dictionary[response.data].value
                )); 
            });
        });        
    }

    getItemIndex(key: string): Promise<StorageResponse<number>> {
        if(key == undefined){
            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<number>().CreateErrorResponse(
                    "the given key is null."
                ));
            });
        }

        if(this.dictionary == undefined || this.dictionary.length == 0){
            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<number>().CreateErrorResponse(
                    "the item list is empty."
                ));
            });
        }

        for(let index = 0; index < this.dictionary.length; index++){
            var obj: Dictionary<T> = this.dictionary[index];

            if(obj.key == key)
            {
                return new Promise((resolve, reject) => {            
                    resolve(new StorageResponse<number>().CreateResultResponse(
                        index
                    ));
                });
            }
        }

        return new Promise((resolve, reject) => {            
            resolve(new StorageResponse<number>().CreateErrorResponse(
                "the item with given key does not exists in list."
            ));
        });
    }

    isItemExists(key: string): Promise<StorageResponse<boolean>> {
        return new Promise((resolve, reject) => {
            if(this.dictionary == undefined || this.dictionary.length == 0 || key == undefined){                       
                resolve(new StorageResponse<boolean>().CreateFailResponse());
                return;
            }

            for (var i = 0; i < this.dictionary.length; i++) {        
                if (this.dictionary[i].key === key) {                          
                    resolve(new StorageResponse<boolean>().CreateSuccessResponse());
                    return;
                }
            }
            
            resolve(new StorageResponse<boolean>().CreateFailResponse());
        });
    }

    getItems(): Promise<StorageResponse<Array<Dictionary<T>>>> {
        return new Promise((resolve, reject) => {            
            resolve(new StorageResponse<Array<Dictionary<T>>>().CreateResultResponse(
                this.dictionary
            ));
        });
    }
}


