import { Injectable } from '@angular/core';
import { LogInfo } from '../../shared-classes/common/log-info';
import { OfflineStorage } from '../../stores/offlineStorage/OfflineStorage';
import { StorageResponse } from '../base/StorageResponse';
import {Observable} from "rxjs";
import { AppConfig } from '../../app.config';
import * as moment from 'moment/moment';

@Injectable()
export class LogService{  
    private localStorageKey: string = "LogList";

    constructor(private store: OfflineStorage, config: AppConfig) {
        var numbers = Observable.timer(1000, config.getConfig('clear_logs_time_period'));
        numbers.subscribe(x => {
            let time = moment().format("HH:mm");            
            if(time == config.getConfig('clear_logs_time')){                
                this.clearList();
                this.addItem(new LogInfo('the log has been deleted at : ' + moment().format("HH:mm:ss"), 'LogService', 'Log Action'));
            }
        });
    }

    public addItem(item: LogInfo): void {
        if(item == undefined){
            return;
        }

        let storageList = <Array<LogInfo>>JSON.parse(this.store.getItem(this.localStorageKey))
        if(!storageList){
            storageList = Array<LogInfo>();
        }

        storageList.unshift(item);
        this.store.setItem(this.localStorageKey, JSON.stringify(storageList)); 
    }
    
    public clearList(): void {
        this.store.removeItem(this.localStorageKey); 
    }  
    
    public getList(): Promise<StorageResponse<Array<LogInfo>>> {                   
        return new Promise((resolve, reject) => {  
            let storageList = <Array<LogInfo>>JSON.parse(this.store.getItem(this.localStorageKey));
            resolve(new StorageResponse<LogInfo>().CreateResultResponse(
                storageList
            ));
        });
    }
}


      


