import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { BuildingInfo } from '../../../shared-classes/event/building-info';

@Component({
 selector:'prevent-fire-item',
  templateUrl: './prevent-fire-item.component.html',
  inputs:['data', 'content']
})

export class PreventFireItemComponent{
  @Output() onItemClicked: EventEmitter<BuildingInfo> = new EventEmitter();

  private data: BuildingInfo;
  private content: any;

  constructor() {}

  itemClicked(){
    this.onItemClicked.emit(this.data);
  }
}


