import { Injectable, EventEmitter } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Rx';
import { AuthenticationService } from './authentication.service'
import 'rxjs/add/operator/finally';
import { ConnectionService } from '../services/connection/connection.service';
import { Dictionary } from '../shared-classes/IDictionary';
import { AppConfig } from '../app.config';
import { QueueService } from '../services/queue/queue.service';
import { FireTruckOfflineActionTypes } from '../shared-classes/offline/fire-truck-offline-action-types';
import { FireTruckOfflineAction } from '../shared-classes/offline/fire-truck-offline-action';
import { StorageResponse } from '../services/base/StorageResponse';
import { AlertifyService } from '../services/general/alertify.service';
import { LogService } from '../services/general/log.service';
import { LogInfo } from '../shared-classes/common/log-info';
import { ApplicationResponseStatus } from '../shared-classes/response/ApplicationResponseStatus';
import { LogTypes } from '../shared-classes/common/log-types';

@Injectable()
export class HttpService {
    baseWebApiSchema: string;
    baseWebApiDomain: string;
    baseWebApiProject: string;
    baseWebApi: string;
    public $loadingStateEmitter: EventEmitter<boolean>;

    constructor(
        private http: HttpClient, 
        private connectionService: ConnectionService,
        private queueService: QueueService,
        private config: AppConfig,
        private alertifyService:AlertifyService,
        private logService: LogService) { 

        this.baseWebApiSchema = config.getConfig('base_web_api_schema');
        this.baseWebApiDomain = config.getConfig('base_web_api_domain');
        this.baseWebApiProject = config.getConfig('base_web_api_project');
        this.baseWebApi = this.baseWebApiSchema + "://" + this.baseWebApiDomain + "/" + this.baseWebApiProject;

        this.$loadingStateEmitter = new EventEmitter();          
    }

    public get<T>(url: string): Observable<T> {
        return this.http.get<T>(url, this.getRequestOptions());
    }

    public post<T>(url: string, params: any, actionType: FireTruckOfflineActionTypes = FireTruckOfflineActionTypes.NONE): Observable<T> {
        if(this.connectionService.isOffline() && actionType != FireTruckOfflineActionTypes.NONE){           
            this.queueService.addItem(new FireTruckOfflineAction(actionType, params)); 
                       
            return Observable.create(observer => {                
                return observer.next(new StorageResponse<T>().CreateSuccessResponse());
            });
        }
        else{
            return this.serverPost(url, params);
        }           
    }

    public getRequestOptions() {
        let httpHeaders: HttpHeaders = new HttpHeaders({ 'Content-Type': 'application/json' });
return { headers: httpHeaders };
    }

    public serverPost<T>(url:string, params: any): Observable<T>{
        this.logService.addItem(new LogInfo("go to: " + url, "http", 'server', LogTypes.debugger));

        if(!this.connectionService.isOffline()){
            this.$loadingStateEmitter.emit(true); 
        }        

        return this.http.post(url, params, this.getRequestOptions())
        .map((response) => { 
            this.logService.addItem(new LogInfo("return from: " + url, "http", 'server', LogTypes.debugger));
            if(response.hasOwnProperty("resultStatus") && response["resultStatus"] != ApplicationResponseStatus.Success){                   
                let message = "Exception occured in serverPost - response faild. Reason: " + response["errorMessage"];
                //this.logService.addItem(new LogInfo(message, url, 'server'));
            } 

            return response;     

        }).catch((error:any) => {
            let message = "Exception occured in serverPost - probably no connection";
            let source = "HttpService";

            if(error && error.message){
                message = error.message;

                if(error.error && error.error.target && error.error.target.__zone_symbol__xhrURL){
                   source = error.error.target.__zone_symbol__xhrURL;                   
                }
            }

            //this.logService.addItem(new LogInfo(message, source, 'connection'));
            return Observable.throw(error.json().error || 'error occured in serverPost')
        }).finally(()=>{
            this.logService.addItem(new LogInfo("finally from: " + url, "http", 'server', LogTypes.debugger));
            this.$loadingStateEmitter.emit(false); 
        });
    }
}



