import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'eventLastNumberPipe'
})
export class EventLastNumberPipe implements PipeTransform {
  transform(value: number, lastLettersCounter: number): string {    
      let sNumber = value.toString();
      if(sNumber.length < lastLettersCounter){
        return sNumber;
      }

      return sNumber.substring(sNumber.length - lastLettersCounter, sNumber.length);
  }
} 


