export class CarCheckData {
    public carName: string;
    public carId: number;
    public employeeId: number;
    public carKm: number;

    constructor(carName: string, carId: number = 0, employeeId: number = 0, carKm: number = 0){
        this.carName = carName;
        this.carId = carId;
        this.employeeId = employeeId;
        this.carKm = carKm;
    }
}


