import { EventLog } from "./event-log";
import { Area } from "../common/area";
import { EventInfo } from "./event-info";
import { employee } from "../employees/employee";
import { EventForceInfo } from "./event-force-info";
import { CarTeam } from "../car/car-team";
import { EventGeneralInfo } from "./event-general-info";


export class CurrentStatusModel {
    public atEvent: Array<CurentStatusForce>;
    public toEvent: Array<CurentStatusForce>;
    public log: EventLog;
    public areas: Array<CurrentStatusArea>;
    public generalInfo: EventGeneralInfo;
    public carsSummary: number;
    public employeesSummary: number = 0;

    constructor(eventInfo: EventInfo){
        this.atEvent = Array<CurentStatusForce>();
        this.toEvent = Array<CurentStatusForce>();
        this.areas = Array<CurrentStatusArea>();

        this.setCurrentStatusForces(eventInfo.forcesInfo);
        this.setCurrentStatusAreas(eventInfo.areas, eventInfo.forcesInfo);

        this.log = eventInfo.eventLog;
        this.generalInfo = eventInfo.generalInfo;        
    }

    private setCurrentStatusForces(forcesInfo: EventForceInfo){
        if(forcesInfo == null || forcesInfo.activeTeams == null || forcesInfo.activeTeams.length == 0){
            return ;
        }

        for(let index = 0; index < forcesInfo.activeTeams.length; index ++){
            let activeTeam = forcesInfo.activeTeams[index];

            if(activeTeam.isCarInTheWayToEvent){
                this.setToEvent(activeTeam);
            }
            else{
                this.setAtEvent(activeTeam);
            }

            this.employeesSummary = this.employeesSummary + activeTeam.employees.length;
        }

        for(let index = 0;index < this.atEvent.length; index ++){
            this.atEvent[index].carTeams = this.atEvent[index].carTeams.sort((a: CarTeam, b: CarTeam) => { return a.isExternalEventUnionForce < b.isExternalEventUnionForce ? -1 : 1 });
        }

        for(let index = 0;index < this.toEvent.length; index ++){
            this.toEvent[index].carTeams = this.toEvent[index].carTeams.sort((a: CarTeam, b: CarTeam) => { return a.isExternalEventUnionForce < b.isExternalEventUnionForce ? -1 : 1 });
        }

        this.atEvent = this.atEvent.sort((a: CurentStatusForce, b: CurentStatusForce) => { return a.typeName < b.typeName ? -1 : 1 });
        this.toEvent = this.toEvent.sort((a: CurentStatusForce, b: CurentStatusForce) => { return a.typeName < b.typeName ? -1 : 1 });

        this.carsSummary = forcesInfo.activeTeams.length;
    }

    private setCurrentStatusAreas(areas: Array<Area>, forcesInfo: EventForceInfo){
        if(forcesInfo == null || forcesInfo.activeTeams == null || forcesInfo.activeTeams.length == 0 || areas == null ||  areas.length == 0){
            return;
        }

        for(let i = 0; i < areas.length; i ++){
            let area = areas[i];

            let newArea = new CurrentStatusArea(area.areaName, area.areaCommanderName, area.areaRadioFrq);

            for(let index = 0; index < forcesInfo.activeTeams.length; index ++){
                let team = forcesInfo.activeTeams[index];

                if(team.eventAreaId == area.eventAreaId){
                    newArea.carTeams.push(team);
                }
            }

            var found = this.areas.find(function(element) {
                return element.areaName == area.areaName;
            });

            if(!found){
                this.areas.push(newArea);
            }
        }
    }

    private setToEvent(team: CarTeam){
        for(let index = 0; index < this.toEvent.length; index ++){
            if(this.toEvent[index].typeName == team.carInfo.carTypeDesc){
                this.toEvent[index].typeCount++;
                this.toEvent[index].employeesCounter = this.toEvent[index].employeesCounter + team.employees.length;
                this.toEvent[index].carTeams.push(team);
                return;
            }
        }

        let currentStatus = new CurentStatusForce(team.carInfo.carTypeDesc, 1, team.employees.length);
        currentStatus.carTeams.push(team);
        this.toEvent.push(currentStatus);
    }

    private setAtEvent(team: CarTeam){
        for(let index = 0; index < this.atEvent.length; index ++){
            if(this.atEvent[index].typeName == team.carInfo.carTypeDesc){
                this.atEvent[index].typeCount++;
                this.atEvent[index].employeesCounter = this.atEvent[index].employeesCounter + team.employees.length;
                this.atEvent[index].carTeams.push(team);
                return;
            }
        }

        let currentStatus = new CurentStatusForce(team.carInfo.carTypeDesc, 1, team.employees.length);
        currentStatus.carTeams.push(team);
        this.atEvent.push(currentStatus);
    }
}

export class CurentStatusForce{
    public typeName: string;
    public typeCount: number;
    public employeesCounter: number;
    public isOpen: boolean = false;
    public carTeams: Array<CarTeam>;

    constructor(typeName: string, typeCount: number, employeesCounter: number){
        this.typeCount = typeCount;
        this.typeName = typeName;
        this.employeesCounter = employeesCounter;
        this.carTeams = Array<CarTeam>();
    }
}

export class CurrentStatusArea{
    public areaName: string;
    public areaCommanderName: string;
    public areaFreq: string;
    public isOpen: boolean = false;
    public carTeams: Array<CarTeam>;

    constructor(areaName: string, areaCommanderName: string, areaFreq: string){
        this.areaName = areaName;
        this.areaCommanderName = areaCommanderName;
        this.areaFreq = areaFreq;
        this.carTeams = Array<CarTeam>();
    }
}


