import { Component, OnInit, ViewChild, ElementRef, Renderer2 } from '@angular/core';

@Component({
  selector:'event-dots',
  templateUrl: './event-dots.component.html',
  inputs: ['dotsCounter', 'selectedDotsCounter']
})

export class EventDotsComponent{
  dotsCounter : number;
  selectedDotsCounter : number;

  @ViewChild('dotWrapper') dotWrapper: ElementRef;

  constructor(private renderer:Renderer2) { 

  }

  ngOnInit() {
    for(let index = 0; index < this.dotsCounter; index ++)
    {
      let span = this.renderer.createElement('span');
      this.renderer.addClass(span, (index < this.selectedDotsCounter && this.selectedDotsCounter <= this.dotsCounter ) ? 'dot-selected' : 'dot');
      this.dotWrapper.nativeElement.appendChild(span);
    }
  }  
}



