import { Injectable } from '@angular/core';
import { AlertifyPushMessage } from '../../shared-classes/node/alertify/alertify-push-message';
import { ShalhevetAlertifyPushMessageTrigger } from '../../shared-classes/node/alertify/shalhevet-alertify-push-message-trigger';
import * as alertify from 'alertifyjs';
import { AlertifyTypes } from '../../shared-classes/alertify/alertify-types';
import { EventGeneralInfo } from '../../shared-classes/event/event-general-info';
import { EventLastNumberPipe } from   '../../shared-components/event-general-info/event-number-pipe/event-last-number.pipe';
import { EventFirstNumberPipe } from '../../shared-components/event-general-info/event-number-pipe/event-first-number.pipe';
import { employee } from '../../shared-classes/employees/employee';
import { AppConfig } from '../../app.config';

@Injectable()
export class AlertifyService {  
    private notifyStaticClass = 'alertify-notifier';
    private promtStaticClass = 'ajs-dialog';

    constructor(private config: AppConfig) {
        alertify.defaults = {
            // dialogs defaults
            autoReset:true,
            basic:false,
            closable:true,
            closableByDimmer:true,
            frameless:false,
            maintainFocus:true, // <== global default not per instance, applies to all dialogs
            maximizable:true,
            modal:true,
            movable:true,
            moveBounded:false,
            overflow:true,
            padding: true,
            pinnable:true,
            pinned:true,
            preventBodyShift:false, // <== global default not per instance, applies to all dialogs
            resizable:true,
            startMaximized:false,
            transition:'pulse',
    
            // notifier defaults
            notifier:{
                // auto-dismiss wait time (in seconds)  
                delay:5,
                // default position
                position:'bottom-right',
                // adds a close button to notifier messages
                closeButton: false
            },
    
            // language resources 
            glossary:{
                // dialogs default title
                title:'',
                // ok button text
                ok: '',
                // cancel button text
                cancel: ''            
            },
    
            // theme settings
            theme:{
                // class name attached to prompt dialog input textbox.
                input:'ajs-input',
                // class name attached to ok button
                ok:'ajs-ok',
                // class name attached to cancel button 
                cancel:'ajs-cancel'
            }
        };
    }    

    public promt(config: PromtConfig): any{   

        let pThis = this;

        switch(config.type){
            case AlertifyTypes.confirm:
                alertify.confirm(config.title, config.msg, config.okCallback, config.cancelCallback).set('labels', {ok: config.okText, cancel: config.cancelText});
                //alertify.confirm('Confirm Title', 'Confirm Message', function(){ alertify.success('Ok') }
                //, function(){ alertify.error('Cancel')});
                break;
            case AlertifyTypes.promt:
                alertify.prompt(config.title, config.msg, config.watermark, config.okCallback, config.cancelCallback).set('labels', {ok: config.okText, cancel: config.cancelText});
                //alertify.prompt( 'Prompt Title', 'Prompt Message', 'Prompt Value'
                //, function(evt, value) { alertify.success('You entered: ' + value) }
                //, function() { alertify.error('Cancel') });
                break;
         } 
         
         if(config.customClass){
            this.setAdditionalClass(this.promtStaticClass, config.customClass);
        } 

         return null;
    }

    public notify(msg: string, type: AlertifyTypes, customClass: string = null, dismisstedCallback: Function = null, delay: number = 10): any{          
        // a delay value of 0 will keep this open
        let pThis = this;

        switch(type){
            case AlertifyTypes.success:
                alertify.success(msg, delay, dismisstedCallback);  
            break;    
            case AlertifyTypes.warning:
                alertify.warning(msg, delay, dismisstedCallback);   
            break;
            case AlertifyTypes.error:
                alertify.error(msg, delay, dismisstedCallback);  
            break;
            case AlertifyTypes.message:
                alertify.message(msg, delay, dismisstedCallback);  
            break;
            case AlertifyTypes.notify:
                alertify.notify(msg, customClass, delay, dismisstedCallback);  
            break;
         } 
         
        if(customClass){
            this.setAdditionalClass(this.notifyStaticClass, customClass);
        }         

         return null;
    }
    
    public eventNotification(alertifyData: AlertifyPushMessage){
        this.notify(alertifyData.message, alertifyData.alertType, 'node-messages');
    }

    public createEmloyeesConfirm(employees: Array<employee>){
        let html = '';
        for(let index = 0; index < employees.length; index ++){
            html = html + '<span>' + employees[index].employeeName + '</span>,';
        }

        return html.slice(0, -1);
    }

    private setAdditionalClass(staticClass: string, className: string = null){
        let elLogs = document.querySelector("." + staticClass)
        if(elLogs && elLogs.className.indexOf(className) == -1){
            elLogs.className = elLogs.className  + " " + className;         
        }
    }

    private clearAdditionalClass(staticClass: string, className: string){
        let elLogs = document.querySelector("." + staticClass)
        
        if(elLogs && elLogs.className.indexOf(className) != -1 && elLogs.childElementCount == 1){
            elLogs.className = elLogs.className.replace(className, '');         
        }
    }

    public clearNotifyAdditionalClass(className: string){
        this.clearAdditionalClass(this.notifyStaticClass, className);
    }  
    
    public clearPromtAdditionalClass(className: string){
        this.clearAdditionalClass(this.promtStaticClass, className);
    } 

    public createEventDetailNotification(event: EventGeneralInfo){
        let lastFilter = new EventLastNumberPipe();
        let firstFilter = new EventFirstNumberPipe();

        let html = '';
        html = '<span class=\'event-type-label\' style="background-color: ' + event.parentEventTypeColor + '">' + event.parentEventTypeDesc + '</span>';
        html = html + '<span class=\'counter large\'>'+ lastFilter.transform(event.eventNumber, 3) + '</span>';
        html = html + '<span class=\'counter small\'>'+ firstFilter.transform(event.eventNumber, 3) + '</span>';
        html = html + '<div class=\'desc\'>' + event.eventDesc + " - " + event.eventTypeDesc + '</div> ';
        html = html + '<div class=\'location\'>' + event.eventLocationDesc + '</div> ';

        return html;
    }

    public error(msg){
        this.notify(msg, AlertifyTypes.error, 'node-messages');
    }

    public warring(msg){
        this.notify(msg, AlertifyTypes.warning, 'node-messages');
    }

    public success(msg){
        this.notify(msg, AlertifyTypes.success, 'node-messages');
    }

    public debugAlert(msg){
        if(this.config.getConfig('is_debug_mode')){
            alert(msg);
        }
    }
}

export class PromtConfig{
    title: string; 
    msg: string; 
    okCallback: Function; 
    cancelCallback: Function;
    customClass: string;
    type: AlertifyTypes = AlertifyTypes.confirm;    
    watermark: string = null;
    cancelText: string = "";
    okText: string = "";

    constructor(title: string, msg: string, okCallback: Function, cancelCallback: Function,  customClass: string = null){
        this.title = title;
        this.msg = msg;
        this.customClass = customClass;
        this.okCallback = okCallback;
        this.cancelCallback = cancelCallback;
    }
}


