import { Component, OnInit } from '@angular/core';
import { MapStateService } from '../services/mapState/map-state.service';
import { ContentService } from '../services/general/content.service';
import { MappingNavigationDataService } from '../services/navigation/mapping-navigation-data.service';
import { SaveSuppliesRequest } from '../shared-classes/request/save.supplies.request';
import { DeviceService } from '../services/general/device.service';
import { SuppliesService } from '../services/supplies/supplies.service';
import { QuickReportService } from '../services/quick-report/quick-report.service';
import { Dictionary } from '../shared-classes/IDictionary';
import { HomeService } from '../services/home/home.service';
import { NavigationService } from '../services/navigation/navigation.service';
import { QuickReportTypes } from '../shared-classes/quick-report/quick-report-types';
import { SuppliesRequest } from '../shared-classes/request/supplies.request';
import { CurrentEventDetailsService } from '../services/event/current-event-details.service';
import { SuppliesModel } from '../shared-classes/supplies/supplies-model';
import { DropDownData } from '../shared-classes/common/drop-down-data';
import { BaseComponent } from '../shared-classes/common/base-component';
import { EmployeesTeamData } from '../shared-classes/employees/employee-team-data';

@Component({
  templateUrl: './supplies.component.html'
})
export class SuppliesComponent extends BaseComponent {
  private isPumpValid: boolean = true;
  private isWaterValid: boolean = true;
  private isPouderValid: boolean = true;
  private isFoamValid: boolean = true;
  private isQualified: boolean = true;
  private isReasonValid: boolean = true;
  private suppliesSaveEmitter: any;
  private suppliesCloseEmitter: any;
  private reasonSelected: DropDownData;
  private reasonsList: Array<DropDownData>;
  private suppliesModel: SuppliesModel;
  
  constructor(
    mapStateService: MapStateService,
    contentService: ContentService,
    mappingNavigationDataService: MappingNavigationDataService,
    deviceService: DeviceService,
    suppliesService: SuppliesService,
    quickReportService: QuickReportService,
    homeService: HomeService,
    private navigationService: NavigationService,
    private currentEventDetailsService: CurrentEventDetailsService
  ){
    super(contentService, 'supplies');
    this.suppliesModel = new SuppliesModel();
    mapStateService.changeMapState(false);
    
    quickReportService.getNotQualifedReasons().then((response) => {
      if(response.isSuccessWithResult()){
        this.reasonsList = response.data;
      }
    });

    currentEventDetailsService.getCurrentCarTeam().then((response) => {
      if(response.isSuccessWithResult()){
        suppliesService.getSupplies(new SuppliesRequest(response.data.carInfo.carId, response.data.activeMission.missionId)).then((res) => {
          if(res.isSuccessWithResult()){
            this.suppliesModel = res.data;
          }
        });    
      }     
    });

    this.suppliesSaveEmitter = mappingNavigationDataService.$suppliesSaveEmitter.subscribe(result => {          
      let navigationData = navigationService.getNavigationData();

      if(this.isDataValid()){ 
        currentEventDetailsService.getCurrentCarTeam().then((response) => {
          let request = new SaveSuppliesRequest();
          request.carId = response.data.carInfo.carId;
          request.MissionEquipmentData = this.suppliesModel;                    
          suppliesService.saveSupplies(request).then((res) => {
            if(navigationData.onSaveCallback){
              navigationData.onSaveCallback();
            }
          });    
        });

        quickReportService.sendQuickReport(navigationData.eventId, this.isQualified ? QuickReportTypes.CarActive : QuickReportTypes.Broken, this.reasonSelected ? this.reasonSelected.text : null);      
      }
   });

   this.suppliesCloseEmitter = mappingNavigationDataService.$suppliesCloseEmitter.subscribe(result => {          
    let navigationData = navigationService.getNavigationData();

    if(navigationData.onCloseCallback){
      navigationData.onCloseCallback();
    }
  });
  }

  ngOnDestroy() {
      this.suppliesSaveEmitter.unsubscribe();
      this.suppliesCloseEmitter.unsubscribe();
  }

  private valueChanged(value: DropDownData){    
    this.reasonSelected = value;
    this.isPumpValid = this.isWaterValid = this.isPouderValid = this.isFoamValid = this.isReasonValid = true;
  }

  isDataValid(){    
    this.isPumpValid = this.isWaterValid = this.isPouderValid = this.isFoamValid = this.isReasonValid = true;
    
    this.isPumpValid = !this.suppliesModel.pumpMinute || this.suppliesModel.pumpMinute.toString().trim() == '' || /^[+]?([0-9])*$/.test(this.suppliesModel.pumpMinute.toString().trim());
    this.isWaterValid = !this.suppliesModel.water || this.suppliesModel.water.toString().trim() == '' || /^[+]?([0-9]+([.][0-9]*)?|[.][0-9]+)$/.test(this.suppliesModel.water.toString().trim());
    this.isPouderValid = !this.suppliesModel.powder || this.suppliesModel.powder.toString().trim() == ''|| /^[+]?([0-9]+([.][0-9]*)?|[.][0-9]+)$/.test(this.suppliesModel.powder.toString().trim());
    this.isFoamValid = !this.suppliesModel.foam || this.suppliesModel.foam.toString().trim() == '' || /^[+]?([0-9]+([.][0-9]*)?|[.][0-9]+)$/.test(this.suppliesModel.foam.toString().trim());
    
    this.isReasonValid = this.isQualified || (!this.isQualified && this.reasonSelected != null && this.reasonSelected.value != undefined);

    return this.isPumpValid && this.isWaterValid && this.isPouderValid && this.isFoamValid && this.isReasonValid;
  }

  private qualifiedStateChanged(state){
    this.isQualified = state;
    this.isPumpValid = this.isWaterValid = this.isPouderValid = this.isFoamValid = this.isReasonValid = true;
  }
}



