import { ItemStorage } from '../base/ItemStorage';
import { Injectable } from '@angular/core';
import { HttpService } from '../../core/http.service';
import { StorageResponse } from '../base/StorageResponse';
import { ConnectionService } from '../connection/connection.service';
import { OfflineStorage } from '../../stores/offlineStorage/OfflineStorage';
import { AvalibleEmployeesModel } from '../../shared-classes/employees/avalible-employees-model';
import { employee } from '../../shared-classes/employees/employee';
import { SaveEmployeesRequest } from '../../shared-classes/request/save-employees-request';
import { AlertifyService } from '../general/alertify.service';
import { ContentService } from '../general/content.service';
import { FireTruckOfflineActionTypes } from '../../shared-classes/offline/fire-truck-offline-action-types';
import { ApplicationResponseStatus } from '../../shared-classes/response/ApplicationResponseStatus';

@Injectable()
export class EmployeesService extends ItemStorage<AvalibleEmployeesModel> {
    private storageItem: AvalibleEmployeesModel;
    private localStorageKey: string = "avalibleEmployees";

    constructor(
        private httpService: HttpService,
        private connectionService: ConnectionService,
        private store: OfflineStorage,
        private alertifyService: AlertifyService,
        contentService: ContentService
    ){              
        super(contentService);
    }

    public getItem(request: any): Promise<StorageResponse<AvalibleEmployeesModel>> {                           
        var pThis = this;   
        return new Promise((resolve, reject) => {                           
            let url = this.httpService.baseWebApi + 'Events/GetAvailableForcesForCarAssignment/';

            this.httpService.post<StorageResponse<any>>(url, request).subscribe(
                (response) => {  
                    if(response.resultStatus == ApplicationResponseStatus.Fail){
                        this.errorMsgPromise.then(() => {                     
                            this.alertifyService.error(this.content.getDataErrorMessage);
                            resolve(new StorageResponse<AvalibleEmployeesModel>().CreateFailResponse());   
                        });                        
                    }
                    else{
                        if(response && response.data && response.data.carAssigmentOptions){
                            this.storageItem = <AvalibleEmployeesModel>response.data.carAssigmentOptions;
                            this.store.setItem(this.localStorageKey, JSON.stringify(this.storageItem));     
                        }                                                                  

                        resolve(new StorageResponse<AvalibleEmployeesModel>().CreateResultResponse(
                            this.storageItem
                        ));    
                    }
                },
                (err) => {
                    this.errorMsgPromise.then(() => {                     
                        this.alertifyService.warring(this.content.getConnectionErrorMessage);
                        resolve(new StorageResponse<AvalibleEmployeesModel>().CreateFailResponse());   
                    }); 
                },                  
            );
        });                 
    }

    public addItem(item: AvalibleEmployeesModel): Promise<StorageResponse<boolean>> {
        if(!item){
            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<boolean>().CreateErrorResponse(
                    "the given item is null or empty."
                ));
            });
        }

        this.storageItem.availableEmployees = item.availableEmployees;
        this.storageItem.availableTeams = item.availableTeams;
        this.store.setItem(this.localStorageKey, JSON.stringify(item));  
        
        return new Promise((resolve, reject) => {            
            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });       
    }

    public removeItem(itemId: number): Promise<StorageResponse<boolean>>{        
        return new Promise((resolve, reject) => {            
            this.storageItem = null;
            this.store.removeItem(this.localStorageKey);

            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });  
    }

    public updateCurrentEventTeam(request: SaveEmployeesRequest): Promise<StorageResponse<boolean>>{                                    
        return new Promise((resolve, reject) => {                           
            let url = this.httpService.baseWebApi + 'Events/SaveCarTeam/';
            this.httpService.post<StorageResponse<any>>(url, request, FireTruckOfflineActionTypes.SaveCarTeam).subscribe(
                (response) => {                        
                    if(response.resultStatus == ApplicationResponseStatus.Fail){
                        this.alertifyService.error(this.content.saveDataErrorMessage);
                        resolve(new StorageResponse<boolean>().CreateFailResponse());
                    }
                    else{
                        this.alertifyService.success(this.content.successMessage);
                        resolve(new StorageResponse<boolean>().CreateSuccessResponse());
                    }
                },
                (err) => {
                    this.alertifyService.error(this.content.saveDataErrorMessage);
                    resolve(new StorageResponse<boolean>().CreateFailResponse());
                },                  
            );                       
        });           
    }    
}


