import { ArrayStorage } from '../base/ArrayStorage';
import { Injectable } from '@angular/core';
import { StorageResponse } from '../base/StorageResponse';
import { HttpService } from '../../core/http.service';
import { ContentService } from '../general/content.service';
import { AlertifyService } from '../general/alertify.service';
import { ApplicationResponseStatus } from '../../shared-classes/response/ApplicationResponseStatus';
import { Layer } from '../../shared-classes/esri/Layer';
import { LayerType } from '../../shared-classes/esri/LayerType';
import { GeometryType } from '../../shared-classes/esri/GeometryType';
import { IGraphicData } from '../../shared-classes/esri/IGraphicData';
import { ConnectionService } from '../connection/connection.service';
import { OfflineStorage } from '../../stores/offlineStorage/OfflineStorage';
import { CustomMap } from '../../shared-classes/esri/custom-map';
import { EmapService } from '../../core/emap.service';
import { Point } from '../../shared-classes/esri/Point';
import { AppConfig } from '../../app.config';
import { NodeService } from '../node/node.service';

@Injectable()
export class LayersService extends ArrayStorage<Layer>{
    private storageList: Array<Layer>;
    private mapLayerUpdateHandlers : {[mapId: string]: {[layerName: string]: Array<number>}} = {};

    constructor(
        private httpService: HttpService,
        contentService: ContentService,
        private connectionService: ConnectionService,
        private store: OfflineStorage,
        private config: AppConfig,
        private emapService: EmapService,
        private nodeService: NodeService,
        private alertifyService: AlertifyService)
    {              
        super(contentService);
    }

    getServerMapLayers(mapId: string):  Promise<StorageResponse<Array<Layer>>> { 
        if(this.storageList != null) 
        {
            return new Promise((resolve, reject) => {  
                resolve(new StorageResponse<Array<Layer>>().CreateResultResponse(
                    this.storageList
                ));
            });
        }
        else{     
            return new Promise((resolve, reject) => {    
                let url = this.httpService.baseWebApi + 'Layers/GetMapLayers/';
                //let request = { ServiceNames: ['additionaldata', 'userlayers', 'sitedata'], LayerNames: ['bridge_three', 'schools', '1'] };
                let request = null;
                this.httpService.post<StorageResponse<any>>(url, request).subscribe(
                    (response) => {  
                        if(response.resultStatus == ApplicationResponseStatus.Fail){
                            this.errorMsgPromise.then(() => {                     
                                this.alertifyService.error(this.content.getDataErrorMessage);
                                resolve(new StorageResponse<Array<Layer>>().CreateFailResponse());   
                            });                               
                        } 

                        if(response.data.length > 0){
                            this.storageList = response.data;

                            resolve(new StorageResponse<Array<Layer>>().CreateResultResponse(
                                this.storageList
                            ));    
                        }
                        else{
                            resolve(new StorageResponse<Array<Layer>>().CreateResultResponse(
                                new Array<Layer>()
                            ));    
                        }
                    },
                    (err) => {
                        this.errorMsgPromise.then(() => {                     
                            this.alertifyService.warring(this.content.getConnectionErrorMessage);
                            resolve(new StorageResponse<Array<Layer>>().CreateFailResponse());   
                        });  
                    },                  
                );    
            });
        }
    }  

    setDefaultLayersVisibility(mapId: string){
        this.emapService.getMap(mapId).then((map: CustomMap) => {                
            let mapLayers = map.getMapLayers();
            if(!mapLayers){;
                return;
            }

            let defaultVisibleLayers = new Array<Layer>();
            for(let index = 0; index < mapLayers.length; index++){
                if(mapLayers[index].visibleOnLoad)
                {
                    mapLayers[index].visible = mapLayers[index].visibleOnLoad;
                    defaultVisibleLayers.push(mapLayers[index]);
                }                
            }

            if(defaultVisibleLayers.length > 0){
                map.setVisibleLayer(defaultVisibleLayers);
            }
        });
    }
    
    // layers: Dictionary<Layer>,
    // backgrounds: Array<BackgroundLayer>,
    // options: esri.MapOptions // only needed: extent, center
    getLayersData(layers: Array<string>): Promise<StorageResponse<{ [layer: string]: Array<IGraphicData> }>> {
        //getting graphic layers initial data from server

        if(this.connectionService.isOffline())
        {
            let response:{[layer: string]: Array<IGraphicData> } = {};

            let storeData = JSON.parse(this.store.getItem('GraphicsLayersInitData'));

            if(storeData == null){
                return new Promise((resolve, reject) => {
                    resolve(new StorageResponse<{[layer: string]: Array<IGraphicData>}>().CreateResultResponse(
                        <{[layer: string]: Array<IGraphicData>}>response
                    ));
                });
            }

            for (let i = 0; i < layers.length; i++)
            {                    
                if (layers[i] in storeData) 
                {
                    response[layers[i]] = storeData[layers[i]];
                }
            }               

            return new Promise((resolve, reject) => {
                resolve(new StorageResponse<{[layer: string]: Array<IGraphicData>}>().CreateResultResponse(
                    <{[layer: string]: Array<IGraphicData>}>response
                ));
            });
        }
        else{
            var pThis = this;   
            return new Promise((resolve, reject) => {                           
                let url = this.httpService.baseWebApi + 'Map/GetMapLayers/';

                this.httpService.post<StorageResponse<any>>(url, {'layerNames': layers}).subscribe(
                    (response) => {                                                                                         
                        let storeData = JSON.parse(this.store.getItem('GraphicsLayersInitData')) || {};
                        let keys = Object.keys(response.data.layers);
                        for (let i = 0; i < keys.length; i++)
                        {
                            storeData[keys[i]] = response.data.layers[keys[i]];
                        }

                        this.store.setItem('GraphicsLayersInitData', JSON.stringify(storeData)); 

                        resolve(new StorageResponse<{[layer: string]: Array<IGraphicData>}>().CreateResultResponse(
                            <{[layer: string]: Array<IGraphicData>}>response.data.layers
                        ));
                    },
                    (err) => {
                        console.log(err);
                    },                  
                );
            });
        }  
    }

    getVisibleLayersData(mapId: string, layers: Array<string>): Promise<Array<Layer>> {
        return new Promise<Array<Layer>>((resolve, reject) => {
            this.emapService.getMap(mapId).then((map: CustomMap) => {                
                let mapLayers = map.getMapLayers();
                if(!mapLayers){
                    resolve(null);
                    return;
                }

                if (layers.length === 0) {
                    for (let j = 0; j < mapLayers.length; j++) {
                        let layer = mapLayers[j];
                        layer.visible = false;
                    }
                }
                else {                    
                    for (let j = 0; j < mapLayers.length; j++) {
                        let layer = mapLayers[j];
                        layer.visible = layers.indexOf(layer.layerName) !== -1;
                    }                    
                }
                resolve(mapLayers);
            });
        });
    }

    async updateRealtimeLayer(mapId: string, layerName: string, data: Array<IGraphicData>)
    {
        if (data === null || data === undefined || data.length === 0)
            return;

        this.emapService.getMap(mapId).then((map: CustomMap) => 
        {            
            let layer = map.getGraphicLayer(layerName);

            if (layer === null)
                return;

            for (let i=0; i < data.length; i++)
            {
                let obj = data[i];
                switch(obj.geometry.type)    
                {
                    case GeometryType.POINT:
                        layer.moveGraphic(obj.data.id, new Point(obj.geometry.x, obj.geometry.y));
                    break;
                }
            }            
        });
    }

    setVisibleLayers(mapId: string, layerNames: Array<string>) {                
        this.emapService.getMap(mapId).then((map: CustomMap) => {                       
            this.getVisibleLayersData(mapId, layerNames).then((layers) => {
                if(layers == null){
                    return;
                }

                map.setVisibleLayer(layers);                    
                for (let i = 0; i < layers.length; i++)
                {
                    let layer = layers[i];
                    if (layer.layerType !== LayerType.GRAPHIC) //handling realtime data for Graphic Layers
                    {
                        continue;
                    }

                    if(!(mapId in this.mapLayerUpdateHandlers))
                    {
                        this.mapLayerUpdateHandlers[mapId] = {};
                    }

                    if(!(layer.layerName in this.mapLayerUpdateHandlers[mapId]))
                    {
                        this.mapLayerUpdateHandlers[mapId][layer.layerName] = new Array();
                    }

                    let namespace = this.config.getConfig('node_host') + this.config.getConfig('node_namespace');

                    if (layer.visible){
                        this.mapLayerUpdateHandlers[mapId][layer.layerName].push(this.nodeService.registerLayerUpdateEvent(namespace, layer.layerName, (data)=>{                            
                                this.updateRealtimeLayer(mapId, data.layer, data.data);
                        }));
                    }
                    else {
                        for (let i=0; i < this.mapLayerUpdateHandlers[mapId][layer.layerName].length; i++)
                        {
                            this.nodeService.unRegisterLayerUpdateEvent(namespace, layer.layerName, this.mapLayerUpdateHandlers[mapId][layer.layerName][i]);
                        }
                        
                    }
                }
            });

        });
    }

    getLayers(url: string): Promise<any> {
        return new Promise((resolve, reject) => {
            this.httpService.get<any>(url).subscribe(
                (response) => {
                    resolve(response);
                },
                (err) => {
                    console.log(err);
                },
            );
        });


    }
}


