import { FireTruckOfflineAction } from "../offline/fire-truck-offline-action";

export class OfflineActionsRequest{
    offlineActions: Array<FireTruckOfflineAction>;
    carId: number;
    deviceId: string;

    constructor(deviceId: string, carId: number, items: Array<FireTruckOfflineAction>){
        this.carId = carId;
        this.offlineActions = items;
        this.deviceId = deviceId;
     }      
}


