import { Injectable } from '@angular/core';
import { ProjectionService } from './projection.service';
import { IGPSResult, ISuccess } from '../../shared-classes/esri/IGPSResult';
import { StatusCode } from '../../shared-classes/common/status-code';
import { IError } from '../../shared-classes/common/error';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class GPSService {      
    private _navigatorWatchHandler: number;
    private _gpsPerrmissionDenied: string;
    private _gpsNotAvailable: string;
    private _gpsUnknown: string;
    private _gpsError: string;                    
    private _watchPosition: Observable<IGPSResult>;
    constructor(private projService: ProjectionService) {                
        this._watchPosition = null;
    }    

    public getPosition(): Promise<IGPSResult>
    {
        let pThis = this;
        return new Promise((resolve, reject) => {        

        if (navigator.geolocation == null || navigator.geolocation.getCurrentPosition == null || navigator.platform.indexOf("Win") != -1)
        {
            return reject(pThis.getErrorMessageResult(pThis._gpsError));
        }

        navigator.geolocation.getCurrentPosition((e)=>{
            resolve(pThis.getSuccessResult(e));
            },
            (e)=>{
                reject(pThis.getErrorResult(e));
            }, { maximumAge: 3000, timeout: 10000, enableHighAccuracy: true });
        
        });
    }

    public watchPosition(): Observable<IGPSResult>
    {        
        let pThis = this;
        if (pThis._watchPosition == null)
        {
            pThis._watchPosition = new Observable(observer => {
                if (navigator.geolocation == null || navigator.geolocation.watchPosition == null)
                {
                    observer.error(pThis.getErrorMessageResult(pThis._gpsError));
                }
    
                pThis._navigatorWatchHandler = navigator.geolocation.watchPosition((e)=>{
                    observer.next(pThis.getSuccessResult(e));
                },
                    (e)=>{
                        this.clearNavigator();
                        observer.error(pThis.getErrorResult(e));
                    }, { maximumAge: 3000, timeout: 10000, enableHighAccuracy: true });                    
            });
        }

        return pThis._watchPosition;
    }

    public stopWatchPosition(){
        this.clearNavigator();
    }

    public clearNavigator(): void{
        navigator.geolocation.clearWatch(this._navigatorWatchHandler);
        this._navigatorWatchHandler = null;
        this._watchPosition = null;
    }
    private getErrorMessageResult(e: string): IGPSResult
    {
        var error: IError = {
            msg: ""
        };
        error.msg = e;

        return this.setResult(StatusCode.FAIL, null, error);
    }

    private getErrorResult(e: any, isHighAccuracy?:boolean)
    {
        var error: IError = {
            msg: ""
        };

        switch (e.code)
        {
            case e.PERMISSION_DENIED:
                error.msg = this._gpsPerrmissionDenied;
                break;
            case e.POSITION_UNAVAILABLE:
                error.msg = this._gpsNotAvailable;
                break;
            case e.TIMEOUT:
                if (!isHighAccuracy)
                    error.msg = this._gpsNotAvailable;
                break;
            case e.UNKNOWN_ERROR:
                error.msg = this._gpsError;
                break;
        }

        return this.setResult(StatusCode.FAIL, null, error);        
    }

    private getSuccessResult(e): IGPSResult
    {
        var success: ISuccess;
        success = {
            accuracy: e.coords.accuracy,
            heading: e.coords.heading,
            lat: e.coords.latitude,
            lon: e.coords.longitude,
            speed: e.coords.speed
        };

        success = this.projectCoordinates(success);

        return this.setResult(StatusCode.SUCCESS, success, null);
    }

    private projectCoordinates(success): ISuccess
    {
        var coords = this.projService.transformWGS1984ToITM(success.lat, success.lon);
        success.x = coords.x;
        success.y = coords.y;

        return success;
    }

    private setResult(status: StatusCode, success: ISuccess, error: IError): IGPSResult
    {
        var result: IGPSResult;
        result = {
            status: status,
            error: error,
            success: success
        };
        return result;
    }
}


