import { Pipe, PipeTransform } from '@angular/core';
import * as moment from 'moment/moment';

@Pipe({
  name: 'dateHours'
})
export class DateHoursPipe implements PipeTransform {
  transform(value: any): string {
      moment.locale('he');
      return moment(value).format("HH:mm");    
  }
}


