import { Component, OnInit } from '@angular/core';
import { Dictionary } from '../shared-classes/IDictionary';
import { ContentService } from '../services/general/content.service';
import { menuTypes } from '../shared-classes/common/menu-types';
import { detailsbarTypes } from '../shared-classes/common/detailsbar-types';
import { NavigationService } from '../services/navigation/navigation.service';
import { MapStateService } from '../services/mapState/map-state.service';
import { CurrentEventDetailsService } from '../services/event/current-event-details.service';
import { EventDetailsService } from '../services/event/event-details.service';
import { DropDownData } from '../shared-classes/common/drop-down-data';
import { ReportService } from '../services/report/report.service';
import { MappingNavigationDataService } from '../services/navigation/mapping-navigation-data.service';
import { BaseComponent } from '../shared-classes/common/base-component';
import { GetEventReportDataRequest } from '../shared-classes/request/get-event-report-data-request';
import { EventReportData } from '../shared-classes/report/event-report-data';
import { EventLocationData } from '../shared-classes/report/event-location-data';
import { ReportModel } from '../shared-classes/report/report-model';
import { UsedEquipment } from '../shared-classes/report/used-equipment';

@Component({
 selector:'report',
  templateUrl: './report.component.html'
})

export class ReportComponent extends BaseComponent{
  private ownerTypeList: Array<DropDownData>;
  private byTypeList: Array<DropDownData>;  
  private outcomeTypeList: Array<DropDownData>;
  
  private ownerTypeSelected: DropDownData;
  private byTypeSelected: DropDownData;
  private outcomeTypeSelected: DropDownData;

  private eventId: number;

  private reportSaveEmitter: any;
  private eventTypeList: Array<DropDownData>;     
  private eventTypeSearch: Function;
  private selectedEventType: DropDownData;

  private cityList: Array<DropDownData>; 
  private citySearch: Function;
  private selectedCity: DropDownData;

  private streetList: Array<DropDownData>;
  private streetSearch: Function;
  private selectedStreet: DropDownData;

  private streetNumberList: Array<DropDownData>;
  private streetNumberSearch: Function;
  private selectedStreetNumber: DropDownData;
  
  private junctionList: Array<DropDownData>;
  private junctionSearch: Function;
  private selectedJunction: DropDownData;

  private roadList: Array<DropDownData>;
  private roadSearch: Function;
  private selectedRoad: DropDownData;

  private roadMileList: Array<DropDownData>;
  private roadMileSearch: Function;
  private selectedRoadMile: DropDownData;

  private usedEquipmentSearch: Function;  
  private usedEquipmentList: Array<UsedEquipment>;

  private reportData: EventReportData;

  constructor( 
    private navigationService: NavigationService,
    private currentEventDetailsService: CurrentEventDetailsService,
    private eventDetailsService: EventDetailsService,
    private reportService: ReportService,
    contentService: ContentService,
    mapStateService: MapStateService,
    mappingNavigationDataService: MappingNavigationDataService
  ){ 
    super(contentService, 'report');

    mapStateService.changeMapState(false);

    let navData = this.navigationService.getNavigationData();
    this.eventId = navData.eventId;

    this.ownerTypeList = new Array<DropDownData>();
    this.byTypeList = new Array<DropDownData>();
    this.outcomeTypeList = new Array<DropDownData>();
    this.reportData = new EventReportData();
    this.eventTypeList = new Array<DropDownData>();
    this.usedEquipmentList = new Array<UsedEquipment>();

    this.selectedCity = new DropDownData();
    this.selectedEventType = new DropDownData();
    this.selectedStreet = new DropDownData();
    this.selectedStreetNumber = new DropDownData();
    this.selectedRoad = new DropDownData();
    this.selectedRoadMile = new DropDownData();
    this.selectedJunction = new DropDownData();    

    let request = new GetEventReportDataRequest();
    request.eventId = this.eventId;

    this.reportService.getReportModel(request).then((response) => {
      if(response.isSuccessWithResult()){        
        this.ownerTypeList = response.data.ownerTypeIds;  
        this.byTypeList = response.data.locationTypes;    
        this.outcomeTypeList = response.data.eventCloseReasons; 
        this.reportData = response.data.reportData;  

        this.initilizeData();        
      }    
    });

    this.reportSaveEmitter = mappingNavigationDataService.$reportSaveEmitter.subscribe(result => {    
      this.saveReport();
    });

    this.eventTypeSearch = (searchVal: string, eventTypeId: string = null) =>{
        this.reportService.getEventTypes(searchVal).then((response) => {        
          if(response && response.data && response.data.length > 0){
            this.eventTypeList = response.data;

            if(eventTypeId){
              for(let index = 0; index < this.eventTypeList.length; index++){
                if(this.eventTypeList[index].value == eventTypeId){
                  this.selectedEventType = this.eventTypeList[index];                 
                  break;
                }
              }
            }
          }        
          else{
            this.eventTypeList = new Array<DropDownData>();
            this.selectedEventType = new DropDownData();
          }
        })
    }

    this.citySearch = (searchVal: string, reportData: EventReportData = null) =>{      
      if(searchVal.length > 0){
        this.reportService.getCities(searchVal).then((response) => {        
          if(response && response.data && response.data.length > 0){
            this.cityList = response.data;

            if(reportData != null && reportData.actualEventLocation && reportData.actualEventLocation.cityId){
              for(let index = 0; index < this.cityList.length; index++){
                if(this.cityList[index].value == reportData.actualEventLocation.cityId){
                  this.selectedCity = this.cityList[index];    
                  
                  if(reportData.actualEventLocation.streetDesc){
                    this.streetSearch(reportData.actualEventLocation.streetDesc, reportData);
                    break;  
                  }
                  else if(reportData.actualEventLocation.roadDesc){
                    this.roadSearch(reportData.actualEventLocation.streetDesc, reportData);
                    break;                      
                  }
                }
              }
            }
          }        
          else{
            this.cityList = new Array<DropDownData>();
            this.selectedCity = new DropDownData();
            this.selectedStreet = new DropDownData();
            this.selectedStreetNumber = new DropDownData();
          }
        })
      }
      else{
        this.cityList = new Array<DropDownData>();
        this.selectedCity = new DropDownData();
        this.selectedStreet = new DropDownData();
        this.selectedStreetNumber = new DropDownData();
      }
    }

    this.streetSearch = (searchVal: string, reportData: EventReportData = null) =>{
      if(this.selectedCity != null && searchVal.length > 0){
        this.reportService.getStreets(searchVal, this.selectedCity.value).then((response) => {        
          if(response && response.data && response.data.length > 0){
            this.streetList = response.data;

            if(reportData != null && reportData.actualEventLocation && reportData.actualEventLocation.streetId){
              for(let index = 0; index < this.streetList.length; index++){
                if(this.streetList[index].value == reportData.actualEventLocation.streetId){
                  this.selectedStreet = this.streetList[index]; 
                  
                  if(reportData.actualEventLocation.houseNumberDesc){
                    this.streetNumberSearch(reportData.actualEventLocation.houseNumberDesc, reportData);
                    break;  
                  }
                }
              }
            }
          }        
          else{
            this.streetList = new Array<DropDownData>();
            this.selectedStreet = new DropDownData();
            this.selectedStreetNumber = new DropDownData();
          }
        });
      } 
      else{
        this.streetList = new Array<DropDownData>();
        this.selectedStreet = new DropDownData();
        this.selectedStreetNumber = new DropDownData();
      } 
    }

    this.streetNumberSearch = (searchVal: string, reportData: EventReportData = null) =>{
      if(this.selectedStreet != null && searchVal.length > 0){
        this.reportService.getStreetNumbers(searchVal, this.selectedStreet.value).then((response) => {        
          if(response && response.data && response.data.length > 0){
            this.streetNumberList = response.data;

            if(reportData != null && reportData.actualEventLocation && reportData.actualEventLocation.houseNumberId){
              for(let index = 0; index < this.streetNumberList.length; index++){
                if(this.streetNumberList[index].value == reportData.actualEventLocation.houseNumberId){
                  this.selectedStreetNumber = this.streetNumberList[index];                 
                  break;
                }
              }
            }
          }        
          else{
            this.streetNumberList = new Array<DropDownData>();
            this.selectedStreetNumber = new DropDownData();
          }
        });
      }  
    }  
    
    this.junctionSearch = (searchVal: string, reportData: EventReportData = null) =>{
      if(searchVal.length > 0){
        this.reportService.getJunctions(searchVal).then((response) => {        
          if(response && response.data && response.data.length > 0){
            this.junctionList = response.data;

            if(reportData != null && reportData.actualEventLocation && reportData.actualEventLocation.junctionId){
              for(let index = 0; index < this.junctionList.length; index++){
                if(this.junctionList[index].value == reportData.actualEventLocation.junctionId){
                  this.selectedJunction = this.junctionList[index];                 
                  break;
                }
              }
            }
          }        
          else{
            this.junctionList = new Array<DropDownData>();
            this.selectedJunction = new DropDownData();
          }
        });
      }
      else{
        this.junctionList = new Array<DropDownData>();
        this.selectedJunction = new DropDownData();
      }  
    }  

    this.roadSearch = (searchVal: string, reportData: EventReportData = null) =>{
      if(this.selectedCity != null && searchVal.length > 0){
        this.reportService.getRoads(searchVal, this.selectedCity.value).then((response) => {        
          if(response && response.data && response.data.length > 0){
            this.roadList = response.data;

            if(reportData != null && reportData.actualEventLocation && reportData.actualEventLocation.roadId){
              for(let index = 0; index < this.roadList.length; index++){
                if(this.roadList[index].value == reportData.actualEventLocation.roadId){
                  this.selectedRoad = this.roadList[index];  

                  if(reportData.actualEventLocation.roadMilestoneDesc){
                    this.roadMileSearch(reportData.actualEventLocation.roadMilestoneDesc, reportData);
                    break;  
                  }
                }
              }
            }
          }        
          else{
            this.roadList = new Array<DropDownData>();
            this.selectedRoad = new DropDownData();
            this.selectedRoadMile = new DropDownData();
          }
        });
      } 
      else{
        this.roadList = new Array<DropDownData>();
        this.selectedRoad = new DropDownData();
        this.selectedRoadMile = new DropDownData();
      } 
    }

    this.roadMileSearch = (searchVal: string, reportData: EventReportData = null) =>{
      if(this.selectedRoad != null && searchVal.length > 0){
        this.reportService.getRoadMiles(searchVal, this.selectedRoad.text).then((response) => {        
          if(response && response.data && response.data.length > 0){
            this.roadMileList = response.data;

            if(reportData && reportData.actualEventLocation && reportData.actualEventLocation.roadMilestoneId){
              for(let index = 0; index < this.roadMileList.length; index++){
                if(this.roadMileList[index].value == reportData.actualEventLocation.roadMilestoneId){
                  this.selectedRoadMile = this.roadMileList[index];                 
                  break;
                }
              }
            }
          }        
          else{
            this.roadMileList = new Array<DropDownData>();
            this.selectedRoadMile = new DropDownData();
          }
        });
      }  
    }   
    
    this.usedEquipmentSearch = (searchVal: string, reportData: EventReportData = null) =>{
      this.reportService.getUsedEquipment(searchVal).then((response) => {        
        if(response && response.data && response.data.length > 0){
          this.usedEquipmentList = response.data;
        }        
        else{
          this.usedEquipmentList = new Array<UsedEquipment>();
        }
      }); 
    }  
  }

  private addUsedEquipment(){
    if(!this.reportData.reportEquipments){
      this.reportData.reportEquipments = new Array<UsedEquipment>();
    }

    this.reportData.reportEquipments.unshift(new UsedEquipment());
  }

  private ownerValueChanged(value: DropDownData){      
    this.ownerTypeSelected = value; 
    this.reportData.relatedBusiness.ownerTypeId = value.value;    
    this.reportData.relatedBusiness.isPrivateOwner = value.value == 1;    
  }

  private byValueChanged(value: DropDownData){       
    this.byTypeSelected = value; 
    this.reportData.actualLocationTypeId = value.value; 
    this.reportData.actualLocationTypeDesc = value.text; 
  }

  private outcomeValueChanged(value: DropDownData){        
    this.outcomeTypeSelected = value;
    this.reportData.eventCloseReasonId = value.value;    
  }

  private eventTypeSelected(value: DropDownData){        
    this.selectedEventType = value;
    this.reportData.actualEventType.eventTypeFullDesc = value.text;
    this.reportData.actualEventType.eventTypeId = value.value;
  }

  private citySelected(value: DropDownData){   
    this.selectedCity = value;
    this.reportData.actualEventLocation.cityId = value.value;
    this.reportData.actualEventLocation.cityDesc = value.text;
  }

  private streetSelected(value: DropDownData){        
    this.selectedStreet = value;
    this.reportData.actualEventLocation.streetId = value.value;
    this.reportData.actualEventLocation.streetDesc = value.text;
  }

  private streetNumberSelected(value: DropDownData){        
    this.selectedStreetNumber = value;
    this.reportData.actualEventLocation.houseNumberId = value.value;
    this.reportData.actualEventLocation.houseNumberDesc = value.text;
  }

  private junctionSelected(value: DropDownData){        
    this.selectedJunction = value;
    this.reportData.actualEventLocation.junctionId = value.value;
    this.reportData.actualEventLocation.junctionDesc = value.text;
  }

  private usedEquipmentSelected(event){    
    this.reportData.reportEquipments[event.itemIndex] = event.item;
  }

  private roadSelected(value: DropDownData){        
    this.selectedRoad = value;
    this.reportData.actualEventLocation.roadId = value.value;
    this.reportData.actualEventLocation.roadDesc = value.text;
  }

  private roadMileSelected(value: DropDownData){        
    this.selectedRoadMile = value;
    this.reportData.actualEventLocation.roadMilestoneId = value.value;
    this.reportData.actualEventLocation.roadMilestoneDesc = value.text;
  }

  private initilizeData(){

    if(this.reportData == null){
      return;
    }

    this.setByType();
    this.setOwnerType();
    this.setOutcomeType();
   
    if(this.reportData.actualEventType.eventTypeId){
      this.eventTypeSearch(this.reportData.actualEventType.eventTypeFullDesc, this.reportData.actualEventType.eventTypeId);
    }

    if(this.reportData.actualLocationTypeId == 1 && this.reportData.actualEventLocation.cityId){
      this.citySearch(this.reportData.actualEventLocation.cityDesc, this.reportData);
    }
    else if(this.reportData.actualLocationTypeId == 3 && this.reportData.actualEventLocation.junctionId){
      this.junctionSearch(this.reportData.actualEventLocation.junctionDesc, this.reportData);
    }
    else if(this.reportData.actualLocationTypeId == 4 && this.reportData.actualEventLocation.roadId){
      if(this.reportData.actualEventLocation.cityId != null && this.reportData.actualEventLocation.cityDesc != null){
        this.selectedCity.value = this.reportData.actualEventLocation.cityId;
        this.selectedCity.text = this.reportData.actualEventLocation.cityDesc;
      }
      
      this.roadSearch(this.reportData.actualEventLocation.roadDesc, this.reportData);
    }
  }

  private setByType(){    
    if(this.reportData.actualLocationTypeId != 0 && this.reportData.actualLocationTypeDesc != null){
      for(let index = 0; index < this.byTypeList.length; index++){
        if(this.byTypeList[index].value == this.reportData.actualLocationTypeId){
          this.byTypeSelected = this.byTypeList[index];   
          break; 
        }
      }
    }
  }

  private setOutcomeType(){
    if(this.reportData.eventCloseReasonId != null && this.reportData.eventCloseReasonId != 0){
      for(let index = 0; index < this.outcomeTypeList.length; index++){
        if(this.outcomeTypeList[index].value == this.reportData.eventCloseReasonId){
          this.outcomeTypeSelected = this.outcomeTypeList[index];   
          break; 
        }
      }
    }
  }

  private setOwnerType(){
    if(this.reportData.relatedBusiness.ownerTypeId != null && this.reportData.relatedBusiness.ownerTypeId != 0){
      for(let index = 0; index < this.ownerTypeList.length; index++){        
        if(this.ownerTypeList[index].value == this.reportData.relatedBusiness.ownerTypeId){
          this.ownerTypeSelected = this.ownerTypeList[index];   
          this.reportData.relatedBusiness.isPrivateOwner = this.ownerTypeSelected.value == 1; 
          break;
        }
      }
    }
  }

  private saveReport(){    
    if((this.byTypeSelected.value == 1 && (!this.isFlatValid() || !this.isFloorsValid() || !this.isFloorValid() || !this.isNumberOfFloors())) || (!this.isEventInjuredValid())){
      return;
    }

    this.reportService.saveEventReport(this.reportData).then((response) => {
      if(response.isSuccess()){
        this.navigationService.goBack();
      }     
    });  
  }

  private isFlatValid(){
    return !this.reportData.actualEventLocation.flatNumber || this.reportData.actualEventLocation.flatNumber.toString().trim() == '' || /^[+]?([0-9])*$/.test(this.reportData.actualEventLocation.flatNumber.toString().trim())
  }

  private isFloorValid(){
    return !this.reportData.actualEventLocation.floorNumber || this.reportData.actualEventLocation.floorNumber.toString().trim() == '' || /^[+]?([0-9])*$/.test(this.reportData.actualEventLocation.floorNumber.toString().trim());
  }

  private isNumberOfFloors(){
    return !this.reportData.actualEventLocation.numberOfFloors || this.reportData.actualEventLocation.numberOfFloors.toString().trim() == '' || /^[+]?([0-9])*$/.test(this.reportData.actualEventLocation.numberOfFloors.toString().trim());
  }

  private isFloorsValid(){
    return ((!this.reportData.actualEventLocation.floorNumber || this.reportData.actualEventLocation.floorNumber.toString().trim() == '') 
    ||  (!this.reportData.actualEventLocation.numberOfFloors || this.reportData.actualEventLocation.numberOfFloors.toString().trim() == '')
    || (parseInt(this.reportData.actualEventLocation.numberOfFloors.toString().trim()) > 0 && parseInt(this.reportData.actualEventLocation.floorNumber.toString().trim()) > 0 
    && parseInt(this.reportData.actualEventLocation.numberOfFloors.toString().trim()) >= parseInt(this.reportData.actualEventLocation.floorNumber.toString().trim())));
  }

  private isEmployeeLightValid(){
    return !this.reportData.eventInjured.employeeLight || this.reportData.eventInjured.employeeLight.toString().trim() == '' || /^[+]?([0-9])*$/.test(this.reportData.eventInjured.employeeLight.toString().trim())
  }

  private isOtherLightValid(){
    return !this.reportData.eventInjured.otherLight || this.reportData.eventInjured.otherLight.toString().trim() == '' || /^[+]?([0-9])*$/.test(this.reportData.eventInjured.otherLight.toString().trim())
  }

  private isEmployeeMediumValid(){
    return !this.reportData.eventInjured.employeeMedium || this.reportData.eventInjured.employeeMedium.toString().trim() == '' || /^[+]?([0-9])*$/.test(this.reportData.eventInjured.employeeMedium.toString().trim())
  }

  private isOtherMediumValid(){
    return !this.reportData.eventInjured.otherMedium || this.reportData.eventInjured.otherMedium.toString().trim() == '' || /^[+]?([0-9])*$/.test(this.reportData.eventInjured.otherMedium.toString().trim())
  }

  private isEmployeeDifficultValid(){
    return !this.reportData.eventInjured.employeeDifficult || this.reportData.eventInjured.employeeDifficult.toString().trim() == '' || /^[+]?([0-9])*$/.test(this.reportData.eventInjured.employeeDifficult.toString().trim())
  }

  private isOtherDifficultValid(){
    return !this.reportData.eventInjured.otherDifficult || this.reportData.eventInjured.otherDifficult.toString().trim() == '' || /^[+]?([0-9])*$/.test(this.reportData.eventInjured.otherDifficult.toString().trim())
  }

  private isEmployeeKilledValid(){
    return !this.reportData.eventInjured.employeeKilled || this.reportData.eventInjured.employeeKilled.toString().trim() == '' || /^[+]?([0-9])*$/.test(this.reportData.eventInjured.employeeKilled.toString().trim())
  }

  private isOtherKilledValid(){
    return !this.reportData.eventInjured.otherKilled || this.reportData.eventInjured.otherKilled.toString().trim() == '' || /^[+]?([0-9])*$/.test(this.reportData.eventInjured.otherKilled.toString().trim())
  }

  private isEventInjuredValid() {
      return this.isEmployeeLightValid() 
      && this.isOtherLightValid() 
      && this.isEmployeeMediumValid() 
      && this.isOtherMediumValid() 
      && this.isEmployeeDifficultValid() 
      && this.isOtherDifficultValid()
      && this.isEmployeeKilledValid() 
      && this.isOtherKilledValid()
  }

  ngOnDestroy() {
    this.reportSaveEmitter.unsubscribe();
  }
}



