
import { Injectable } from '@angular/core';
import { FireTruckOfflineAction } from '../../shared-classes/offline/fire-truck-offline-action';
import { ArrayStorage } from '../base/ArrayStorage';
import { ContentService } from '../general/content.service';
import { StorageResponse } from '../base/StorageResponse';
import { OfflineStorage } from '../../stores/offlineStorage/OfflineStorage';
import { ConnectionService } from '../connection/connection.service';

@Injectable()
export class QueueService extends ArrayStorage<FireTruckOfflineAction>{
    private storageList: Array<FireTruckOfflineAction> = new Array<FireTruckOfflineAction>();
    private localStorageKey: string = "queueList";

    constructor(contentService: ContentService, 
        private store: OfflineStorage,
        private connectionService: ConnectionService){
        super(contentService);
    }
    
    public addItem(item: FireTruckOfflineAction): Promise<StorageResponse<boolean>>{
        if(item == undefined){
            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<boolean>().CreateErrorResponse(
                    "the given item is null."
                ));
            });
        }

        this.storageList.push(item);
        this.store.setItem(this.localStorageKey, JSON.stringify(this.storageList));

        console.log(this.storageList);
        return new Promise((resolve, reject) => {            
            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });
    }

    public getList(request: any = null): Promise<StorageResponse<Array<FireTruckOfflineAction>>>{
        if(this.connectionService.isOffline())
        {
            this.storageList =  <Array<FireTruckOfflineAction>>JSON.parse(this.store.getItem(this.localStorageKey));
        }
        
        if(this.storageList != null) 
        {
            return new Promise((resolve, reject) => {  
                resolve(new StorageResponse<Array<FireTruckOfflineAction>>().CreateResultResponse(
                    this.storageList
                ));
            });
        }
        else{
            return new Promise((resolve, reject) => {  
                resolve(new StorageResponse<Array<FireTruckOfflineAction>>().CreateFailResponse());
            });
        }
    }

    public removeList(): Promise<StorageResponse<boolean>>{
        this.storageList = new Array<FireTruckOfflineAction>();
        this.store.removeItem(this.localStorageKey);

        return new Promise((resolve, reject) => {            
            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });
    }
}


