import { Injectable } from '@angular/core';
import {Subscription, Observable, Subject} from "rxjs";
import {interval} from "rxjs/observable/interval";
import {BehaviorSubject} from "rxjs";
import {HttpClient} from "@angular/common/http";

@Injectable()
export class KeepAliveMonitorService {

    private _networkCurrentStatus = new BehaviorSubject<boolean>(false);
    networkCurrentStatus = this._networkCurrentStatus.asObservable();
    private _networkLastStatus:boolean = false;
    keepAliveTimer:Subscription;
    ticks:Subscription[] = [];

    constructor(private _http:HttpClient) {
            
       
    }
    
    
    start(url:string,tick:number){
        this.keepAliveTimer = interval(tick).subscribe(tick =>{
           this._http.get<any>(url).subscribe(data =>{
            this.stop();    
                
            },(err =>{
            }));
        });
    }

    stop(){
        debugger
        this.keepAliveTimer.unsubscribe();
        if (!this.NetworkLastStatus){
                    this.NetworkLastStatus = true;
                    this.updatedNetworkCurrentStatus(true);
                    
                }
        // this.ticks.forEach(element => {
        //     element.unsubscribe();
        // });
      
       
    }
   
    updatedNetworkCurrentStatus(alive: boolean){
        this._networkCurrentStatus.next(alive);
    } 
    
    updatedNetworkLastStatus(alive: boolean){
        this._networkLastStatus = alive;
    }

    get NetworkLastStatus(): boolean {
        return this._networkLastStatus;
    }

    set NetworkLastStatus(status: boolean) {
            this._networkLastStatus = status;
    }
}


