import { Injectable, EventEmitter } from '@angular/core';
import { HttpService } from '../../core/http.service';
import { ConnectionService } from '../connection/connection.service';
import { HomeService } from '../home/home.service';
import { StorageResponse } from '../base/StorageResponse';
import { OfflineActionsRequest } from '../../shared-classes/request/offline-actions-request';
import { QueueService } from '../queue/queue.service';
import { ContentService } from './content.service';
import { AlertifyService } from './alertify.service';
import { ItemStorage } from '../base/ItemStorage';
import { ApplicationResponseStatus } from '../../shared-classes/response/ApplicationResponseStatus';
import { LogService } from './log.service';
import { LogInfo } from '../../shared-classes/common/log-info';
import { LogTypes } from '../../shared-classes/common/log-types';
import { EventListService } from '../event-list/event-list.service';
import { DeviceService } from './device.service';
import { ConnectionStateTypes } from '../../shared-classes/common/connection-state-types';

@Injectable()
export class RebootService extends ItemStorage<any>{  

    public $rebootFired: EventEmitter<boolean>;

    constructor(
        private httpService: HttpService, 
        private homeService: HomeService,
        private queueService: QueueService,
        private alertifyService: AlertifyService,
        private logService: LogService,
        private eventListService: EventListService,
        private deviceService: DeviceService,
        contentService: ContentService,
        private connectionService: ConnectionService
    ) {
        super(contentService);
        this.$rebootFired = new EventEmitter();
    }

    public sendAllQueuedItems(): Promise<StorageResponse<boolean>>{
        return new Promise((resolve, reject) => {             
                this.queueService.getList().then((queueResponse) => {
                    if(queueResponse.isSuccessWithResult() && queueResponse.data.length > 0){
                         let queueList = queueResponse.data;
                       this.homeService.getCar().then((homeResponse) => {
                            let carId = homeResponse.data.carId;
                            let request = new OfflineActionsRequest(this.deviceService.getDeviceId(), carId, queueList);                            
                            let url = this.httpService.baseWebApi + 'Main/UpdateOfflineActions/';

                            //this.logService.addItem(new LogInfo('update items array length: ' + queueList.length, 'RebootService: sendAllQueuedItems', 'reboot', LogTypes.debugger));

                            this.httpService.post<StorageResponse<any>>(url, request).subscribe((response) => {                                                     
                                if(response.resultStatus == ApplicationResponseStatus.Fail){
                                    this.alertifyService.error(this.content.updateDataErrorMessage);
                                    resolve(new StorageResponse<boolean>().CreateFailResponse());
                                } 
                                else{
                                    this.queueService.removeList();
                                    resolve(new StorageResponse<boolean>().CreateSuccessResponse());                                                       
                                }
                            },
                            (err) => {
                                this.alertifyService.error(this.content.updateDataErrorMessage);
                                resolve(new StorageResponse<boolean>().CreateFailResponse());                            
                            });
                        });
                    }
                    else{
                       this.rebootSystem().then(()=>{
                            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
                       });                                       
                   }
            });
        });      
    }

    public rebootSystem(): Promise<StorageResponse<boolean>>{
        return new Promise((resolve, reject) => {   
            //this.logService.addItem(new LogInfo('reboot system start', 'RebootService: System reboot start', 'reboot', LogTypes.info));          
            this.homeService.reboot().then(() =>{
                this.eventListService.reboot().then(() => {
                    //this.logService.addItem(new LogInfo('success', 'RebootService: System reboot completed', 'reboot', LogTypes.info));
                    this.$rebootFired.emit(true);
                    resolve(new StorageResponse<boolean>().CreateSuccessResponse());                                     
                });
            }); 
        });  
    }
}


      


