import { AddEquipmentRequestBase } from "./add-equipment-request-base";

export class AddEquipmentLackRequest extends AddEquipmentRequestBase{
    lackQuantity: number;

    constructor(carId: number, carCheckId: number, carEquipmentId: number, reportedEmployeeId: number, input: number){
        super(carId, carCheckId, carEquipmentId, reportedEmployeeId);

        this.lackQuantity = input;
        
    }
}


