//esri imports
import esriMap = require('esri/map');
import esri = require('esri');
import dojoString = require("dojo/string");
import TileInfo = require("esri/layers/TileInfo");
import SpatialReference = require("esri/SpatialReference");
import SimpleMarkerSymbol = require("esri/symbols/SimpleMarkerSymbol");
import PictureMarkerSymbol = require('esri/symbols/PictureMarkerSymbol');
import SimpleFillSymbol = require("esri/symbols/SimpleFillSymbol");
import SimpleLineSymbol = require("esri/symbols/SimpleLineSymbol");
import Color = require("esri/Color");
import Graphic = require("esri/graphic");
import ImageParameters = require("esri/layers/ImageParameters");
import esriConfig = require('esri/config');
import urlUtils = require("esri/urlUtils");
import Geometry = require("esri/geometry/Geometry");
import Symbol = require("esri/symbols/Symbol");
import jsonUtils = require("esri/symbols/jsonUtils");

//local imports
import { BackgroundLayer } from './BackgroundLayer';
import { WebTiledLayer } from './WebTiledLayer';
import { lang } from 'moment';
import { EventTypes } from '../event/event-types';
import { MapConfiguration } from './MapConfiguration';
import { WebTiledLayerOptions } from './WebTiledLayerOptions';
import { Point } from './Point';
import { Extent } from './Extent';
import { Circle } from './Circle';
import { GraphicLayerType } from './GraphicLayerType';
import { GPSService } from '../../services/general/gps.service';
import { Subscription } from 'rxjs/Subscription';
import { IGPSResult } from './IGPSResult';
import { DynamicMapServiceLayer } from './DynamicMapServiceLayer';
import { LayerType } from './LayerType';
import { IAGSService } from './IAGSService';
import { Layer } from './Layer';
import { GraphicsLayer } from './GraphicsLayer';
import { forEach } from '@angular/router/src/utils/collection';
import { GeometryType } from './GeometryType';
import { CustomPromise } from '../common/custom-promise';
import { LayersService } from '../../services/layers/layers.service';

export class CustomMap extends esriMap {

    private containerID: string = "container";
    private eventHandler: MapEventHandler;
    private active_background: Array<WebTiledLayer>;
    private graphic_layers: { [id: string]: GraphicsLayer } = {};
    private loadedMapResources: { [resource: string]: DynamicMapServiceLayer } = {};
    private promise: CustomPromise<CustomMap>;
    private __tileInfo: any;
    private mapConfiguration: MapConfiguration;

    private gpsMarker: {
        point: number;
        circle: number;
    };

    private marker: {
        point: number;
    };

    private defaultSelectionSymbol: {
        point: SimpleMarkerSymbol;
        polygon: SimpleFillSymbol;
        polyline: SimpleLineSymbol;
    }; //default selection symbol    

    constructor(containerID: string,
        mapConfiguration: MapConfiguration,
        private gpsService: GPSService,
        private layerService: LayersService) {
        super(containerID, mapConfiguration.options);
        this.containerID = containerID;
        this.active_background = new Array();

        this.mapConfiguration = mapConfiguration;

        this.gpsMarker = {
            point: null,
            circle: null
        };

        this.marker = {
            point: null
        };

        if (this.mapConfiguration.proxy.useProxy) {
            urlUtils.addProxyRule({
                urlPrefix: this.mapConfiguration.proxy.urlPrefix,
                proxyUrl: this.mapConfiguration.proxy.proxyUrl
            });

            esriConfig.defaults.io.alwaysUseProxy = true;
            esriConfig.defaults.io.corsDetection = false;
        }
        this.promise = new CustomPromise<CustomMap>();
        this.addEvent(EventTypes.MAP_LOAD, () => {
            //initializing graphic layers            
            var lineSymbol = new SimpleLineSymbol(SimpleLineSymbol.STYLE_SOLID,
                new Color([0, 0, 255]), 2);

            //Initializing selection default symbol
            this.defaultSelectionSymbol = {
                point: new SimpleMarkerSymbol(SimpleMarkerSymbol.STYLE_CIRCLE,
                    10, lineSymbol, new Color([0, 0, 255])),
                polyline: lineSymbol,
                polygon: new SimpleFillSymbol(SimpleFillSymbol.STYLE_SOLID,
                    lineSymbol, new Color([0, 0, 255, 0.50]))
            };

            for (let gLayer in GraphicLayerType) {
                
                this.addGrapichLayer(gLayer);
            }

            this.promise.resolve(this);
        });

    }

    getMapLayers(): Array<Layer>{        
        if(!this.mapConfiguration || !this.mapConfiguration.layers || this.mapConfiguration.layers.length == 0){
            return new Array<Layer>();
        }

        return this.mapConfiguration.layers;
    }

    public setMapLayers(layers: Array<Layer>){        
        if(!layers || layers.length == 0){
            return;
        }

        this.mapConfiguration.layers = layers;
    }

    public getMapPromise()
    {
        return this.promise;        
    }
    
    public addGrapichLayer(layerId: string)
    {
        this.graphic_layers[layerId] = new GraphicsLayer();
        this.addLayer(this.graphic_layers[layerId]);
        return this.graphic_layers[layerId];
    }

    public getGraphicLayer(layerId: string){
        if (!(layerId in this.graphic_layers))
        {
            return null;    
        }

        return this.graphic_layers[layerId];        
    }

    private getSelectionSymbol(geometry: Geometry): any {
        switch (geometry.type) {
            case 'point':
                return this.defaultSelectionSymbol.point;

            case 'polyline':
                return this.defaultSelectionSymbol.polyline;

            case 'polygon':
                return this.defaultSelectionSymbol.polygon;
        }
    }

    public SetMapSelection(geometries: Array<Geometry>, symbol?: Symbol) {
        if (geometries === null || geometries === undefined || geometries.length === 0)
            return -1;

        var layer = this.getGraphicLayer(GraphicLayerType.SELECTION);

        if (symbol === undefined || symbol === null) {
            symbol = this.getSelectionSymbol(geometries[0]);
        }

        geometries.forEach(geometry => {
            layer.addGraphic(geometry, symbol);
        });
    }

    public clearMapSelection() {
        var layer = this.getGraphicLayer(GraphicLayerType.SELECTION);
        layer.clearGraphics();
    }

    public setGPSMarker(x: number, y: number, accuracy: number): Extent {
        var point: Point = new Point(x, y);
        var circle: Circle = new Circle(x, y, accuracy);
        let layer = this.getGraphicLayer(GraphicLayerType.GPS);
        if (this.gpsMarker.point == null) {
            var pSymbol = new PictureMarkerSymbol({
                url: "images/gps.png",
                width: 48,
                height: 48
            });

            var fillSymbol = new SimpleFillSymbol(SimpleFillSymbol.STYLE_SOLID,
                null, new Color([130, 177, 255, 0.25]));            
            this.gpsMarker.circle = layer.addGraphic(circle, fillSymbol);
            this.gpsMarker.point = layer.addGraphic(point, pSymbol);

        }
        else { // move the graphic if it already exists        
            layer.moveGraphic(this.gpsMarker.point, point);
            layer.moveGraphic(this.gpsMarker.circle, circle);
        }

        return circle.getExtent();
    }

    public removeGpsMarker() {
        let layer = this.getGraphicLayer(GraphicLayerType.GPS);
        layer.removeGraphic(this.gpsMarker.point);
        layer.removeGraphic(this.gpsMarker.circle);
        this.gpsMarker = { //initializing gps marker object
            point: null,
            circle: null
        };
    }

    public setMapMarker(x: number, y: number){
        var point: Point = new Point(x, y);        
        let layer = this.getGraphicLayer(GraphicLayerType.DEFAULT);
        if (this.marker.point == null) {
            var pSymbol = new PictureMarkerSymbol({
                url: "images/marker.png",
                width: 48,
                height: 48
            });

            var fillSymbol = new SimpleFillSymbol(SimpleFillSymbol.STYLE_SOLID,
                null, new Color([130, 177, 255, 0.25]));            
            this.marker.point = layer.addGraphic(point, pSymbol);
        }
        else { // move the graphic if it already exists        
            layer.moveGraphic(this.marker.point, point);
        }
    }

    public removeMapMarker() {
        let layer = this.getGraphicLayer(GraphicLayerType.DEFAULT);
        layer.removeGraphic(this.marker.point);
        this.marker = { 
            point: null
        };
    }

    public gpsOn() {
        this.gpsService.watchPosition().subscribe((result: IGPSResult) => {
            this.zoomToExtent(this.setGPSMarker(result.success.x, result.success.y, result.success.accuracy));
        }, (result: IGPSResult) => {
            console.log(result.error.msg);
        })
    }

    public gpsOff() {
        this.gpsService.clearNavigator();
        this.removeGpsMarker();
    }

    public setCenter(json: { x: number; y: number; level: number; })
    public setCenter(level?: number)
    public setCenter(p1?: any) {
        var mapPoint: Point;
        mapPoint = (p1 && typeof p1 == "object" && ('x' in p1 && 'y' in p1)) ? new Point(p1.x, p1.y) : null;
        var level = (typeof p1 == "number") ? p1 : p1.level;
        if ((mapPoint == undefined || mapPoint == null) && (level == undefined || level == null)) {
            return;
        }

        if (mapPoint != undefined || mapPoint !== null) {
            if (level != undefined || level != null) {
                return this.centerAndZoom(mapPoint, level);
            }
            else {
                return this.centerAt(mapPoint);
            }
        }
        else if ((mapPoint === undefined || mapPoint === null) && this.isLevelInRange(level))
            return this.setLevel(level);
    }

    private isLevelInRange(level: number): boolean {
        return this.getZoomLevelResolution(level) != -1;
    }

    public getZoomLevelResolution(level: number): number {
        var lods = this.__tileInfo.lods;
        return (level in lods) ? lods[level].resolution : -1;
    }

    public getNumberOfLevels() {
        return this.__tileInfo.lods.length - 1;
    }

    public zoomIn(): void {
        this.setCenter(this.getLevel() + 1);
    }
    public zoomOut(): void {
        this.setCenter(this.getLevel() - 1);
    }

    public zoomToExtent(extent: Extent): void
    public zoomToExtent(xmin: number, ymin: number, xmax: number, ymax: number): void
    public zoomToExtent(p1?: any, p2?: any, p3?: any, p4?: any): void {
        var extentPointFactor = 50;
        var oExtent;
        if (typeof (p1) == "object") {
            oExtent = p1;
        }
        else {
            oExtent = new Extent(p1, p2, p3, p4);
        }

        if (p1 == p3 && p2 == p4) {
            oExtent.xmin = oExtent.xmin - extentPointFactor;
            oExtent.ymin = oExtent.ymin - extentPointFactor;
            oExtent.xmax = oExtent.xmax + extentPointFactor;
            oExtent.ymax = oExtent.ymax + extentPointFactor;
        }

        this.setExtent(oExtent, true);
    }

    addEvent(event: EventTypes, callback: Function): number {
        if (!this.eventHandler)
            this.eventHandler = new MapEventHandler(this);

        return this.eventHandler.addEvent(event, callback);
    }

    private clearBackground() {
        for (let i = 0; i < this.active_background.length; i++) {
            this.removeLayer(this.active_background[i]);
        }

        this.active_background = new Array();
    }

    addWebTiledLayer(background: BackgroundLayer, clear: boolean = true) {
        if (clear) //by default removing previouse backgrounds
        {
            this.clearBackground();
        }

        let tileInfo = new TileInfo({
            rows: background.tileWidth,
            cols: background.tileHeight,
            dpi: background.dpi,
            format: background.imageFormat,
            origin: new Point(background.origin),
            spatialReference: new SpatialReference(background.origin.spatialReference),
            lods: this.mapConfiguration.lods[background.lodIndex]
        });

        let webTiledLayerOptions = new WebTiledLayerOptions({
            id: background.id,
            tileInfo: tileInfo,
            initialExtent: (background.hasOwnProperty('extent')) ? new Extent(background.extent) : this.mapConfiguration.options.extent,
            fullExtent: (background.hasOwnProperty('extent')) ? new Extent(background.extent) : this.mapConfiguration.options.extent,
            visible: true,
            getUrlFunction: this.getTileUrl,
            urlTemplate: background.urlTemplate
        });

        let layer = new WebTiledLayer(webTiledLayerOptions);
        this.addLayer(layer);

        this.active_background.push(layer); //adding to array to support combined backgrounds

        //patching esri js api to support different background lods on the same map
        this.__tileInfo = tileInfo;
        //////////////////////////////////////////////////////////////////////////
    }

    getTileUrl(level: number, row: any, column: any, layer: BackgroundLayer) {
        return dojoString.substitute(layer.urlTemplate,
            ["L" + dojoString.pad(level.toString(), 2, '0'),
            "R" + dojoString.pad(row.toString(16), 8, '0'),
            "C" + dojoString.pad(column.toString(16), 8, '0')]);
    }

    getMapDynamicResource(resource: string): Promise<DynamicMapServiceLayer> {
        return new Promise<DynamicMapServiceLayer>((resolve, reject) => {
            if (!this.mapConfiguration.services[LayerType.AGS_DYNAMIC].hasOwnProperty(resource)) {
                reject();
            }

            if (this.loadedMapResources.hasOwnProperty(resource)) {
                resolve(this.loadedMapResources[resource]);
                return;

            }

            let serviceConfig = <IAGSService>this.mapConfiguration.services[LayerType.AGS_DYNAMIC][resource];

            var imageParams = new ImageParameters();
            imageParams.format = serviceConfig.imageFormat;

            let mService = new DynamicMapServiceLayer(serviceConfig.restUrl, {
                id: resource,
                imageParameters: imageParams,
                opacity: serviceConfig.opacity,
                visible: true
            });

            mService.suspend(); //suspend layer from drawing

            mService.on("error", (e) => {
                console.log("error adding resource: " + resource + " error: " + e);
                reject();
            });

            mService.on("load", () => {
                //overriding service object, esri js API has bug while re-ordering layerinfos if the defaultVisiblity in the service is false            
                var dyn = mService.createDynamicLayerInfosFromLayerInfos();
                for (var i = 0; i < dyn.length; i++) {
                    dyn[i].defaultVisibility = true;
                }

                mService.setDynamicLayerInfos(dyn, true);
                mService.setVisibleLayers([-1], true);
                //////////////////////////////////////////////////////


                //getting layer descriptions from AGS rest api
                let layersRestUrl = (this.mapConfiguration.proxy.useProxy) ? (this.mapConfiguration.proxy.proxyUrl + "?") : "";
                layersRestUrl += serviceConfig.restUrl + "/layers?f=json";

                this.layerService.getLayers(layersRestUrl).then((response) => {
                    for (let i = 0; i < response.layers.length; i++) {
                        let description = JSON.parse(response.layers[i].description);
                        if (!description.hasOwnProperty("layerName")) {
                            continue;
                        }

                        mService.addLayerMapping(description.layerName, response.layers[i].id);
                    }
                    this.loadedMapResources[resource] = mService;
                    mService.resume();
                    resolve(mService);
                })

            });

            this.addLayer(mService);
        });
    }

    getGeometry(geometry:any){
        switch(geometry.type)
        {
            case GeometryType.POINT:
                return new Point(geometry.x, geometry.y);
        }

        return null;
    }

    setVisibleLayer(layersList: Array<Layer>) {                
        let mapping: { [layeyType: number]: Array<Layer> } = {};
        //mapping requested layers according to layer type
        for (let i = 0; i < layersList.length; i++) {
            if (!mapping.hasOwnProperty(layersList[i].layerType)) {
                mapping[layersList[i].layerType] = new Array();
            }

            mapping[layersList[i].layerType].push(layersList[i]);
        }

        //looping each type of layer
        for (let key in mapping) {
            if (mapping.hasOwnProperty(key)) {
                let layers = mapping[key];

                switch (parseInt(key)) {
                    case LayerType.AGS_DYNAMIC: //Regular AGS dynamic layer
                        //grouping layers according to resource name
                        let resources: { [resource: string]: Array<Layer> } = {};
                        for (let i = 0; i < layers.length; i++) {
                            if (!resources.hasOwnProperty(layers[i].resource)) {
                                resources[layers[i].resource] = new Array();
                            }

                            resources[layers[i].resource].push(layers[i]);
                        }

                        //looping each resource
                        for (let resource in resources) {
                            if (resources.hasOwnProperty(resource)) {                                
                                let cLayers = resources[resource];
                                this.getMapDynamicResource(resource).then((mService) => {                                    
                                    //getting visible layers in service
                                    let visibleLayers = mService.visibleLayers.concat([]);

                                    //removing -1 from service
                                    if (visibleLayers.indexOf(-1) !== -1) {
                                        visibleLayers = [];
                                    }

                                    //looping requested layers in the service
                                    for (let j = 0; j < cLayers.length; j++) {
                                        let layer = cLayers[j];
                                        let layerID = mService.getLayerID(cLayers[j].layerName);

                                        let index = visibleLayers.indexOf(layerID);

                                        if (layer.visible && index === -1) {
                                            visibleLayers.push(layerID);
                                        } else if (!layer.visible && index !== -1) {
                                            visibleLayers.splice(index, 1);
                                        }
                                    }

                                    //setting visibility in the service
                                    mService.setVisibleLayers(visibleLayers, false);
                                });
                            }
                        }
                        break;
                        
                    case LayerType.GRAPHIC:
                        for (let i=0; i < layers.length; i++)
                        {
                            let layer = layers[i];
                            let gLayer = this.getGraphicLayer(layer.layerName);
                            
                            if (!layer.visible && gLayer === null)
                            {
                                continue;
                            }

                            if (!layer.visible && gLayer !== null)
                            {
                                gLayer.hide();
                                continue;
                            }

                            if (gLayer !== null)
                            {
                                gLayer.show();                            
                                continue;
                            }
                            
                            gLayer = this.addGrapichLayer(layer.layerName);        
                            let symbol = jsonUtils.fromJson(layer.symbol);
                            this.layerService.getLayersData([layer.layerName]).then((response)=>{
                                if (response.data !== null && layer.layerName in response.data)
                                {
                                    let layer_data = response.data[layer.layerName];
                                    for (let i=0; i < layer_data.length; i++)
                                    {
                                        let obj = layer_data[i];
                                        gLayer.addGraphic(this.getGeometry(obj.geometry), symbol, obj.data);
                                    }
                                }
                            });                            
                        }
                    default:
                        break;
                }
            }
        }
    }
}


export class MapEventHandler {
    private map: CustomMap;
    //private eventDefers: { [event: number]: ng.IDeferred<any> };
    private callbacks: { [event: number]: Array<Function> };
    private disabled: boolean = false;

    constructor(map: CustomMap) {
        this.map = map;
        this.callbacks = {};
        for (var e in EventTypes) {//Initialzing callbacks array
            var key = parseInt(e);
            this.callbacks[key] = new Array<Function>();
        }

        var ptrThis = this;

        map.on("extent-change", (data) => {
            ptrThis.executeCallbacks(EventTypes.EXTENT_CHANGE, {
                delta: data.delta,
                extent: data.extent,
                levelChange: data.levelChange,
                lod: data.lod
            });
        });

        map.on("load", (data) => {
            ptrThis.executeCallbacks(EventTypes.MAP_LOAD, data);
        });

        map.on("dbl-click", (data) => {
            ptrThis.executeCallbacks(EventTypes.DOUBLE_CLICK, {
                'mapPoint': data.mapPoint,
                'screenPoint': data.screenPoint
            });
        });

        map.on("click", (data) => {
            ptrThis.executeCallbacks(EventTypes.CLICK, {
                'mapPoint': data.mapPoint,
                'screenPoint': data.screenPoint
            });
        });

        map.on("pan", (data) => {
            ptrThis.executeCallbacks(EventTypes.PAN, {
                delta: data.delta,
                extent: data.extent
            });
        });

        map.on("mouse-move", (data) => {
            ptrThis.executeCallbacks(EventTypes.MOUSE_MOVE, {
                'mapPoint': data.mapPoint,
                'screenPoint': data.screenPoint
            });
        });

        map.on("mouse-over", (data) => {
            ptrThis.executeCallbacks(EventTypes.MOUSE_OVER, {
                'data': data
            });
        });

        map.on("mouse-down", (data) => {
            if (data.button === 2) {
                ptrThis.executeCallbacks(EventTypes.RIGHT_CLICK, {
                    mapPoint: data.mapPoint,
                    screenPoint: data.screenPoint
                });
            }

            if (data.button === 1) {
                ptrThis.executeCallbacks(EventTypes.MOUSE_DOWN, {
                    mapPoint: data.mapPoint,
                    screenPoint: data.screenPoint
                });
            }
        });

        map.on("mouse-drag-start", (data) => {
            ptrThis.executeCallbacks(EventTypes.MOUSE_DRAG_START, {
                mapPoint: data.mapPoint,
                screenPoint: data.screenPoint
            });
        });

        map.on("mouse-drag", (data) => {
            ptrThis.executeCallbacks(EventTypes.MOUSE_DRAG, {
                mapPoint: data.mapPoint,
                screenPoint: data.screenPoint
            });
        });
        map.on("resize", (data) => {
            ptrThis.executeCallbacks(EventTypes.RESIZE, {});
        });
    }

    public registerEventNotification
    public addEvent(event: EventTypes, callback: Function): number {
        return this.callbacks[event].push(callback) - 1;
    }

    public removeEvent(event: EventTypes, handler: number): void {
        if (event in this.callbacks && handler in this.callbacks[event])
            delete this.callbacks[event][handler];
    }

    public disableAllEvents() {
        this.disabled = true;
        this.map.disablePan();
        this.map.disableScrollWheelZoom();
    }

    public enableAllEvents() {
        this.disabled = false;
        this.map.enablePan();
        this.map.enableScrollWheelZoom();
    }

    private executeCallbacks(event: EventTypes, data: any): void {
        if (this.disabled)
            return;

        for (var i = 0; i < this.callbacks[event].length; i++) {
            if (this.callbacks[event] == null)
                continue;

            try {
                this.callbacks[event][i](data);
            }
            catch (e) { }
        }
    }
}


