import { Component, OnInit } from '@angular/core';
import { MappingNavigationDataService } from '../../services/navigation/mapping-navigation-data.service';
import { employee } from '../../shared-classes/employees/employee';
import { ContentService } from '../../services/general/content.service';
import { NavigationService } from '../../services/navigation/navigation.service';
import { HomeService } from '../../services/home/home.service';
import { AvalibleEmployeesModel } from '../../shared-classes/employees/avalible-employees-model';
import { EmployeesService } from '../../services/employees/employees.service';
import { EmployeesRequest } from '../../shared-classes/request/employees-request';
import { EmployeeViewType } from '../../shared-classes/employees/employee-view-type';
import { DeviceService } from '../../services/general/device.service';
import { AlertifyService } from '../../services/general/alertify.service';
import { EmployeesTeamData } from '../../shared-classes/employees/employee-team-data';
import { BaseComponent } from '../../shared-classes/common/base-component';
import { SaveEmployeesRequest } from '../../shared-classes/request/save-employees-request';
import { AlertifyTypes } from '../../shared-classes/alertify/alertify-types';

@Component({
 selector:'employees-team',
  templateUrl: './employees-team.component.html'
})

export class EmployeesTeamComponent extends BaseComponent{
  mapStateService: any;
  private teamEmployeeSaveEmitter: any;
  private currentEventTeam: Array<employee>;
  private avalibleEmployeesModel: AvalibleEmployeesModel;
  private teamName: string = '';
  private employeeView: EmployeeViewType;
  private carId: number;
  private isLoaded: boolean = false;
  private isValid: boolean = true;

  constructor(
      private mappingNavigationDataService: MappingNavigationDataService,
      private contentService: ContentService,
      private navigationService: NavigationService,
      private homeService: HomeService,
      private employeesService: EmployeesService,
      private deviceService: DeviceService,
      private alertifyService: AlertifyService
  ) {   
    super(contentService, 'employee-team');
    
    this.teamEmployeeSaveEmitter = this.mappingNavigationDataService.$teamEmployeeSaveEmitter.subscribe(result => { 
      if(this.currentEventTeam == null || this.currentEventTeam.length == 0){
        this.isValid = false;
      } 
      else{
        let request = new SaveEmployeesRequest();
        request.carId = this.carId;
        request.employees = this.currentEventTeam;
         this.employeesService.updateCurrentEventTeam(request).then((res) => {         
           if(res.isSuccess()){
            let data = <EmployeesTeamData>this.navigationService.getNavigationData();
            if(data && data.onCloseCallback){
              data.onCloseCallback();
            }
            else{
              this.navigationService.goBack();
            }          
           }
         });  
      }  
    });

    this.employeeView = EmployeeViewType.big;
    this.homeService.getCar().then((res) => {
      if(res.isSuccessWithResult())
      {
        this.teamName = res.data.carCallCode;
        this.carId = res.data.carId;
      }      
    });

    this.setAvalibleEmployees(this.deviceService.getDeviceId());
    this.setTeamEmployees();
    this.currentEventTeam = Array<employee>();
    this.avalibleEmployeesModel = new AvalibleEmployeesModel();
  }

  deleteAllTeam(){    
    let tempArray = new Array<employee>();
    for(let i = 0; i < this.currentEventTeam.length; i++){      
      tempArray.push(this.currentEventTeam[i]); 
    }

    for(let i = 0; i < tempArray.length; i++){
      this.removeEmployee({ employee: tempArray[i], index: i});
    }    

    this.currentEventTeam = Array<employee>();
  }

  setTeamEmployees(){
    this.homeService.getTeamEmployees().then((response) => {
      if(response.isSuccessWithResult())
      {
        for(let i = 0; i < response.data.length; i++)
        {
          this.currentEventTeam.push(response.data[i]);
          this.currentEventTeam[i].teamCarId = null;
        }
      }
     });
  }

  setAvalibleEmployees(deviceId: string){
    let request = new EmployeesRequest(deviceId);
    this.employeesService.getItem(request).then((response) => {      
      if(response.isSuccessWithResult())
      {        
        this.avalibleEmployeesModel.availableEmployees = response.data.availableEmployees;        
        this.avalibleEmployeesModel.availableTeams = response.data.availableTeams;
        this.isLoaded = true;
      }
     });
  }

  addEmployees(emp){
    this.isValid = true;
    this.currentEventTeam = this.currentEventTeam.concat(emp.employees);  
    this.avalibleEmployeesModel.availableTeams[emp.index].employees = [];
  }

  addEmployee(emp){
      this.isValid = true;
      let employee = emp.employee;
      let index = emp.index;
      this.currentEventTeam.unshift(employee);
      this.avalibleEmployeesModel.availableEmployees.splice(index, 1);
  }

  removeEmployee(emp){    
    let employee = emp.employee;
    let index = emp.index;

    if(employee.teamCarId){
      for(let i = 0; i < this.avalibleEmployeesModel.availableTeams.length; i++){
        if(employee.teamCarId == this.avalibleEmployeesModel.availableTeams[i].carInfo.carId){
          this.avalibleEmployeesModel.availableTeams[i].employees.push(employee);
        }
      }
    }
    else{
      this.avalibleEmployeesModel.availableEmployees.push(employee);
    }
    
    this.currentEventTeam.splice(index, 1);
}

  addQuickEmployee(emp){
    this.isValid = true;
    let employee = emp.employee;
    let index = emp.index;
    let emloyeeIndex = emp.emloyeeIndex;
    this.currentEventTeam.unshift(employee);
    this.avalibleEmployeesModel.availableTeams[index].employees.splice(emloyeeIndex, 1);
  }

  ngOnDestroy(){
    this.teamEmployeeSaveEmitter.unsubscribe();
  }
}



