import { DeviceRequest } from "./device-request";
import { QuickReportTypes } from "../quick-report/quick-report-types";
import * as moment from 'moment/moment';

export class SaveQuickReportRequest extends DeviceRequest {
    
    public carId: number;
    public eventLogReportTypeId: QuickReportTypes;
    public reportTime: string;
    public reportDesc: string;
    public eventId: number;
    
    constructor(deviceId: string){
        super(deviceId);
        this.reportTime = moment().format("YYYY/MM/DD HH:mm");
    }      
}


