import { EventDetailsTypes } from "./event-details-types";

export class EventDetailsData{
    eventId: number;
    eventDetailsType: EventDetailsTypes;
    eventNum: number;
    isNew: boolean;

    constructor(eventId: number, eventDetailsType: EventDetailsTypes, isNew: boolean = false, eventNum: number = null){
        this.eventId = eventId;
        this.eventDetailsType = eventDetailsType;
        this.isNew = isNew;
        this.eventNum = eventNum;
    }
}


