import { EventEmitter, Injectable } from '@angular/core';

@Injectable()
export class MapStateService {
    public mapStateChangedEvent$: EventEmitter<boolean>;
    
    constructor() {
        this.mapStateChangedEvent$ = new EventEmitter();
    }

    changeMapState(isMapVisible){                
        this.mapStateChangedEvent$.emit(isMapVisible);
    } 
}


