import { Component, EventEmitter, Output, OnChanges } from '@angular/core';
import { UsedEquipment } from '../../shared-classes/report/used-equipment';

@Component({
  selector: 'used-equipment',
  templateUrl: './used-equipment.component.html',
  inputs:['watermark', 'dataList', 'dataSearch', 'selected', 'itemIndex']
})
export class UsedEquipmentComponent {
  @Output() onValueChange: EventEmitter<any> = new EventEmitter();

  public watermark: string;
  public dataList: Array<UsedEquipment>;
  public selected: UsedEquipment = null;
  public dataSearch: Function;
  public itemIndex: number;

  constructor() {}

  private itemSelected(value: UsedEquipment){        
    this.selected = value;
    this.onValueChange.emit({ item : value, itemIndex : this.itemIndex });
  }
}

 




