import { ArrayStorage } from '../base/ArrayStorage';
import { Injectable } from '@angular/core';
import { EventGeneralInfo } from '../../shared-classes/event/event-general-info';
import { StorageResponse } from '../base/StorageResponse';
import { HttpService } from '../../core/http.service';
import { ConnectionService } from '../connection/connection.service';
import { OfflineStorage } from '../../stores/offlineStorage/OfflineStorage';
import { ContentService } from '../general/content.service';
import { AlertifyService } from '../general/alertify.service';
import { ApplicationResponseStatus } from '../../shared-classes/response/ApplicationResponseStatus';
import { DeviceService } from '../general/device.service';
import { HomeService } from '../home/home.service';
import { fireTruckInfo } from '../../shared-classes/common/fire-truck-info';
import { EventListRequest } from '../../shared-classes/request/event-list-request';
import { LogService } from '../general/log.service';
import { LogInfo } from '../../shared-classes/common/log-info';
import { LogTypes } from '../../shared-classes/common/log-types';

@Injectable()
export class EventListService extends ArrayStorage<EventGeneralInfo>{
    private storageList: Array<EventGeneralInfo>;
    private localStorageKey: string = "eventListModel";

    constructor(
        private store: OfflineStorage, 
        private httpService: HttpService, 
        private connectionService: ConnectionService,
        private deviceService:DeviceService,
        private homeService: HomeService,
        contentService: ContentService,
        private alertifyService: AlertifyService,
        private logService: LogService)
    {              
        super(contentService);
    }

    public addItem(item: EventGeneralInfo): Promise<StorageResponse<boolean>> {
        if(item == undefined){
            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<boolean>().CreateErrorResponse(
                    "the given item is null."
                ));
            });
        }

        this.isItemExists(item).then((response) => {
            if(response.isSuccess()){
                this.getItemIndex(item).then((response) => {
                    if(response.resultStatus == ApplicationResponseStatus.Fail){
                        return new Promise((resolve, reject) => {            
                            resolve(new StorageResponse<boolean>().CreateErrorResponse(
                                response.errorMessage
                            ));
                        });
                    }
    
                    this.storageList[response.data] = item;
                    
                    return new Promise((resolve, reject) => {            
                        resolve(new StorageResponse<boolean>().CreateSuccessResponse());
                    });
                });
            } 
            else if(this.storageList)
            {
                this.storageList.unshift(item);
                this.store.setItem(this.localStorageKey, JSON.stringify( this.storageList)); 
            }            

            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<boolean>().CreateSuccessResponse());
            });
        });
    }

    public isItemExists(item: EventGeneralInfo): Promise<StorageResponse<boolean>> {
        if(this.storageList == undefined || this.storageList.length == 0){
            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<boolean>().CreateFailResponse());
            });
        }

        for(let index = 0; index < this.storageList.length; index ++)
        {
            if(item.eventId == this.storageList[index].eventId)
            {
                return new Promise((resolve, reject) => {            
                    resolve(new StorageResponse<boolean>().CreateSuccessResponse());
                });
            }
        }

        return new Promise((resolve, reject) => {            
            resolve(new StorageResponse<boolean>().CreateFailResponse());
        });
    }

    public removeItem(itemID: number): Promise<StorageResponse<boolean>> {
        if(this.storageList == undefined || this.storageList.length == 0){
            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<number>().CreateErrorResponse(
                    "the item list is empty."
                ));
            });
        }
        this.getItemById(itemID).then((response) => {
            if(response.resultStatus == ApplicationResponseStatus.Fail)
            {
                return new Promise((resolve, reject) => {            
                    resolve(new StorageResponse<boolean>().CreateErrorResponse(
                        response.errorMessage
                    ));
                });
            }

            this.getItemIndex(response.data).then((res) => {
                if(res.isFail())
                {
                    return new Promise((resolve, reject) => {            
                        resolve(new StorageResponse<boolean>().CreateErrorResponse(
                            res.errorMessage
                        ));
                    });
                }
    
                this.storageList.splice(res.data, 1);
                
                return new Promise((resolve, reject) => {            
                    resolve(new StorageResponse<boolean>().CreateSuccessResponse());
                });
            });
        });
    }

    public getItemById(id: number): Promise<StorageResponse<EventGeneralInfo>> {
        if(this.storageList == undefined || this.storageList.length == 0){
            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<number>().CreateErrorResponse(
                    "the item list is empty."
                ));
            });
        }

        for(let index = 0; index < this.storageList.length; index++){
            if(this.storageList[index].eventId == id)
            {
                return new Promise((resolve, reject) => {            
                    resolve(new StorageResponse<number>().CreateResultResponse(
                        this.storageList[index]
                    ));
                });
            }
        }

        return new Promise((resolve, reject) => {            
            resolve(new StorageResponse<number>().CreateErrorResponse(
                "the item does not exists in list."
            ));
        });
    }

    public getItemIndex(item: EventGeneralInfo): Promise<StorageResponse<number>> {
        if(item == undefined){
            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<number>().CreateErrorResponse(
                    "the given item is null."
                ));
            });
        }

        if(this.storageList == undefined || this.storageList.length == 0){
            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<number>().CreateErrorResponse(
                    "the item list is empty."
                ));
            });
        }

        for(let index = 0; index < this.storageList.length; index++){
            if(this.storageList[index].eventId == item.eventId)
            {
                return new Promise((resolve, reject) => {            
                    resolve(new StorageResponse<number>().CreateResultResponse(
                        index
                    ));
                });
            }
        }

        return new Promise((resolve, reject) => {            
            resolve(new StorageResponse<number>().CreateErrorResponse(
                "the item does not exists in list."
            ));
        });
    }

    public getList(request: any): Promise<StorageResponse<Array<EventGeneralInfo>>> {                   
        if(this.connectionService.isOffline())
        {
            this.storageList =  <Array<EventGeneralInfo>>JSON.parse(this.store.getItem(this.localStorageKey));
        }
        
        if(this.storageList != null) 
        {
            return new Promise((resolve, reject) => {  
                resolve(new StorageResponse<Array<EventGeneralInfo>>().CreateResultResponse(
                    this.storageList
                ));
            });
        }
        else{
            var pThis = this;
            return new Promise((resolve, reject) => {  
                let url = this.httpService.baseWebApi + 'events/GetActiveEvents/';
                this.httpService.post<StorageResponse<any>>(url, request).subscribe(
                    (response) => {  
                        if(response.resultStatus == ApplicationResponseStatus.Fail){
                            this.errorMsgPromise.then(() => {                     
                                this.alertifyService.error(this.content.getDataErrorMessage);
                                resolve(new StorageResponse<Array<EventGeneralInfo>>().CreateFailResponse());   
                            });                             
                        }
                        else{
                            this.storageList = <Array<EventGeneralInfo>>response.data.activeEvents;
                            this.addList(this.storageList);
    
                            resolve(new StorageResponse<Array<EventGeneralInfo>>().CreateResultResponse(
                                this.storageList
                            ));    
                        }
                    },
                    (err) => {
                        this.errorMsgPromise.then(() => {                     
                            this.alertifyService.warring(this.content.getConnectionErrorMessage);
                            resolve(new StorageResponse<Array<EventGeneralInfo>>().CreateFailResponse());   
                        }); 
                    },                
                );
            }); 
        }                   
    }

    public addList(items: Array<EventGeneralInfo>): Promise<StorageResponse<boolean>> {
        if(items == undefined || items.length == 0){
            return new Promise((resolve, reject) => {            
                resolve(new StorageResponse<boolean>().CreateErrorResponse(
                    "the given items list is null or empty."
                ));
            });
        }

        this.store.setItem(this.localStorageKey, JSON.stringify(items));  
        this.storageList = items;
        
        return new Promise((resolve, reject) => {            
            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });       
    }

    public removeList(): Promise<StorageResponse<boolean>> {  
        return new Promise((resolve, reject) => {            
            this.storageList = null;         
            this.store.removeItem(this.localStorageKey);         
            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
        });                
    }

    public reboot(): Promise<StorageResponse<boolean>> { 
        //this.logService.addItem(new LogInfo('event list reboot start', 'EventListService', 'reboot', LogTypes.info)); 
        return new Promise((resolve, reject) => {   
            //this.logService.addItem(new LogInfo('event list remove start', 'EventListService', 'reboot', LogTypes.info));  
            this.removeList().then(() => {
                //this.logService.addItem(new LogInfo('event list remove end', 'EventListService', 'reboot', LogTypes.info));                 
                let deviceId = this.deviceService.getDeviceId(); 
                //this.logService.addItem(new LogInfo('get home page model start', 'EventListService', 'reboot', LogTypes.info)); 
                this.homeService.getItem().then((response) => {   

                    if(response.isSuccessWithResult())
                    {  
                        //this.logService.addItem(new LogInfo('get home page model end', 'EventListService', 'reboot', LogTypes.info)); 
                        let fireTruckData = <fireTruckInfo>response.data.fireTruckInfo;     
                        //this.logService.addItem(new LogInfo('event list get list start', 'EventListService', 'reboot', LogTypes.info));                  
                        this.getList(new EventListRequest(deviceId, fireTruckData.carInfo.positionStation.unionId)).then(() => {      
                            //this.logService.addItem(new LogInfo('event list get list end', 'EventListService', 'reboot', LogTypes.info));                       
                            resolve(new StorageResponse<boolean>().CreateSuccessResponse());
                        });   
                    }
                });        
            });              
        });                                       
    }    
}


