import {Injectable} from "@angular/core";
import {Observable} from "rxjs";
import * as moment from 'moment/moment';

@Injectable()
export class DateTimerService {

  private timer: Observable<string>;

  // value : date as string
  // unit : (second, minute, hour, day, month, year) units to count

  constructor(value: string, unit: DatePartUnitTypes = DatePartUnitTypes.Minute) {
    moment.locale('he');

    switch (unit){
      case DatePartUnitTypes.Day:
        this.timer = Observable.interval(1000).map(tick => moment(value).startOf('day').fromNow()).share();
      break;
      case DatePartUnitTypes.Hour:
        this.timer = Observable.interval(1000).map(tick => moment(value).startOf('hour').fromNow()).share();
      break;
      case DatePartUnitTypes.Month:
        this.timer = Observable.interval(1000).map(tick => moment(value).startOf('month').fromNow()).share();
      break;
      case DatePartUnitTypes.Second:
        this.timer = Observable.interval(1000).map(tick => moment(value).startOf('second').fromNow()).share();
      break;
      case DatePartUnitTypes.Year:
        this.timer = Observable.interval(1000).map(tick => moment(value).startOf('year').fromNow()).share();
      break;
      case DatePartUnitTypes.Minute:
      default:
        this.timer = Observable.interval(1000).map(tick => moment(value).startOf('minute').fromNow()).share();
      break;
    }
  }

  getBeforeHours(): Observable<string> {
    return this.timer;
  } 
}

export enum DatePartUnitTypes{
  Second = 0, 
  Minute = 1,
  Hour = 2,
  Day = 3,
  Month = 4,
  Year = 5
}


