import { EventEmitter, Injectable, OnInit } from '@angular/core';
import { Dictionary } from '../../shared-classes/IDictionary';
import { StorageResponse } from '../base/StorageResponse';
import { HttpService } from '../../core/http.service';
import { OfflineStorage } from '../../stores/offlineStorage/OfflineStorage';
import { ConnectionService } from '../connection/connection.service';
import { DeviceService } from '../general/device.service';
import { ChatMessage } from '../../shared-classes/common/chat-message';
import { NodeService } from '../node/node.service';
import { AppConfig } from '../../app.config';
import { HomeService } from '../home/home.service';
import * as moment from 'moment/moment';

@Injectable()
export class ChatService {
    private storeArray: Array<ChatMessage>;
    private namespace = this.config.getConfig('node_host') + this.config.getConfig('chat_namespace');   
    private chatServerBroadcastEmitter: any;
    private chatHistoryEmitter: any;
    private carName: string;
    public $chatBroadcastEmitter: EventEmitter<any>;

    constructor(
        private httpService: HttpService,
        private store: OfflineStorage,
        private connectionService: ConnectionService,
        private deviceService: DeviceService,
        private nodeService: NodeService,
        private config: AppConfig,
        private homeService: HomeService
    ) {
        this.homeService.getCar().then((response) => {
            this.carName = response.data.carCallCode;          
            this.nodeService.createSocket(this.namespace);      
        });

        this.$chatBroadcastEmitter = new EventEmitter();
    }

    public connectChatRoom(eventId: number): Promise<StorageResponse<Array<ChatMessage>>>{
        return new Promise((resolve, reject) => {  
            this.nodeService.registerChatEvent(this.namespace, eventId.toString());

            this.chatHistoryEmitter = this.nodeService.$chatHistoryEmitter.subscribe((messages) => {
                this.storeArray = new Array<ChatMessage>();
                if(messages == null || messages.length == 0){
                    resolve(new StorageResponse<Array<ChatMessage>>().CreateResultResponse(
                        this.storeArray
                    ));
                    return;
                }

                for(let i = 0; i < messages.length; i++){                    
                    let item = new ChatMessage(messages[i].dateSent.replace('Z', ''), messages[i].data, messages[i].sender, messages[i].sender == this.carName);
                    this.storeArray.push(item);
                }

                resolve(new StorageResponse<Array<ChatMessage>>().CreateResultResponse(
                    this.storeArray
                ));
            });
    
            this.chatServerBroadcastEmitter = this.nodeService.$chatServerBroadcastEmitter.subscribe((message) => {                
                let item = new ChatMessage(message.expire, message.data, message.sender, false);
                this.storeArray.push(item);

                this.$chatBroadcastEmitter.emit(true);
            });           
        });
    }

    public disConnectChatRoom(eventId: number): Promise<StorageResponse<boolean>>{
        return new Promise((resolve, reject) => { 
            this.nodeService.unRegisterChatEvent(this.namespace, eventId.toString());
            this.chatServerBroadcastEmitter.unsubscribe();
            this.chatHistoryEmitter.unsubscribe();

            resolve(new StorageResponse<boolean>().CreateResultResponse(
                true
            ));
        });
    }

    public sendMessage(message: string, eventId: number){
        this.nodeService.sendChatMessage(this.namespace, message, eventId.toString());
        let item = new ChatMessage(moment().format("YYYY/MM/DD HH:mm"), message, this.carName, true);
        this.storeArray.push(item);
    }
}


