import { Component, EventEmitter, Output, OnChanges } from '@angular/core';
import { DropDownData } from '../../shared-classes/common/drop-down-data';

@Component({
  selector: 'designed-select',
  templateUrl: './designed-select.component.html',
  inputs:['watermark', 'dataList', 'isDisabled', 'selectedValue']
})
export class DesignedSelectComponent {
  @Output() onValueChange: EventEmitter<DropDownData> = new EventEmitter();

  public watermark: string;
  public dataList: Array<DropDownData>;
  public selectedValue: DropDownData = null;
  public isDisabled: boolean = false;

  constructor() {}

  private onChange(val){        
    this.onValueChange.emit(this.selectedValue);
  }

  ngOnChanges(changes) {
    if(this.dataList && this.dataList.length > 0){
      if(this.watermark && !this.isValueExists(this.watermark)){
        this.dataList.unshift({ value: null, text: this.watermark });         
      }
  
      if(changes.isDisabled || !this.selectedValue){
        this.selectedValue = this.dataList[0];
        this.onValueChange.emit(this.selectedValue);
      }    
    }    
  }

  isValueExists(str){
    for(let index = 0; index < this.dataList.length; index++){
      if(this.dataList[index].text == str){
        return true;
      }
    }

    return false;
  }
}

 




