export class CarCheckRequest{
    carId: number;
    carCheckTypeId: number;
    checkerEmployeeId: number;
    carKm: string;

    constructor(carId: number, checkerEmployeeId: number, carKm: string = null){
        this.carId = carId;
        this.checkerEmployeeId = checkerEmployeeId;
        this.carKm = carKm;
        this.carCheckTypeId = 1;
     }      
}


