import { Component, OnInit, ViewChild, AfterViewChecked, ElementRef } from '@angular/core';
import { EventInfo } from '../../shared-classes/event/event-info';
import { NavigationService } from '../../services/navigation/navigation.service';
import { EventDetailsTypes } from '../../shared-classes/event/event-details-types';
import { EventDetailsData } from '../../shared-classes/event/event-details-data';
import { menuTypes } from '../../shared-classes/common/menu-types';
import { NavigationData } from '../../shared-classes/common/navigation-data';
import { ContentService } from '../../services/general/content.service';
import { EventDetailsService } from '../../services/event/event-details.service';
import { EventRequest } from '../../shared-classes/request/event-request';
import { EventGeneralInfo } from '../../shared-classes/event/event-general-info';
import { CurrentEventDetailsService } from '../../services/event/current-event-details.service';
import { EventDetailsStatus } from '../../shared-classes/event/event-details-status';
import { DateHoursPipe } from '../../services/pipes/date-hours.pipe';
import { EmployeesTeamData } from '../../shared-classes/employees/employee-team-data';
import { MapStateService } from '../../services/mapState/map-state.service';
import { HomeService } from '../../services/home/home.service';
import { ChangeMissionStatusRequest } from '../../shared-classes/request/change-mission-status-request';
import * as moment from 'moment/moment';
import { EmapService } from '../../core/emap.service';
import { CustomMap } from '../../shared-classes/esri/custom-map';
import { StorageResponse } from '../../services/base/StorageResponse';
import { debug } from 'util';
import { NodeService } from '../../services/node/node.service';
import { detailsbarTypes } from '../../shared-classes/common/detailsbar-types';
import { SuppliesService } from '../../services/supplies/supplies.service';
import { SaveAutsRequest } from '../../shared-classes/request/save.auts.request';
import { DeviceService } from '../../services/general/device.service';
import { LaunchMissionStatusRequest } from '../../shared-classes/request/launch-mission-status-request';
import { ControlTimeRequest } from '../../shared-classes/request/control-time-request';
import { SuppliesRequest } from '../../shared-classes/request/supplies.request';
import { SuppliesModel } from '../../shared-classes/supplies/supplies-model';
import { CarTeam } from '../../shared-classes/car/car-team';
import { ActiveMission } from '../../shared-classes/common/active-mission';
import { SaveSuppliesRequest } from '../../shared-classes/request/save.supplies.request';
import { AlertifyTypes } from '../../shared-classes/alertify/alertify-types';
import { AlertifyService, PromtConfig } from '../../services/general/alertify.service';
import { ActiveCarCheckRequest } from '../../shared-classes/request/active-car-check.request';
import { CarCheckData } from '../../shared-classes/car/car-check-data';
import { EventListService } from '../../services/event-list/event-list.service';
import { fireTruckInfo } from '../../shared-classes/common/fire-truck-info';
import { EventListRequest } from '../../shared-classes/request/event-list-request';
import { EventControlStatusTypes } from '../../shared-classes/event/event-control-status-types';
import { ProjectionService } from '../../services/general/projection.service';
import { ChatService } from '../../services/chat/chat.service';
import { ChatMessage } from '../../shared-classes/common/chat-message';
import { RoutePath } from '../../shared-classes/common/route-path';
import { WazeService } from '../../services/general/waze.service';
import { GPSService } from '../../services/general/gps.service';
import { StatusCode } from '../../shared-classes/common/status-code';
import { KabautPoint } from '../../shared-classes/request/kabaut-point';
import { NewMissionStatusRequest } from '../../shared-classes/request/new-mission-status-request';
import { CancelMissionRequest } from '../../shared-classes/request/cancel-mission-request';
import { UpdateNewMissionStatusRequest } from '../../shared-classes/request/update-new-mission-status-request';
import { ConnectionService } from '../../services/connection/connection.service';
import { Status } from '../../shared-classes/common/status';
import { LayersService } from '../../services/layers/layers.service';
import { Layer } from '../../shared-classes/esri/Layer';
import { EventForceInfo } from '../../shared-classes/event/event-force-info';
import { LogService } from '../../services/general/log.service';
import { LogInfo } from '../../shared-classes/common/log-info';

@Component({
 selector:'event-details',
  templateUrl: './event-details.component.html'
})

export class EventDetailsComponent{
  @ViewChild('scrollMe') private myScrollContainer: ElementRef;

  private eventDetailsType: EventDetailsTypes;
  private eventId: number;
  private content: any;
  private currentEventStatus: EventDetailsStatus;
  private reportButtonText: string;
  private currentDispatchTime: string;
  private currentExitTime: string;
  private currentArrivalTime: string;
  private currentFinishTime: string;
  private currentBackOnStationTime: string;
  private eventInfo: EventInfo;
  private activeMissionUpdated: any;
  private eventUpdated: any;
  private currentEventUpdated: any;  
  private isFinishedEvent: boolean;
  private isBackOnStationEvent: boolean;
  private menuType: menuTypes = menuTypes.nomenu;
  private detailsbarType: detailsbarTypes = detailsbarTypes.default;
  private isAutsShow: boolean = false;
  private navigationData: any;
  private layersNavigationData: any;
  private isMkValid: boolean = true;
  private suppliesModel: SuppliesModel;
  private currentEventLocationUpdated: any;
  private eventLocationUpdated: any;  
  private currentEventRemoved: any; 
  private deviceBack: any;
  private chatBroadcast: any;
  private currentEventAddedEmitter: any;   
  private isChatHide: boolean = true;
  private message: string = null;
  private chatMessages: Array<ChatMessage>;
  private routes: RoutePath;
  private stateChangedEvent: any;
  private isOfflineShow: boolean;
  private layerList: Array<Layer>;
  private isLayersShow: boolean = false;
  private isChangeStatusInProgress: boolean = false;

  constructor(
    private navigationService: NavigationService,
    private contentService: ContentService,
    private eventService: EventDetailsService,
    private currentEventDetailsService: CurrentEventDetailsService,
    private mapStateService: MapStateService,
    private homeService: HomeService,
    private emapService: EmapService,
    private nodeService: NodeService,
    private suppliesService: SuppliesService,
    private deviceService: DeviceService,
    private alertifyService: AlertifyService,
    private eventListService: EventListService,
    private projectionService: ProjectionService,
    private chatService: ChatService,
    private wazeService: WazeService,
    private gpsService: GPSService,
    private connectionService: ConnectionService,
    private layersService: LayersService,
    private logService:LogService
  ) {   
    this.mapStateService.changeMapState(true);
    this.contentService.load().then(()=>{
      this.content = this.contentService.getContent('event-details');
      this.navigationData = this.getAutsNavigationData();
      this.layersNavigationData = this.getLayersNavigationData();      
    });  
    this.suppliesModel = new SuppliesModel();
    this.routes = new RoutePath();
    this.layerList = new Array<Layer>();

    this.chatBroadcast = this.chatService.$chatBroadcastEmitter.subscribe(() => {
      setTimeout(() => {
        this.scrollToBottom();            
      }, 0);
    });

    this.activeMissionUpdated = this.currentEventDetailsService.$currentActiveMissionUpdatedEmitter.subscribe((mission: ActiveMission) => {                
      //this.logService.addItem(new LogInfo("activeMissionUpdated", "details", 'server'));
      this.isChangeStatusInProgress = false;      
      this.setActiveMissionStatuses(mission); 

      if(mission.currentMissionStatus.missionStatusId == EventDetailsStatus.Finish && !this.isOfflineShow){        
        let data = new NavigationData(this.routes.SUPPLIES, { eventId: this.eventId, eventDetailsType: this.eventDetailsType, onSaveCallback: () => {
            this.currentEventDetailsService.getCurrentCarTeam().then((response) => {

              if(response.isSuccessWithResult() && response.data.carInfo && response.data.carInfo.positionStation && response.data.carInfo.positionStation.coord && response.data.carInfo.positionStation.coord.lat && response.data.carInfo.positionStation.coord.lon){
                this.wazeService.navigate(response.data.carInfo.positionStation.coord.lat, response.data.carInfo.positionStation.coord.lon);
              }
              else{
                this.alertifyService.warring(this.content.noCoordsFound);
              }
                
              this.navigationService.goBack();
            });
          }, onCloseCallback: () => {
            this.currentEventDetailsService.getCurrentCarTeam().then((response) => {
              if(response.isSuccessWithResult() && response.data.carInfo && response.data.carInfo.positionStation && response.data.carInfo.positionStation.coord && response.data.carInfo.positionStation.coord.lat && response.data.carInfo.positionStation.coord.lon){
                this.wazeService.navigate(response.data.carInfo.positionStation.coord.lat, response.data.carInfo.positionStation.coord.lon);
              }
              else{
                this.alertifyService.warring(this.content.noCoordsFound);
              } 
            });
          } 
        });

        this.navigationService.navigate(data);
      } 
      else if(mission.currentMissionStatus.missionStatusId == EventDetailsStatus.BackOnStation && !this.isOfflineShow){
        this.currentEventDetailsService.getCurrentCarTeam().then((res) => {      
          if(res.isSuccessWithResult())
          {                    
            this.suppliesService.getSupplies(new SuppliesRequest(res.data.carInfo.carId, res.data.activeMission.missionId)).then((supResponse) => {
              if(supResponse.isSuccessWithResult()){
                this.suppliesModel = supResponse.data;
                this.isAutsShow = true;
              }
            });
          }
        });             
      } 
    });

    this.currentEventAddedEmitter = this.currentEventDetailsService.$currentEventAddedEmitter.subscribe((eventInfo) => {            
      this.getCurrentEvent(); 
    });

    this.currentEventUpdated = this.currentEventDetailsService.$currentEventUpdatedEmitter.subscribe((eventInfo) => {
      //this.logService.addItem(new LogInfo("currentEventUpdated", "details", 'server'));
      this.isChangeStatusInProgress = false;
      this.getCurrentEvent(false);
    });

    this.currentEventLocationUpdated = this.currentEventDetailsService.$currentEventLocationUpdatedEmitter.subscribe((eventInfo) => {
      this.getCurrentEvent(true, true);
    });

    this.eventUpdated = this.eventService.$eventUpdatedEmitter.subscribe((eventInfo: EventInfo) => {
      //this.logService.addItem(new LogInfo("eventUpdated", "details", 'server'));
      this.getEvent(eventInfo.generalInfo.eventId, false);
    });

    this.eventLocationUpdated = this.eventService.$eventLocationUpdatedEmitter.subscribe((eventInfo: EventInfo) => {
      this.getEvent(eventInfo.generalInfo.eventId);
    });

    this.currentEventRemoved = this.currentEventDetailsService.$currentEventRemovedEmitter.subscribe((eventId: number) => {
      if(this.eventInfo && this.eventInfo.generalInfo && eventId == this.eventInfo.generalInfo.eventId){
        this.navigationService.navigate(new NavigationData(this.routes.HOME));  
      }
    });

    this.stateChangedEvent = this.connectionService.stateChanged.subscribe((result) => {    
      this.isOfflineShow = this.connectionService.isOffline();
    });

    this.deviceBack = this.deviceService.$deviceBackEmitter.subscribe((e) => { 
      if(this.isAutsShow && !this.isAutsValid()){
        e.preventDefault();
      } 
      else{
        this.navigationService.goBack();
      }     
    });
  }

  ngOnInit() {       
    this.layersService.setDefaultLayersVisibility('myMap');

    this.isOfflineShow = this.connectionService.isOffline();     
    let data = <EventDetailsData>this.navigationService.getNavigationData();
    this.getReportButtonText(); 
    
    if(data == null){
      this.eventInfo = new EventInfo();
      this.eventDetailsType = EventDetailsTypes.CreateEvent;
      this.eventId = -1;      
    }
    else{
      this.eventId = data.eventId;
      this.eventDetailsType = data.eventDetailsType;

      if(!this.eventDetailsType){
        if(this.currentEventDetailsService.isCurrentEventById(this.eventId)){
          this.eventDetailsType = EventDetailsTypes.CurrentEvent;
        }
        else{
          this.eventDetailsType = EventDetailsTypes.CarRequest;
        }
      }

      if(this.eventDetailsType == EventDetailsTypes.CurrentEvent){
        this.getCurrentEvent(true, data.isNew); 
      }
      else{
        this.getEvent(this.eventId); 
      }      
    }
  }

  scrollToBottom(): void {
    try {
        this.myScrollContainer.nativeElement.scrollTop = this.myScrollContainer.nativeElement.scrollHeight;
    } catch(err) { }                 
  }

  ngOnDestroy(){
    this.layersService.setVisibleLayers('myMap', []);
    this.activeMissionUpdated.unsubscribe();
    this.eventUpdated.unsubscribe();    
    this.eventLocationUpdated.unsubscribe();    
    this.currentEventUpdated.unsubscribe();
    this.currentEventLocationUpdated.unsubscribe();
    this.currentEventRemoved.unsubscribe();  
    this.currentEventAddedEmitter.unsubscribe();   
    this.chatBroadcast.unsubscribe();       
    this.deviceBack.unsubscribe(); 
    this.stateChangedEvent.unsubscribe();           
  }

  private zoomToLatLon(lat: number, lon: number) {     
    document.getElementById('myMap').style.height = "662px";
    document.getElementById('myMap').style.width = "1443px";

    this.emapService.getMap('myMap').then((map: CustomMap) => {  
      
      setTimeout(()=>{
        map.resize(true);                   

        setTimeout(()=>{
          let coords = this.projectionService.transformWGS1984ToITM(lat,lon);
          map.setCenter({ x: coords.x, y: coords.y, level: map.getNumberOfLevels() - 1 });
          map.setMapMarker(coords.x, coords.y);                   
        }, 0);        
      }, 0);         
    });
  }

  private getEvent(eventId, isZoom: boolean = true): void{
    this.eventService.getItem(new EventRequest(eventId)).then((response) => {      
      if(response.isSuccessWithResult())
      {         
        this.eventInfo = response.data;   
        this.eventDetailsType = EventDetailsTypes.CarRequest;
        this.currentEventStatus = null;
        this.currentDispatchTime = null;
        this.currentExitTime = null;
        this.currentArrivalTime = null;
        this.currentFinishTime = null;
        this.currentBackOnStationTime = null;

        if(isZoom && this.eventInfo.generalInfo.lat && this.eventInfo.generalInfo.lon){
          this.zoomToLatLon(this.eventInfo.generalInfo.lat, this.eventInfo.generalInfo.lon);                                          
        }       
      }
    });  
  }

  private getCurrentEvent(isZoom: boolean = true, isLaunchWaze: boolean = false): void{
    this.currentEventDetailsService.getItem().then((response) => {      
      if(response.isSuccessWithResult())
      {         
          this.eventInfo = response.data;   
          this.eventId = this.eventInfo.generalInfo.eventId;
          this.eventDetailsType = EventDetailsTypes.CurrentEvent;

          if(isZoom && this.eventInfo.generalInfo.lat && this.eventInfo.generalInfo.lon){
            this.zoomToLatLon(this.eventInfo.generalInfo.lat, this.eventInfo.generalInfo.lon);                          
          }

          if(isLaunchWaze && this.eventInfo.generalInfo.lat &&  this.eventInfo.generalInfo.lon){
            this.wazeService.navigate(this.eventInfo.generalInfo.lat, this.eventInfo.generalInfo.lon);
          }
      }
    });
    
    this.currentEventDetailsService.getCurrentCarTeam().then((res) => {      
      if(res.isSuccessWithResult())
      {         
        this.setActiveMissionStatuses(res.data.activeMission);        
      }
    });       
  }

  private setActiveMissionStatuses(mission){      
      let filterPipe = new DateHoursPipe();
      this.currentEventStatus = <EventDetailsStatus>mission.currentMissionStatus.missionStatusId;
      this.currentDispatchTime = mission.statusDispatch ? filterPipe.transform(mission.statusDispatch.missionStatusTime) : null;
      this.currentExitTime = mission.statusExit ? filterPipe.transform(mission.statusExit.missionStatusTime) : null;
      this.currentArrivalTime = mission.statusArrival ? filterPipe.transform(mission.statusArrival.missionStatusTime) : null;
      this.currentFinishTime = mission.statusFinish ? filterPipe.transform(mission.statusFinish.missionStatusTime) : null;
      this.currentBackOnStationTime = mission.statusBackOnStation ? filterPipe.transform(mission.statusBackOnStation.missionStatusTime) : null;

      this.getReportButtonText();
  }

  private getReportButtonText(): void{    
    let stageText = this.content.exit;

    if(this.eventDetailsType == EventDetailsTypes.CarRequest || (this.eventDetailsType == EventDetailsTypes.CurrentEvent && this.currentEventStatus == EventDetailsStatus.Dispatch)){
      stageText = this.content.exit;
    }
    else if(this.eventDetailsType == EventDetailsTypes.CurrentEvent && this.currentEventStatus == EventDetailsStatus.Exit){
      stageText = this.content.arrival;
    }
    else if(this.eventDetailsType == EventDetailsTypes.CurrentEvent && this.currentEventStatus == EventDetailsStatus.Arraival){
      stageText = this.content.finish;
    }
    else if(this.eventDetailsType == EventDetailsTypes.CurrentEvent && this.currentEventStatus == EventDetailsStatus.Finish
    ){
      stageText = this.content.returnToStation;
    }

    this.reportButtonText = stageText;
  }

  private home(){
    this.navigationService.navigate(new NavigationData(this.routes.HOME));
  }

  private currentStatus(){
    this.navigationService.navigate(new NavigationData(this.routes.CURRENT_STATUS, { eventId: this.eventId, eventDetailsType: this.eventDetailsType }));
  }

  private preventionFiles(){
    this.navigationService.navigate(new NavigationData(this.routes.PREVENT_FIRE, { eventId: this.eventId, eventDetailsType: this.eventDetailsType }));
  }

  private employeeTeam(isOnCloseCallback: boolean = false, carId, eventId){
    this.currentEventDetailsService.getItem().then((res)=>{
      let data = null;
      if(this.eventInfo.generalInfo != null){
        data = new EmployeesTeamData(this.eventInfo.generalInfo.eventNumber, this.eventInfo.generalInfo.eventId);  
  
        if(isOnCloseCallback){
          data.onCloseCallback = ()=> {
            this.navigationService.goBack();
            this.sendLaunchMission(carId, eventId);
          }  
        }
      }
      data.isBack = !res.isSuccessWithResult();
      this.mapStateService.changeMapState(false);
      this.navigationService.navigate(new NavigationData(this.routes.EMPLOYEES_TEAM, data)); 
    }); 
  }

  private activeEvents(){
    let data = null;
    if(this.eventInfo && this.eventInfo.generalInfo){
      data = new EventDetailsData(this.eventInfo.generalInfo.eventId, this.eventDetailsType);    
    }
    
    this.mapStateService.changeMapState(false);
    this.navigationService.navigate(new NavigationData(this.routes.EVENTS, data));
  }

  private carStatus(){
    this.homeService.getCar().then((homeResponse) => {
      this.navigationService.navigate(new NavigationData(this.routes.CAR_STATUS, { eventId: this.eventId, eventDetailsType: this.eventDetailsType, carName: homeResponse.data.carCallCode }));
    });    
  }

  private updateControlTime(){
    this.homeService.getCar().then((response) => {
      let request = new ControlTimeRequest(response.data.carId, this.eventId, moment().format("YYYY/MM/DD HH:mm"), EventControlStatusTypes.InControl);
      this.currentEventDetailsService.updateControlTime(request);
    });

    if(this.isOfflineShow){
      this.currentEventDetailsService.getItem().then((response) => {
        response.data.generalInfo.controlTime = moment().format("YYYY/MM/DD HH:mm");
        this.currentEventDetailsService.updateItem(response.data);
      });
    }
  }

  private tools(){    
    let data = new NavigationData(this.routes.SUPPLIES, { eventId: this.eventId, eventDetailsType: this.eventDetailsType, onSaveCallback: () => {      
        this.navigationService.goBack();
      } 
   });

    this.navigationService.navigate(data);
  }

  private additionalDetails(){
    let data = null;
    if(this.eventInfo.generalInfo != null){
      data = new EventDetailsData(this.eventInfo.generalInfo.eventId, this.eventDetailsType);    
    }
    
    this.mapStateService.changeMapState(false);
    this.navigationService.navigate(new NavigationData(this.routes.REPORT, data));
  }

  private anotherEvent(){
    let pThis = this
    let config = new PromtConfig(
      this.content.confirmTitleChangeEvent, 
      this.content.confirmTextChangeEvent,
      function(){ 
        pThis.cancelMission(true);
       }, 
      function(clicked){         
      },
      'event-details-promt'
    );

    this.alertifyService.promt(config);
  }

  private chat(){    
    this.chatMessages = new Array<ChatMessage>();
    this.chatService.connectChatRoom(this.eventInfo.generalInfo.eventId).then((response) => {
      if(response.isSuccessWithResult()){
        this.chatMessages = response.data;

        setTimeout(() => {
          this.scrollToBottom();            
        }, 0);
      }

      this.isChatHide = false;
    });
  }

  private closeChat(){
    this.chatService.disConnectChatRoom(this.eventInfo.generalInfo.eventId).then((response) => {
      this.chatMessages = new Array<ChatMessage>();
      this.message = null;
      this.isChatHide = true;
    });
  }

  private sendMessage(){
    if(this.message == null || this.message.trim().length == 0){
      return;
    }
    
    this.chatService.sendMessage(this.message, this.eventInfo.generalInfo.eventId);
    this.message = '';

    setTimeout(() => {
      this.scrollToBottom();            
    }, 0);
  }

  private cancelEvent(){
    let pThis = this;
    let config = new PromtConfig(
      this.content.confirmTitleCancelEvent, 
      this.content.confirmTextCancelEvent,
      function(){ 
        pThis.cancelMission();
       }, 
      function(){
          console.log("cancel") 
      },
      'event-details-promt'
    );

    this.alertifyService.promt(config);
  }

  private cancelMission(isAnotherEvent: boolean = false){
    this.currentEventDetailsService.getCurrentCarTeam().then((response) => {
      if(response.isSuccessWithResult()){
        let requst = new CancelMissionRequest(response.data.activeMission.missionId, response.data.carInfo.carId);
        this.currentEventDetailsService.cancelMission(requst).then((res) =>{
          if(isAnotherEvent){    
            this.navigateToAnotherEvent(response.data.carInfo.positionStation.unionId);
          }
          else{
            this.navigationService.navigate(new NavigationData(''));
          }
        });          
      }
    });
  }

  private navigateToAnotherEvent(unionId: number){
    let deviceId = this.deviceService.getDeviceId();                                    
    this.eventListService.getList(new EventListRequest(deviceId, unionId)).then((res) => {
      if(res.isSuccessWithResult())
      {
          if(res.data.length == 0){
            this.navigationService.navigate(new NavigationData(''));
          }
          else if(res.data.length == 1){
            let eventType = EventDetailsTypes.CarRequest;
            if(this.currentEventDetailsService.isCurrentEventById(res.data[0].eventId))
            {
              eventType = EventDetailsTypes.CurrentEvent;
            }
        
            let data = new EventDetailsData(res.data[0].eventId, eventType);
            this.navigationService.navigate(new NavigationData(this.routes.EVENT_DETAILS, data));   
          }
          else{
            this.navigationService.navigate(new NavigationData(this.routes.EVENTS));
          }
      }
    });
  }

  private report(){
    let data = null;
    if(this.eventInfo.generalInfo != null){
      data = new EventDetailsData(this.eventInfo.generalInfo.eventId, this.eventDetailsType);    
    }
    
    this.mapStateService.changeMapState(false);
    this.navigationService.navigate(new NavigationData(this.routes.QUICK_REPORT, data));
  }

  private sendNewMission(){
    this.homeService.getCar().then((homeResponse) => {
      let request = new NewMissionStatusRequest(homeResponse.data.carId);  
      request.exitTime = moment().format("YYYY/MM/DD HH:mm");
      this.currentEventDetailsService.addNewMission(request).then((response) => {
        this.isChangeStatusInProgress = false;
      }); 
    });
  }

  private sendLaunchMission(carId, eventId){
    this.suppliesModel = new SuppliesModel();
    let request = new LaunchMissionStatusRequest(carId, eventId);
    request.launchTime = moment().format("YYYY/MM/DD HH:mm");          
    this.currentEventDetailsService.launchMission(request).then((response) => {
      this.isChangeStatusInProgress = false;

      if(this.eventInfo && this.eventInfo.generalInfo)
      {
        this.wazeService.navigate(this.eventInfo.generalInfo.lat, this.eventInfo.generalInfo.lon);
      }      
    }); 
  }

  private sendChangeMission(carId, eventId, nextState){
    let request = new ChangeMissionStatusRequest();
    request.carId = carId;
    request.eventId = eventId; 
    request.missionStatusId = nextState;            
    request.statusTime = moment().format("YYYY/MM/DD HH:mm");

    this.gpsService.getPosition().then((gpsResponse) => {      
      if(gpsResponse.status == StatusCode.SUCCESS){
        request.coord = new KabautPoint();
        request.coord.lat = gpsResponse.success.lat;
        request.coord.lon = gpsResponse.success.lon;
        request.coord.x_itm = gpsResponse.success.x;
        request.coord.y_itm = gpsResponse.success.y;
        request.coord.locationTime = moment().format("YYYY/MM/DD HH:mm")
      }
      this.currentEventDetailsService.changeMissionStatus(request).then((res) => {
        this.isChangeStatusInProgress = false;               
      });       
    },
    (e) =>{
      this.currentEventDetailsService.changeMissionStatus(request).then((res) => {
        this.isChangeStatusInProgress = false;               
      });       
    });
  }

  private sendChangeVirtualMission(carId, nextState){
    let request = new UpdateNewMissionStatusRequest(carId, nextState);          
    request.statusTime = moment().format("YYYY/MM/DD HH:mm");
    this.currentEventDetailsService.updateNewMissionStatus(request);             
  }

  private changeMissionStatus(){

    if(this.isChangeStatusInProgress){
      return;
    }

    if(!this.isOfflineShow){
      this.isChangeStatusInProgress = true;
   }

    this.homeService.getCar().then((homeResponse) => {
      let carId = homeResponse.data.carId;

      this.currentEventDetailsService.getCurrentCarTeam().then((res) => { 
        if(!res.isSuccessWithResult()){ 
          if(this.eventId == -1){
            this.sendNewMission();
          }
          else{
            this.launchMissionTeamEmployees(carId, this.eventId);
            this.setOfflineLaunch();
          }   
        }
        else{
          let currentStep = <EventDetailsStatus>res.data.activeMission.currentMissionStatus.missionStatusId;
          let nextState = this.getNextState(currentStep);          
  
          if(res.data.activeMission.eventId != this.eventId){
            this.manualChangeEvent(carId, res.data.activeMission.missionId, this.eventId);
          }
          else if(res.data.activeMission.eventId == -1){
            if(currentStep == EventDetailsStatus.BackOnStation){
              this.sendNewMission();
              this.setOfflineCurrentStep(res.data, EventDetailsStatus.Exit);
            }
            else{
              this.sendChangeVirtualMission(carId, nextState);
              this.setOfflineCurrentStep(res.data, nextState);
            }   
          }
          else{
            if(currentStep == EventDetailsStatus.BackOnStation){
              this.launchMissionTeamEmployees(carId, res.data.activeMission.eventId);
              this.setOfflineCurrentStep(res.data, EventDetailsStatus.Exit);
            }
            else{
              this.sendChangeMission(carId, res.data.activeMission.eventId, nextState);
              this.setOfflineCurrentStep(res.data, nextState);
            }
          }
        }
      });
    });                
  }

  private setOfflineCurrentStep(carTeam: CarTeam, nextState: EventDetailsStatus){
    if(!this.isOfflineShow){
      return;
    }

    let stepTime = moment().format("YYYY/MM/DD HH:mm");
    let filterPipe = new DateHoursPipe();
    let status = new Status();
    status.missionStatusId = nextState;
    status.missionStatusTime = stepTime;
    
    carTeam.activeMission.currentMissionStatus.missionStatusId = nextState;

    switch(carTeam.activeMission.currentMissionStatus.missionStatusId){
      case EventDetailsStatus.Dispatch:
      case EventDetailsStatus.Exit:
        carTeam.activeMission.statusDispatch = status;
        carTeam.activeMission.statusExit = status;
        carTeam.activeMission.statusArrival = null;
        carTeam.activeMission.statusFinish = null;
        carTeam.activeMission.statusBackOnStation = null;
      break;
      case EventDetailsStatus.Arraival: 
        carTeam.activeMission.statusArrival = status;    
      break;
      case EventDetailsStatus.Finish:
        carTeam.activeMission.statusFinish = status;       
      break;
      case EventDetailsStatus.BackOnStation:      
        carTeam.activeMission.statusBackOnStation = status;    
        this.isAutsShow = true;
      break;
    }

    this.currentEventDetailsService.updateAcitveMission(carTeam);
  }

  private setOfflineLaunch(){
    if(!this.isOfflineShow){
      return;
    }

    let stepTime = moment().format("YYYY/MM/DD HH:mm");
    let filterPipe = new DateHoursPipe();
    let status = new Status();
    status.missionStatusId = EventDetailsStatus.Exit;
    status.missionStatusTime = stepTime;
    
    this.homeService.getCar().then((response) => {      
        let carTeam = new CarTeam();
        carTeam.carInfo = response.data;
        carTeam.activeMission = new ActiveMission();
        carTeam.activeMission.eventId = this.eventInfo.generalInfo.eventId;
        carTeam.activeMission.currentMissionStatus = new Status();
        carTeam.activeMission.currentMissionStatus.missionStatusId = EventDetailsStatus.Exit;
        carTeam.activeMission.statusDispatch = status;
        carTeam.activeMission.statusExit = status;
        carTeam.activeMission.statusArrival = null;
        carTeam.activeMission.statusFinish = null;
        carTeam.activeMission.statusBackOnStation = null;
        this.eventInfo.forcesInfo = new EventForceInfo();
        this.eventInfo.forcesInfo.activeTeams = new Array<CarTeam>();
        this.eventInfo.forcesInfo.activeTeams.push(carTeam);                                        
        this.currentEventDetailsService.addItem(this.eventInfo, false);
        
        this.eventDetailsType = EventDetailsTypes.CurrentEvent;
        this.setOfflineCurrentStep(carTeam, EventDetailsStatus.Exit);
    });
  }

  private manualChangeEvent(carId, currentMissionId, newEventId){    
    let pThis = this;

    let config = new PromtConfig(
      this.content.confirmTitleCancelEvent, 
      this.content.confirmTextCancelEvent,
      function(){ 
        let requst = new CancelMissionRequest(currentMissionId, carId);
        pThis.currentEventDetailsService.cancelMission(requst).then((res) =>{
          if(newEventId != -1){
            pThis.launchMissionTeamEmployees(carId, newEventId);
          }          
          else{
            pThis.sendNewMission();
          }
        });  
       }, 
      function(){
          pThis.isChangeStatusInProgress = false;
      },
      'event-details-promt'
    );

    this.alertifyService.promt(config);
  }

  private launchMissionTeamEmployees(carId, eventId){
    this.homeService.getTeamEmployees().then((response) => {      
      if(response.isSuccessWithResult() && response.data.length > 0){
        this.confirmEmployees(response.data,carId, eventId);
      }
      else{
        setTimeout(()=>{ 
          this.employeeTeam(true, carId, eventId) 
        }, 1000);
      }
     });
  }

  private confirmEmployees(employees, carId, eventId){
    let pThis = this;

    let config = new PromtConfig(
      this.content.emloyeesConfirm, 
      this.alertifyService.createEmloyeesConfirm(employees),
      function(){           
        pThis.employeeTeam(true, carId, eventId);      
      },
      function(){   
        pThis.sendLaunchMission(carId, eventId);                    
       },       
      'event-details-promt'
    );

    config.cancelText = this.content.okText;
    config.okText = this.content.cancelText;

    this.alertifyService.promt(config);
  }

  private getNextState(status: EventDetailsStatus): EventDetailsStatus{
    switch (status)
    {
        case EventDetailsStatus.Dispatch:
          return EventDetailsStatus.Exit;
        case EventDetailsStatus.Exit:
          return EventDetailsStatus.Arraival;
        case EventDetailsStatus.Arraival:
          return EventDetailsStatus.Finish;
        case EventDetailsStatus.Finish:
          return EventDetailsStatus.BackOnStation;
        default:
        return EventDetailsStatus.Dispatch;
    }
  }

  private getAutsNavigationData(){           
    return {         
        buttonLeft: {
            callback: () => { 
                this.saveAuts()               
            }, 
            class: "image-sprite tab-icn-ok"
        },
        title: this.content.autsTitle
    };
  } 
  
  private getLayersNavigationData(){           
    return {         
        buttonRight: {
            callback: () => {
                this.isLayersShow = false;
            },
            class: "image-sprite tab-icn-x"
        },      
        title: this.content.layersTitle
    };
  } 

  private openLayers(){
    if(this.isLayersShow){
      return;
    }

    this.isLayersShow = true;
    this.emapService.getMap('myMap').then((map:CustomMap) =>{      
      this.layerList = map.getMapLayers();
    });
  }

  private layerClicked(layer: Layer){
    if(!layer || layer.layerName.trim() == ''){
      return;
    }

    this.emapService.getMap('myMap').then((map: CustomMap) => {            
      map.setVisibleLayer([layer]);
    });
  }

  private saveAuts(){        
    if(this.isAutsValid()){
      this.currentEventDetailsService.getCurrentCarTeam().then((response) => {
        let request = new SaveSuppliesRequest();
        request.carId = response.data.carInfo.carId;
        request.MissionEquipmentData = this.suppliesModel;     
        request.MissionEquipmentData.missionId = response.data.activeMission.missionId;          
        this.suppliesService.saveAuts(request).then((res) => {
          if(res.isSuccess()){
            this.isAutsShow = false;   
            this.activeEvents();   
          }
        });            
      });
    }   
  }

  private isAutsValid(){    
    this.isMkValid = this.suppliesModel.carKM && this.suppliesModel.carKM.toString().trim() != '' && /^[+]?([0-9]+([.][0-9]*)?|[.][0-9]+)$/.test(this.suppliesModel.carKM.toString().trim());
    return this.isMkValid;
  }
}



