import { Component, OnInit } from '@angular/core';
import { MapStateService } from '../../services/mapState/map-state.service';
import { ContentService } from '../../services/general/content.service';
import { HomeService } from '../../services/home/home.service';
import { NavigationService } from '../../services/navigation/navigation.service';
import { NavigationData } from '../../shared-classes/common/navigation-data';
import { CarCheckService } from '../../services/car/car-check-service';
import { CarCheckData } from '../../shared-classes/car/car-check-data';
import { StartCarCheckRequest } from '../../shared-classes/request/start-car-check.request';
import { DropDownData } from '../../shared-classes/common/drop-down-data';
import { RouteConfigLoadStart } from '@angular/router/src/events';
import { RoutePath } from '../../shared-classes/common/route-path';
import { BaseComponent } from '../../shared-classes/common/base-component';

@Component({
  templateUrl: './car-check-start.component.html'
})
export class CarCheckStartComponent extends BaseComponent {
  private isMkValid: boolean = true;
  private employees: Array<DropDownData>;
  private isEmployeeValid: boolean = true;
  private carKm: string = '';
  private employeeSelected = new DropDownData();
  private routes: RoutePath;

  constructor(
    mapStateService: MapStateService,
    contentService: ContentService,
    carCheckService: CarCheckService,
    private homeService: HomeService,
    private navigationService: NavigationService
  ){  
    super(contentService, 'car-check-start');

    mapStateService.changeMapState(false);
    this.routes = new RoutePath();

    homeService.getCar().then((response) => {
      let request = new StartCarCheckRequest(response.data.carId, response.data.positionStation.unionId);
      carCheckService.getEmployeesCarCheck(request).then((res) => {
        if(res.isSuccessWithResult()){          
          this.employees = res.data.options;
        }
      });
    });
  }

  private startCheck(){    
    if(this.isDataValid()){
      this.homeService.getCar().then((response) => {
        this.navigationService.navigate(new NavigationData(this.routes.CAR_CHECK, new CarCheckData('', response.data.carId, parseInt(this.employeeSelected.value), parseInt(this.carKm.trim()))));
      });
    }   
  }

  private isDataValid(){    
    this.isMkValid = this.carKm.trim() != '' && /^[0-9]*$/.test(this.carKm.trim());
    this.isEmployeeValid = this.employeeSelected != null && this.employeeSelected.value != undefined;
    
    return this.isMkValid && this.isEmployeeValid;
  }

  private valueChanged(value: DropDownData){        
    this.employeeSelected = value;
  }

}



