import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { employee } from '../../shared-classes/employees/employee';
import { EmployeeViewType } from '../../shared-classes/employees/employee-view-type';

@Component({
 selector:'employee',
  templateUrl: './employee.component.html',
  inputs: ['item', 'isLastElement', 'index', 'employeeType']
})

export class EmployeeComponent{
  @Output() onClick: EventEmitter<any> = new EventEmitter();

  item: employee;
  isLastElement: boolean;
  callback: Function;
  index: number;
  employeeType: EmployeeViewType

  constructor() {
    this.employeeType = EmployeeViewType.small;
  }

  employeeClicked(): void {    
    this.onClick.emit({ employee: this.item, index: this.index });
}


  ngOnInit() {   
    
  }

  ngOnDestroy(){

  }
}



