import { EventInjuredData } from "./event-injured-data";
import { EventType } from "./event-type";
import { EventRelatedBusiness } from "./event-related-business";
import { EventLocationData } from "./event-location-data";
import { EquipmentInfo } from "./equipment-info";
import { UsedEquipment } from "./used-equipment";

export class EventReportData{
    public eventReportId: number;
    public eventId: number;
    public unionId: number;
    public actualLocationTypeId: number;
    public actualLocationTypeDesc: string;
    public eventInjured: EventInjuredData;
    public actualEventLocation: EventLocationData;
    public actualEventType: EventType;
    public relatedBusiness: EventRelatedBusiness;
    public generalDesc: string;
    public reportRemarks: string;
    public burnedContent: string;
    public eventCloseReasonId: number | null;
    public areaDamage: number;
    public eventCommanderReportRemarks: string;
    public reportFireCauseDesc: string;
    public defectsDesc: string;
    public reportEquipments: Array<UsedEquipment>;

    constructor(){
        this.eventInjured = new EventInjuredData();
        this.actualEventLocation = new EventLocationData();
        this.actualEventType = new EventType();
        this.relatedBusiness = new EventRelatedBusiness();
        this.reportEquipments = new Array<UsedEquipment>();
    }
}


