import esriExtent = require("esri/geometry/Extent");
export class Extent extends esriExtent
{

    constructor(extent: Object);
    constructor(xmin: number, ymin: number, xmax: number, ymax: number);

    constructor(p1?: any, p2?: any, p3?: any, p4?: any)
    {    
        var dstExtent = {
            "spatialReference": { "wkid": 2039 }
        };

        if (typeof (p1) === "object")
        {
            dstExtent = Object.assign(dstExtent, p1);
        }
        else
            dstExtent = Object.assign(dstExtent, {
                xmin: p1,
                ymin: p2,
                xmax: p3,
                ymax: p4
            });

        console.log(JSON.stringify(dstExtent));
        super(dstExtent);
    }
}


