import { CarTeam } from "../car/car-team";

export class LaunchMissionStatusRequest {
    
    public carId: number;
    public eventId: number;
    public launchDateTime: Date;
    public launchTime: string;

    constructor(carId: number, eventId: number){
        this.eventId = eventId;
        this.carId = carId;
        this.launchDateTime = new Date();
    }      
}


