import * as moment from 'moment/moment';

export class CancelMissionRequest {
    public activeMissionId: number;
    public cancelDateTime: Date;
    public cancelTime: string;
    public carId: number;

    constructor(missionId: number, carId: number){
        this.activeMissionId = missionId;
        this.cancelTime = moment().format("YYYY/MM/DD HH:mm");
        this.cancelDateTime = new Date();
        this.carId = carId;
    }
}


