
import esri = require("esri");
import Graphic = require("esri/graphic");
import Geometry = require("esri/geometry/Geometry");
import esriGraphicsLayer = require("esri/layers/GraphicsLayer");
import { Sequence } from "../common/sequence";
import Symbol = require("esri/symbols/Symbol");
import { Observable } from "rxjs/Observable";
import { IGraphicEventData } from "./IGraphicEventData";

export class GraphicsLayer extends esriGraphicsLayer {
    private m_graphics: { [key: string]: Graphic } = {};
    private sequence: Sequence;
    private click: Observable<IGraphicEventData>;

    constructor(options?: esri.GraphicsLayerOptions) {
        super(options);
        this.sequence = new Sequence();

        this.click = new Observable(observer => {
            this.on("click", (eventArgs: any) => {
                try {
                    observer.next({
                        graphic: eventArgs.graphic || eventArgs.event.graphic,
                        mouseX: eventArgs.x,
                        mouseY: eventArgs.y
                    });
                }
                catch (e) {
                }
            });
        });
    }

    public count() {
        return this.graphics.length;
    }

    public clearGraphics() {
        this.clear();
        this.m_graphics = {};
        this.sequence.initialize();
    }

    public hasGraphic(att_name: string, att_value: any): boolean;
    public hasGraphic(id: number): boolean;
    public hasGraphic(p1: any, p2?: any): boolean {
        //check if graphic exists in the requested layer
        if (p1 === null || p1 === undefined)
            return false;

        if (typeof p1 === "number")
            return this.m_graphics.hasOwnProperty(p1);

        if (typeof p1 === "string" && p2 !== undefined && p2 !== null) {
            return (this.getGraphicByAttribute(p1, p2) !== null)
        }
    }

    public getGraphic(att_name: string, att_value: any): Graphic;
    public getGraphic(id: number): Graphic;
    public getGraphic(p1: any, p2?: any): Graphic {
        if (!(this.hasGraphic(p1, p2)))
            return null;

        if (typeof p1 === "number")
            return this.m_graphics[p1];

        if (typeof p1 === "string" && p2 !== undefined && p2 !== null) {
            return this.getGraphicByAttribute(p1, p2);
        }
    }

    private getGraphicByAttribute(att_name: string, att_value: any): Graphic {
        for (let id in this.graphics) {
            if ("name" in this.graphics[id].attributes && this.graphics[id].attributes[att_name] == att_value)
                return this.graphics[id];
        }

        return null;
    }

    public refresh() {
        this.redraw();
    }

    public removeGraphic(att_name: string, att_value: any);
    public removeGraphic(graphic: Graphic);
    public removeGraphic(id: number);
    public removeGraphic(p1: any, p2?: any): void {
        //removing specific graphic from requested layer
        if (p1 == undefined || p1 == null)
            return;

        let graphic;

        if (typeof p1 === "object" && p1 instanceof Graphic) {
            graphic = p1;
        }
        else {
            graphic = this.getGraphic(p1, p2);
        }

        delete this.m_graphics[graphic.attributes["id"]]
        this.remove(graphic);
    }


    public moveGraphic(id: number, geometry: Geometry): void {
        //change graphic geometry        
        if (!this.hasGraphic(id))
            return;

        this.getGraphic(id).setGeometry(geometry);
    }

    public addGraphic(geometry: Geometry, symbol: Symbol, attributes?: any): number {
        if (attributes === null || attributes === undefined) {
            attributes = {};
        }

        if (!attributes.hasOwnProperty('id')) {
            attributes['id'] = this.sequence.next();
        }

        let graphic = new Graphic(geometry, symbol, attributes);

        this.add(graphic);
        this.m_graphics[attributes['id']] = graphic;

        return attributes['id'];
    }

    public onClick(): Observable<IGraphicEventData> {
        return this.click;
    }
}


