import { Injectable, EventEmitter, NgZone } from '@angular/core';
import { ConnectionService } from '../connection/connection.service';
import { AppConfig } from '../../app.config';
import { DeviceInformation } from '../../shared-classes/common/deviceInformation';
import { ConnectionStateTypes } from '../../shared-classes/common/connection-state-types';
import { NodeService } from '../node/node.service';
import { RebootService } from './reboot.service';
import { NavigationService } from '../navigation/navigation.service';
import { RoutePath } from '../../shared-classes/common/route-path';
import { LogTypes } from '../../shared-classes/common/log-types';
import { LogInfo } from '../../shared-classes/common/log-info';
import { LogService } from './log.service';
import { KeepAliveMonitorService } from './keep-alive.service';
import { Observable } from 'rxjs';

@Injectable()
export class DeviceService {  
    
    private device_info: DeviceInformation;
    private cordovaApp: boolean;
    public $deviceBackEmitter: EventEmitter<any>; 
    private routes: RoutePath;

    constructor(        
        private connectionService: ConnectionService, 
        private config: AppConfig,
        private navigationService: NavigationService,
        private zone: NgZone,
        private keepService: KeepAliveMonitorService,
        private logService: LogService
    ) {
       
      this.keepService.networkCurrentStatus.subscribe(status=>{
        if (status){           
            connectionService.onChangeState(ConnectionStateTypes.online);
        }
     });

        this.device_info = <DeviceInformation>config.getConfig('default_device_information'); 
        let isDebugMode =   config.getConfig('is_debug_mode');     
        this.$deviceBackEmitter = new EventEmitter();        
        this.routes = new RoutePath();

        this.cordovaApp = document.URL.indexOf( 'http://' ) === -1 && document.URL.indexOf( 'https://' ) === -1;
        if (window.device && !isDebugMode) //if running under cordova with device plugin
        {
            this.device_info.uuid = window.device.uuid.toLowerCase();            
        }

        if (this.cordovaApp) {
            // Observable.fromEvent(document, 'resume').subscribe(event => {
            //     this.zone.run(() => {

            //     });
            //  });
             Observable.fromEvent(document, 'online').subscribe(event => {
               this.zone.run(() => {
                this.keepService.start('https://jsonplaceholder.typicode.com/albums/1',1000);
               });
            });
            Observable.fromEvent(document, 'offline').subscribe(event => {
               this.zone.run(() => {
                this.keepService.NetworkLastStatus = false;
                connectionService.onChangeState(ConnectionStateTypes.offline); 
               });
            });
            Observable.fromEvent(document, 'backbutton').subscribe(event => {
                this.zone.run(() => {
                    if(this.navigationService.getRouter().url == this.routes.EVENT_DETAILS){
                        this.$deviceBackEmitter.emit(event);
                    }            
                    else{
                        this.navigationService.goBack();
                    }
                });
             });
           
            // document.addEventListener('online', (e)=>{
            //     this.zone.run(() => {
            //         this.keepService.start('https://jsonplaceholder.typicode.com/albums/1',1000);
            //     });
            // }, false);
            // document.addEventListener('offline', (e)=>{
            //     this.zone.run(() => {
            //         this.keepService.NetworkLastStatus = false;
            //         connectionService.onChangeState(ConnectionStateTypes.offline);
            //     });
            // }, false);
            // document.addEventListener('backbutton', (e)=>{
            //     this.zone.run(() => {
            //         if(this.navigationService.getRouter().url == this.routes.EVENT_DETAILS){
            //             this.$deviceBackEmitter.emit(e);
            //         }            
            //         else{
            //             this.navigationService.goBack();
            //         }
            //     });
            // }, false);   
        }
        else{
            window.addEventListener('online', (e)=>{
                this.keepService.start('https://jsonplaceholder.typicode.com/albums/1',1000);
            }, false);
            window.addEventListener('offline', (e)=>{
                this.keepService.NetworkLastStatus = false;
                this.keepService.updatedNetworkCurrentStatus(false);
                connectionService.onChangeState(ConnectionStateTypes.offline);
            }, false);
            window.addEventListener('backbutton', (e)=>{
                if(this.navigationService.getRouter().url == this.routes.EVENT_DETAILS){
                    this.$deviceBackEmitter.emit(e);
                }            
                else{
                    this.navigationService.goBack();
                }
            }, false);
        }                   
    }
    getDeviceId(): string {           
        return this.device_info.uuid;        
    }
    
    isCorodva() : boolean{
        return this.cordovaApp;
    }
}


