import { EventControlStatusTypes } from "../event/event-control-status-types";

export class ControlTimeRequest {
    
    public carId: number;
    public eventId: number;
    public updateTime: string;
    public eventControlStatusId: EventControlStatusTypes;

    constructor(carId: number, eventId: number, updateTime: string, eventControlStatus: EventControlStatusTypes){
        this.carId = carId;
        this.eventId = eventId;
        this.updateTime = updateTime;
        this.eventControlStatusId = eventControlStatus;
    }      
}


