import { Component } from '@angular/core';
import { CustomMap } from '../../shared-classes/esri/custom-map';
import { EmapService } from '../../core/emap.service';
import { MapConfiguration } from '../../shared-classes/esri/MapConfiguration';
import { EventTypes } from '../../shared-classes/event/event-types';
import { ConnectionService } from '../../services/connection/connection.service';
import { Extent } from '../../shared-classes/esri/Extent';
import { MapStateService } from '../../services/mapState/map-state.service';
import { GraphicLayerType } from '../../shared-classes/esri/GraphicLayerType';
import { GPSService } from '../../services/general/gps.service';
import { Point } from '../../shared-classes/esri/Point';
import { NavigationService } from '../../services/navigation/navigation.service';
import { CurrentEventDetailsService } from '../../services/event/current-event-details.service';
import { EventDetailsService } from '../../services/event/event-details.service';
import { ProjectionService } from '../../services/general/projection.service';
import { EventInfo } from '../../shared-classes/event/event-info';
import { WazeService } from '../../services/general/waze.service';
import { debug } from 'util';
import { Layer } from '../../shared-classes/esri/Layer';
import { LayersService } from '../../services/layers/layers.service';

@Component({
  selector: 'emap',
  templateUrl: './emap.component.html',
  inputs: ['mapID', 'mapClass', 'mapWidth', 'mapHeight', 'isHideButtons'],
})

export class EmapComponent {
  emap: CustomMap;
  mapID: string;
  mapClass: string;
  mapWidth: string = '1443px';
  mapHeight: string = '662px';
  mapConfiguration: MapConfiguration;
  map_buttons_class: any;
  defaultBackground: boolean = true;
  gpsOn: boolean = false;
  backgroundToggleIndex = 0;
  isHideButtons: boolean = false;
  configuartion: Promise<MapConfiguration>;
  constructor(private service: EmapService,
    private connectionService: ConnectionService,
    private mapStateService: MapStateService,
    private gpsService: GPSService,
    private navigationService: NavigationService,
    private currentEventDetailsService: CurrentEventDetailsService,
    private eventDetailsService: EventDetailsService,
    private projectionService: ProjectionService,
    private wazeService: WazeService,
    private layersService: LayersService) { 
    }

  ngAfterViewInit() {
    //get hardcoded basic map config
    //this.service.getMapConfiguration(this.mapID).then((mapConfiguration: MapConfiguration) => {  
      this.service.getMapServerConfiguration().then((mapConfiguration: MapConfiguration) => {            
      this.mapConfiguration = mapConfiguration;

      //creating map
      this.emap = new CustomMap(this.mapID, mapConfiguration, this.gpsService, this.layersService);

      this.emap.addEvent(EventTypes.MAP_LOAD, () => {
        setTimeout(()=>{
          this.mapStateService.changeMapState(false);
          this.service.addMap(this.mapID, this.emap);

          //get server map configuration layers and place it in ma config
          this.layersService.getServerMapLayers(this.mapID).then((response) => {            
            if(response.isSuccessWithResult()){
              this.service.getMap(this.mapID).then((map: CustomMap) => {            
                map.setMapLayers(<Array<Layer>>response.data);              
              });                           
            }
          }); 
        }, 1000);        
      });

      //Setting Background 
      this.setDefaultBackground();

      this.connectionService.stateChanged.subscribe(ConnectionStateTypes =>
        this.setDefaultBackground());
      });
   }

  
  private calculateExtentByCenter(p: Point, resolution: number): Extent {
    let mWidth = (resolution * (parseInt(this.mapWidth) / 2));
    let mHeight = (resolution * (parseInt(this.mapHeight) / 2));

    return new Extent({
      xmin: p.x - mWidth,
      ymin: p.y - mHeight,
      xmax: p.x + mWidth,
      ymax: p.y + mHeight,
      spatialReference: { wkid: 2039 }
    });
  }

  zoomToEvent() {    
    let eventData = this.navigationService.getNavigationData();
    
    if (eventData === null)
    {
      return;
    }

    if (this.currentEventDetailsService.isCurrentEventById(eventData.eventId)) {
      this.currentEventDetailsService.getItem().then((evnetInfo) => {
        this.zoomToLatLon(evnetInfo.data.generalInfo.lat, evnetInfo.data.generalInfo.lon, this.emap.getNumberOfLevels() - 1);
      });
    }
    else this.eventDetailsService.getItem({ eventId: eventData.eventId }).then((evnetInfo) => {
      this.zoomToLatLon(evnetInfo.data.generalInfo.lat, evnetInfo.data.generalInfo.lon, this.emap.getNumberOfLevels() - 1);
    });
  }

  zoomToLatLon(lat: number, lon: number, level: number) {  
    this.gpsOn = false;
    this.emap.gpsOff();
    
    let coords = this.projectionService.transformWGS1984ToITM(lat,lon);
    this.emap.setCenter({ x: coords.x, y: coords.y, level: level });
  }

  gpsToggle() {
    if (this.gpsOn) {
      this.emap.gpsOff();
    }
    else {
      this.emap.gpsOn();
    }

    this.gpsOn = !this.gpsOn;
  }

  toggleBackgournd() {
    if (this.connectionService.isOffline()) {
      for (let i = 0; i < this.mapConfiguration.backgrounds.length; i++) {
        if (this.mapConfiguration.backgrounds[i].offline) {
          this.emap.addWebTiledLayer(this.mapConfiguration.backgrounds[i]);
          return;
        }
      }
    }
    else {
      this.backgroundToggleIndex++;
      if (this.backgroundToggleIndex > this.mapConfiguration.backgroundToggle.length - 1) {
        this.backgroundToggleIndex = 0;
      }

      this.emap.addWebTiledLayer(this.mapConfiguration.backgrounds[this.backgroundToggleIndex]);
      this.defaultBackground = !this.defaultBackground;
    }
  }

  private setDefaultBackground() {
    if (this.connectionService.isOffline()) {
      for (let i = 0; i < this.mapConfiguration.backgrounds.length; i++) {
        if (this.mapConfiguration.backgrounds[i].offline) {
          this.emap.addWebTiledLayer(this.mapConfiguration.backgrounds[i]);
          return;
        }
      }
    }
    else {
      for (let i = 0; i < this.mapConfiguration.backgrounds.length; i++) {
        if (this.mapConfiguration.backgrounds[i].visible && !this.mapConfiguration.backgrounds[i].offline) {
          this.emap.addWebTiledLayer(this.mapConfiguration.backgrounds[this.backgroundToggleIndex]);
          return;
        }
      }

      console.log('No Visible Background Exists!')
    }
  }

  navigateTo(){
    this.wazeService.navigate(31.92794,34.78495);
  }
  // destroy map
  ngOnDestroy() {
    if (this.emap) {
      this.service.removeMap(this.mapID);
      this.emap.destroy();
    }
  }
}



