import { EventEmitter, Injectable } from '@angular/core';
import { Dictionary } from '../../shared-classes/IDictionary';
import { StorageResponse } from '../base/StorageResponse';
import { HttpService } from '../../core/http.service';
import { OfflineStorage } from '../../stores/offlineStorage/OfflineStorage';
import { ConnectionService } from '../connection/connection.service';
import { QuickReportTypes } from '../../shared-classes/quick-report/quick-report-types';
import { HomeService } from '../home/home.service';
import { DeviceService } from '../general/device.service';
import { SaveQuickReportRequest } from '../../shared-classes/request/save-quick-report-request';
import { DropDownData } from '../../shared-classes/common/drop-down-data';
import { ContentService } from '../general/content.service';
import { AlertifyService } from '../general/alertify.service';
import { ItemStorage } from '../base/ItemStorage';
import { FireTruckOfflineActionTypes } from '../../shared-classes/offline/fire-truck-offline-action-types';
import { ApplicationResponseStatus } from '../../shared-classes/response/ApplicationResponseStatus';

@Injectable()
export class QuickReportService extends ItemStorage<any> {
    private notQualifedReasons: Array<DropDownData>;
    private localStoragenotQualifedReasonsKey: string = "notQualifedReasonsKey";

    constructor(
        private httpService: HttpService,
        private connectionService: ConnectionService,
        private homeService: HomeService,
        private deviceService: DeviceService,
        contentService: ContentService,
        private store: OfflineStorage,
        private alertifyService: AlertifyService
    ) {   
        super(contentService);
    }

    getNotQualifedReasons(): Promise<StorageResponse<Array<DropDownData>>>{        
        if(this.connectionService.isOffline())
        {
            this.notQualifedReasons =  <Array<DropDownData>>JSON.parse(this.store.getItem(this.localStoragenotQualifedReasonsKey));
        }
        else if(this.notQualifedReasons != null) 
        {
            return new Promise((resolve, reject) => {  
                resolve(new StorageResponse<Array<DropDownData>>().CreateResultResponse(
                    this.notQualifedReasons
                ));
            });
        }
        else{     
            return new Promise((resolve, reject) => {                           
                let url = this.httpService.baseWebApi + 'Events/GetCarInEventInactiveReasons/';
              
                this.httpService.post<StorageResponse<any>>(url, null).subscribe(
                    (response) => {  
                        if(response.resultStatus == ApplicationResponseStatus.Fail){
                            this.errorMsgPromise.then(() => {                     
                                this.alertifyService.error(this.content.getDataErrorMessage);
                                resolve(new StorageResponse<Array<DropDownData>>().CreateFailResponse());    
                            });                            
                        }
                        else{
                            this.notQualifedReasons = Array<DropDownData>();        
                            for(let index = 0; index < response.data.options.length; index ++){
                                if(response.data.options[index].value != '' && response.data.options[index].text != ''){
                                    let data = new DropDownData();
                                    data.value = response.data.options[index].value;
                                    data.text = response.data.options[index].text;
                                    this.notQualifedReasons.push(data);
                                }
                            }
                                                                                                                  
                            this.store.setItem(this.localStoragenotQualifedReasonsKey, JSON.stringify(this.notQualifedReasons)); 
    
                            resolve(new StorageResponse<Array<DropDownData>>().CreateResultResponse(
                                this.notQualifedReasons
                            ));    
                        }
                    },
                    (err) => {
                        this.errorMsgPromise.then(() => {                     
                            this.alertifyService.warring(this.content.getConnectionErrorMessage);
                            resolve(new StorageResponse<Array<DropDownData>>().CreateFailResponse());    
                        });
                    },                  
                );
            });
        }
    }

    sendQuickReport(eventId: number, reportType: QuickReportTypes, reportDesc: string = null): Promise<StorageResponse<boolean>>{
        if(!reportType || (reportType == QuickReportTypes.Broken && !reportDesc)){
            return new Promise((resolve, reject) => {                                
                resolve(new StorageResponse<boolean>().CreateFailResponse());
            }); 
        }

        return new Promise((resolve, reject) => {   
            this.homeService.getCar().then((response) => {
                let deviceId = this.deviceService.getDeviceId();
                let request = new SaveQuickReportRequest(deviceId);
                request.carId = response.data.carId;
                request.eventLogReportTypeId = reportType;
                request.eventId = eventId;
                request.reportDesc = reportDesc;
  
                let url = null;
                if(request.eventId != -1){
                    url = this.httpService.baseWebApi + 'Events/AddNewEventLogReport/';
                }       
                else{
                    url = this.httpService.baseWebApi + 'Events/AddNewVirtualMissionLogReport/';
                }

                this.httpService.post<StorageResponse<any>>(url, request, FireTruckOfflineActionTypes.EventReport).subscribe(
                    (response) => {   
                        if(response.resultStatus == ApplicationResponseStatus.Fail){
                            this.errorMsgPromise.then(() => {                     
                                this.alertifyService.error(this.content.getDataErrorMessage);
                                resolve(new StorageResponse<boolean>().CreateFailResponse());   
                            });   
                        } 
                        else{
                            this.alertifyService.success(this.content.successMessage);
                            resolve(new StorageResponse<boolean>().CreateResultResponse(
                                true
                            ));    
                        }                                                                    
                    },
                    (err) => {
                        this.errorMsgPromise.then(() => {                     
                            this.alertifyService.warring(this.content.getConnectionErrorMessage);
                            resolve(new StorageResponse<boolean>().CreateFailResponse());    
                        });                           
                    },                  
                );            
            });               
        });
    }
}


