import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { employee } from '../../shared-classes/employees/employee';
import { AvalibleTeam } from '../../shared-classes/common/avalible-team';

@Component({
 selector:'employee-set',
  templateUrl: './employee-set.component.html',
  inputs: ['employeeSet', 'index']
})

export class EmployeeSetComponent{
  @Output() onClick: EventEmitter<any> = new EventEmitter();
  @Output() onEmployeeClick: EventEmitter<any> = new EventEmitter();
  
  employeeSet: AvalibleTeam;
  index: number;

  constructor() {}

  employeesClicked(): void {    
    this.onClick.emit({ employees: this.employeeSet.employees, index: this.index });
  }

  addEmployee(employee, employeeIndex){
    this.onEmployeeClick.emit({ employee: employee, index: this.index, emloyeeIndex: employeeIndex });
}

  ngOnInit(){}

  ngOnDestroy(){}
}



